import React from "react";
import { withStyles } from "@material-ui/core/styles";
import 'react-confirm-alert/src/react-confirm-alert.css';
import Button from "components/CustomButtons/Button.js"
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import { MenuItem, InputLabel, FormControl, Select, Chip, Input,TextField } from "@material-ui/core";
import CustomInput from "components/CustomInput/CustomInput";
import Autocomplete from 'components/Autocomplete/Autocomplete';
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TablePagination from "@material-ui/core/TablePagination";
import Loader from "react-loader-spinner";
import { confirmAlert } from "react-confirm-alert";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import BorderColorIcon from "@material-ui/icons/BorderColor";
import { red } from "@material-ui/core/colors";
import { userService,sampleService ,dataControllerService} from "services/api.services";
import Checkbox from '@material-ui/core/Checkbox';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import moment from "moment";
const useStyles = () => (styles)
const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250,
        },
    },
};
class CheckoutListViewPage extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            checklist:[],
            isCreateCheckoutlist:false,isEditCheckoutList:false,tank_id:'',sample_id:'',locationList:[],location:null,
            isLoading:true,checkoutList:[],checkout_id:'',
            selected_checklists:[],updated_by:'',on_date:'',
            isLocation:false,defaultLocation:'',inspector_name:'',comment:'',checkoutTestList:[],test_values:[],selectedTest_value:'',test:null,
            isTestSelected:false,defaultselectedTest:'',selectSingleTestValue:'',
            // Pagination
            page: 0, rowsPerPage: 10,
            }
            dataControllerService.getAdminChecklist((checklist)=>{
              if(checklist){
                checklist.map((ele, i) => {
                  ele.sub_categorys.map((sub_cat)=>{
                    this.state[sub_cat._id] = false
                    return false
                  })
                  return false
              })
              }
        })
            
    }
    componentDidMount = () => {
      this._onLoad()
        userService.getAllLocation((locationList) => { if (locationList && locationList.data && locationList.data.length) this.setState({ locationList: locationList.data || [], }); });
      }
      _onLoad=()=>{
        this._getCheckoutList();
        dataControllerService.getAdminChecklist((checklist)=>{if (checklist) this.setState({ checklist: checklist || [], });})
        this._getCheckoutTestList()
    
      }
  /******************************Handler********************** */  
  _handleReset=()=>{
      this.setState({ isCreateCheckoutlist:true,isEditCheckoutList:false,tank_id:'',sample_id:'',location:null,isOrderLoading:false, 
      page: 0, rowsPerPage: 10,checkout_id:'',selected_checklists:[],updated_by:'',on_date:'',isLocation:false,defaultLocation:"",inspector_name:'',
      comment:'',test_values:[],selectedTest_value:'',test:null,defaultselectedTest:'',selectSingleTestValue:''
    })
    this.state.checklist.map((ele, i) => {
      ele.sub_categorys.map((sub_cat)=>{
        this.state[sub_cat._id] = false
        return false
      })
      return false
  })
  this._getCheckoutList();
  }
  _handleLocationSelection=(location)=>{
    if(location){
    this.setState({location:location.id,defaultLocation:location.name})
    }
  }
  _getCheckoutList=()=>{
    sampleService.getCheckoutList(result => { if (result) { 
     this.setState({ checkoutList: result.data})
     this.setState({ isLoading: false })
      }

    })
   }
   _getCheckoutTestList=()=>{
    sampleService.getCheckoutTestList(result => { if (result) { 
     this.setState({ checkoutTestList: result.data})
     this.setState({ isLoading: false })
      }

    })
    
  }
  _handleCheckListService(name, values) {
    if(this.state[name]===false){
    this.setState({ [name]: true})
    this.state.selected_checklists.push(name)
    }
    else if(this.state[name] === true){
      let dataList = this.state.selected_checklists.filter((id) => id !== name);
      // this.state.selected_checklists.pop(name)
      this.setState({selected_checklists:dataList,[name]: false})
    }
}
_handleTestSelection=(test)=>{
  if(test !== null){
    this.setState({selectedTest_value:''})
  if(test.values && test.values.length === 1){
    this.setState({selectedTest_value:test.values[0],test_values:test.values,test:test.id,defaultselectedTest:test.name})
  }
  else if(test.values && test.values.length !== 1){
    this.setState({test_values:test.values,test:test.id,defaultselectedTest:test.name})
  }
  }
  else{
    return false
  }
  return false
}
_handleTestValueSelection=(event)=>{
  this.setState({selectedTest_value:event.target.value})

}
  /********************************Api integration**************************************** */
  _onCreateCheckout=()=>{
    if(this.state){
      let formdata = {
        location: this.state.location ||null,
            sample_id:this.state.sample_id,
            tracking_id:this.state.tank_id,
            selected_checklists:this.state.selected_checklists || [],
            inspector_name:this.state.inspector_name,
            comment:this.state.comment,
            test:this.state.test,
            test_value:this.state.selectedTest_value
      };
      sampleService.createCheckout(formdata, (result) => {
        if(result){
          this._handleReset();
          this._getCheckoutList();
        }
      });
    }
  }
  _getCheckoutbyID = (id) => {
    this._handleReset();
    sampleService.getCheckoutById(id, (result) => {
      if (result) {
        this.state.locationList.map((ele)=>{
          if(result.location_name === ele.name){
            this.setState({location:ele._id})
          }
          return false
        })
        this.state.checkoutTestList.map((ele)=>{
          if(result.test===ele.id){
            this.setState({defaultselectedTest:ele.name,isTestSelected:true,test_values:ele.values})
          }
          return false
        })
        this.setState({
            checkout_id:result._id,
            defaultLocation: result.location_name,
            sample_id:result.sample_id,
            tank_id:result.tank_id,
            updated_by:result.updated_by_fname + " " + result.updated_by_surname,
            selected_checklists:result.selected_checklists,
            on_date:result.updated_at,
            isEditCheckoutList:true,
            isLocation:true,
            inspector_name:result.inspector_name,
            comment:result.comment,
            test:result.test,
            selectedTest_value:result.test_value,
        });
      }
      this.state.selected_checklists.map((ele)=>{
          this.setState({ [ele]: true})
          return false
        })
    });
  };
  _updateCheckoutListById = () => {
    if(this.state.checkout_id){
        const formdata = {
            location: this.state.location || null ,
            sample_id:this.state.sample_id,
            tracking_id:this.state.tank_id,
            selected_checklists:this.state.selected_checklists || [],
            inspector_name:this.state.inspector_name,
            comment:this.state.comment,
            test:this.state.test,
            test_value:this.state.selectedTest_value
        }
        confirmAlert({
            title: 'Confirm',
            message: 'Are you sure you want to update',
            buttons: [
                {
                    label: 'Update',
                    onClick: () => {
                        sampleService.updateCheckoutById(this.state.checkout_id,formdata, result => {
                            if (result) {
                                this.setState({on_date:result.updated_at})
                                this._getCheckoutList();
                            }
                        })
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => console.log('cancel update')
                }
            ]
        });
    } 
  }
  _deleteCheckout = (id) => {
    confirmAlert({
      title: "Confirm",
      message: "Are you sure you want to delete",
      buttons: [{ label: "Delete", onClick: () => { sampleService.deleteCheckout(id, (result) => { if (result) { this._getCheckoutList(); } }); }, },
      { label: "Cancel", onClick: () => console.log("cancel delete"), },
      ],
    });
  };
  _handleSearchFilter = (event) => {  this.setState({ isLoading: true });
  sampleService.searchCheckout(event.target.value, (result) => {
    if (result) { this.setState({checkoutList: result.data, isLoading: false,  page: 0,     });  }
  });
};
    /*------------------------------ Pagination -----------------------------------*/
    _handleChangePage = (event, newPage) => { this.setState({ page: newPage }); };
    _handleChangeRowsPerPage = (event) => { this.setState({ rowsPerPage: parseInt(+event.target.value) }); this.setState({ page: 0 }); };
  /*----------------------------------- Render ---------------------------*/
  _renderHeader = () => {
    return (
      <GridContainer>
        <GridItem xs={6} sm={6} md={6}><Button onClick={() => {
           this._handleReset(); this.setState({ isCreateCheckoutlist: !this.state.isCreateCheckoutlist }); }} 
           color="info" > {this.state.isCreateCheckoutlist ? " Return " : "Create Check List"} </Button>
            </GridItem>
      </GridContainer>
    );
  };
  _getCheckOutServices() {
    const { classes } = this.props;
    const data = this.state.checklist.slice(0);
    const checkout_service_Item = data.map((ele) => (        
      <GridContainer>
        {ele.sub_categorys.length ?<GridItem xs={12} sm={12} md={12}><h5 style={{ paddingTop: 15 }}><b>{ele.name}</b></h5></GridItem> :null}
          <GridItem xs={12} sm={12} md={12}>
          <List className={classes.root}>
            {ele.sub_categorys.map((sub_cat) => (
                <ListItem key={"value"} role={undefined} dense button >
                <ListItemIcon>
                  <Checkbox
                          defaultChecked
                          color="default"
                          checked={this.state[sub_cat._id]}
                          onChange={(values) => this._handleCheckListService(sub_cat._id, values)}
                          inputProps={{ 'aria-label': 'checkbox with default color' }}
                  />
                </ListItemIcon>
                <ListItemText id={"labelId"} primary={sub_cat.name} />
                </ListItem>
              ))}
          </List>
              </GridItem>    
      </GridContainer>
    ))
    return checkout_service_Item;
  }
  
  _renderCheckoutListForm = () => {
    const { classes } = this.props;
    const location_value = { name: this.state.defaultLocation };
    const selectedTestonUpdate= { name: this.state.defaultselectedTest };
     return (
          <Card>
              <CardHeader color="primary">  
               <h4 className={classes.cardTitleWhite}>{this.state.isEditCheckoutList?  `Check List Details `:"Create Check List Form"}  </h4>
                <p className={classes.cardCategoryWhite}>{this.state.isEditCheckoutList?  ` Update Check List form`:" Complete Check List form"}  </p>
              </CardHeader>
            <CardBody>
            <GridContainer>
            {!this.state.isLocation ? (
              <GridItem xs={12} sm={12} md={12}>
                <FormControl className={classes.formControl}>
                  <Autocomplete  id="location-type-selection"
                    onChange={(event, newValue) =>  this._handleLocationSelection(newValue) }
                    options={this.state.locationList}
                    getOptionLabel={(option) => option.name}
                    style={{ backgroundColor: "white" }}
                    autoComplete
                    includeInputInList
                    renderInput={(params) => (   <TextField  {...params}  label="Location"  variant="outlined" />   )}
                  />
                </FormControl>
              </GridItem>
            ) : null}
            {this.state.isLocation ? (
              <GridItem xs={12} sm={12} md={12}>
                <FormControl className={classes.formControl}>
                  <Autocomplete
                    id="location-type-selection"
                    onChange={(event, newValue) =>   this._handleLocationSelection(newValue)   }
                    value={location_value}
                    options={this.state.locationList}
                    getOptionLabel={option => option.name}
                    disablePortal
                    includeInputInList
                    autoComplete
                    renderInput={(params) => ( <TextField   {...params}  label="Location" variant="outlined"  />        )}
                  />
                </FormControl>
              </GridItem>
            ) : null}
            <GridItem xs={12} sm={12} md={6}>
                <CustomInput
                labelText="Inspector Name"
                onChange={e => {this.setState({ inspector_name: e.target.value });    }}
                id="inspector_name"
                inputProps={{ type: "text", value: this.state.inspector_name }}
                formControlProps={{   fullWidth: true,   }}
                />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
                <CustomInput
                labelText="Sample ID"
                onChange={e => {this.setState({ sample_id: e.target.value });    }}
                id="sample_id"
                inputProps={{ type: "text", value: this.state.sample_id }}
                formControlProps={{   fullWidth: true,   }}
                />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
                <CustomInput
                labelText="Tank ID"
                onChange={e => {this.setState({ tank_id: e.target.value });  }}
                id="tank_id"
                inputProps={{ type: "text", value: this.state.tank_id }}
                formControlProps={{  fullWidth: true,  }}
            />
            </GridItem>
            
            </GridContainer>
            {this._getCheckOutServices()}
            <GridContainer>
              <GridItem xs={12} sm={12} md={12} >
              <h5 style={{ paddingTop: 15 }}><b>On-Field Tests </b></h5>
              </GridItem>
              <GridItem xs={12} sm={12} md={6}>
              
                {!this.state.isTestSelected? <FormControl className={classes.formControl}>
                  <Autocomplete  id="location-type-selection"
                    onChange={(event, newValue) =>  this._handleTestSelection(newValue) }
                    options={this.state.checkoutTestList}
                    getOptionLabel={(option) => option.name}
                    style={{ backgroundColor: "white" }}
                    autoComplete
                    includeInputInList
                    renderInput={(params) => (   <TextField  {...params}  label="On-Field Tests"  variant="outlined" />   )}
                  />
                </FormControl> :null}
                {this.state.isTestSelected ? <FormControl className={classes.formControl}>
                  <Autocomplete
                    id="location-type-selection"
                    onChange={(event, newValue) =>  this._handleTestSelection(newValue) }
                    value={selectedTestonUpdate}
                    options={this.state.checkoutTestList}
                    getOptionLabel={option => option.name}
                    disablePortal
                    includeInputInList
                    autoComplete
                    renderInput={(params) => ( <TextField   {...params}  label="On-Field Tests" variant="outlined"  />        )}
                  />
                </FormControl> :null}
               
              </GridItem>
              
                {this.state.test_values.length === 1 ?
                <GridItem xs={12} sm={12} md={6}>
                <CustomInput
                labelText="Test Value"
                onChange={e => {this.setState({ selectedTest_value: e.target.value });  }}
                id="selectedTest_value"
                inputProps={{ type: "text", value: this.state.selectedTest_value }}
                formControlProps={{  fullWidth: true,  }}
            /> </GridItem>:
            <GridItem xs={12} sm={12} md={6}>
                            <FormControl className={classes.formControl}>
                                <InputLabel className={classes.InputLabelGrey} id={"TestValue-selection"}>Test Values</InputLabel>
                                <Select
                                     labelId={"TestValue-selection"} id={"TestValue-selection"} name={"TestValue-selection"} single
                                    value={this.state.selectedTest_value}
                                    onChange={this._handleTestValueSelection}
                                    input={<Input id="select-multiple-chip" />}
                                    renderValue={(selected) => (   
                                        <Chip key={selected} label={selected} className={classes.chip} />  
                                         )}
                                    MenuProps={MenuProps}
                                >
                                    {this.state.test_values.map((name,index) => (   <MenuItem key={index} value={name} >   {name}  </MenuItem>   ))}
                                </Select>
                            </FormControl>
                </GridItem>
                }
            <br/>
            <GridItem xs={12} sm={12} md={12}>
            <FormControl className={classes.formControl}>
            {/* <form className={classes.MultilineTest} noValidate autoComplete="off"> */}
            <TextField
                label="Comment"
                multiline
                rowsMax={3}
                value={this.state.comment}
                onChange={e => {this.setState({ comment: e.target.value });  }}
              />  
          {/* </form> */}
          </FormControl>
        </GridItem>
        </GridContainer>
            {this.state.isEditCheckoutList === true ?<GridContainer>
              <GridItem xs={12} sm={12} md={6}><p style={{ paddingTop: 15 }}><b>Updated By : </b>{this.state.updated_by}</p></GridItem>
              <GridItem xs={12} sm={12} md={6}><p style={{ paddingTop: 15 }}><b>Date : </b>{moment(this.state.on_date).format("MMM DD, YYYY")}</p></GridItem>
            </GridContainer>:null}
            </CardBody>

            <CardFooter>
              <Button onClick={this._handleReset} color="danger">Reset</Button>
              {!this.state.isEditCheckoutList  ? (<Button onClick={this._onCreateCheckout} color="success"> Create Check List </Button>) : null}
              {this.state.isEditCheckoutList ? (<Button onClick={this._updateCheckoutListById} color="success"> Update Check List </Button>) : null}
            </CardFooter>
          </Card>
      )
  }
  _renderCheckoutListTable=()=>{
    const { classes } = this.props;
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="info"> <h4 className={classes.cardTitleWhite}> Check List </h4> </CardHeader>
            <CardBody>
              <GridItem xs={12} sm={12} md={4}>
                <CustomInput labelText={"Search"} id={"search"} onChange={this._handleSearchFilter} inputProps={{ type: "text" }}  formControlProps={{ fullWidth: true,  }} />
              </GridItem>
              {this.state.isLoading ? (
                <GridItem xs={12} sm={12} md={4}>  <Loader type="ThreeDots" color="#a5d1e6"  height={50}  width={50}/>  </GridItem>
              ) : (
                  <div className={classes.tableResponsive}>
                     <Table className={classes.table}>
                        <TableHead className={classes.info}>
                          <TableRow className={classes.tableHeadRow}>
                          <TableCell className={ classes.tableCell + " " + classes.tableHeadCell} key={"location"}   >  Location Name  </TableCell>
                          
                          <TableCell className={ classes.tableCell + " " + classes.tableHeadCell} key={"update_by"}   >  Updated By  </TableCell>
                          
                          <TableCell className={ classes.tableCell + " " + classes.tableHeadCell} key={"date_in"}   >  Date </TableCell>
                          
                          <TableCell  className={ classes.tableCell + " " + classes.tableHeadCell } key={"inspector_name"} > Inspector Name</TableCell>
                          <TableCell  className={ classes.tableCell + " " + classes.tableHeadCell } key={"tank_id"} >Tank Id</TableCell>
                            <TableCell className={ classes.tableCell + " " + classes.tableHeadCell } key={"sampile_id"} > Sample Id </TableCell>
                            <TableCell  className={ classes.tableCell + " " + classes.tableHeadCell } key={"option"} > Options </TableCell>
                          </TableRow>
                        </TableHead>
                        <TableBody>
                          {this.state.checkoutList.length ? ( this.state.checkoutList
                          .slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage +this.state.rowsPerPage )
                              .map((value, key) => (
                                <TableRow key={key} tabIndex={-1} className={classes.tableBodyRow} >
                                  <TableCell   className={classes.tableCell} key={"location" + key} >{value.location_name} </TableCell>
                                  
                                  <TableCell   className={classes.tableCell} key={"update_by" + key} >{value.updated_by_fname +" "+ value.updated_by_surname} </TableCell>
                                  
                                  <TableCell   className={classes.tableCell} key={"date_in" + key} > {moment(value.updated_at).format("MMM DD, YYYY",)} </TableCell>
                                  <TableCell className={classes.tableCell}key={"inspector_name" + key} > {value.inspector_name || null} </TableCell>
                                  <TableCell className={classes.tableCell}key={"tank_id" + key} > {value.tank_id} </TableCell>
                                  <TableCell className={classes.tableCell} key={"sampile_id" + key} >  {value.sample_id} </TableCell>
                                  <TableCell className={classes.tableCell} key={"option" + key}>
                                    <DeleteForeverIcon onClick={() => this._deleteCheckout(value._id)} style={{ color: red[500] }} />
                                    <BorderColorIcon onClick={() => this._getCheckoutbyID(value._id)}/>
                                  </TableCell>
                                </TableRow>
                              ))
                          ) : (
                              <TableRow>No records available</TableRow>
                            )}
                        </TableBody>
                      </Table>
                    <TablePagination component="div" count={this.state.checkoutList.length} rowsPerPage={this.state.rowsPerPage} page={this.state.page} onChangePage={this._handleChangePage} onChangeRowsPerPage={this._handleChangeRowsPerPage} />
                  </div>
                )}
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    );

  }
render() {
    return (
      <div>
        {this._renderHeader()}
        {this.state.isCreateCheckoutlist ? this._renderCheckoutListForm() : null}
        {this._renderCheckoutListTable()}
      </div>
    );
  }
}

export default withStyles(useStyles)(CheckoutListViewPage);
