const useStyles = () => (styles)
const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const INITIAL_STATE = {
  attention: '', bid_number: '', bcontact: '', location: '',
  isCreateOrder: false, onShowOrderTab: true, isEditOrder: false,
  selectedManager: [], selectedShippingMethod: [],
  // MULTI EMAIL
  emailCollection: [], value: "", emailError: null,
  // KIT
  selectedKit: [], selectedKitID: "",
  selectedShippingMethodId: "",
  selectedManagerId: "",
  order_id: '', defaultLocation: '',
  isLocation: false,
  //premnet Address  
  address: '', city: '', state: '', country: '', zipcode: '', address_check: false, ErrorMessage: '',
  //Shipping Address
  shipping_name: '', shipping_address: '', shipping_city: '', shipping_state: '', shipping_country: '', shipping_zipcode: '', shipping_cost: '',
  tracking_id: '', selectedShippingService: [], selectedShippingServiceId: '', quantity: '',
  isShippingTrackable: false, shipping_account: '', comment: '',
  return_lable_check: false, return_label_quantity: '', return_label_cost: '', purchase_order: '', charge_card: false,
  pendingOrderList: [], completeOrderList: [], isCompletedOrderView: false, isPendingOrderView: false, orderStatus: '', created_by: '', shipping_created_at: "",
  selectedPacking: [constants.PRE_DEFINED_VALUES.PACKING[0]],
}
class OrderKitsViewPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      ...INITIAL_STATE,
      // On Load list
      locationList: [], orderList: [], kitList: [], managerList: [], ShippingList: [], shippingServiceList: [],
      isLoading: true, orderCompletedCount: 0, orderPendingCount: 0,
      // Pagination
      page: 0, rowsPerPage: 10,
      packingList: [],
    }
  }
  componentDidMount = () => {
    this._onLoad()
  }
  _onLoad = () => {
    this._getOrderCountList()
    this._getOrderList();
    this._getLocationList();
    this._getShippingMethodList();
    this._getFuelKitList();
    this._getManagerList();
    this._getShippingSeviceList();
    this._getPackaging()
  }

  _getPackaging = () => this.setState({ packingList: constants.PRE_DEFINED_VALUES.PACKING })

  _getOrderCountList = () => {
    sampleService.getOrderCountList((result) => {
      if (result) {
        const count = result.data;
        try {
          this.setState({
            orderPendingCount: count.orders_pending || this.state.orderPendingCount,
            orderCompletedCount: count.orders_completed || this.state.orderCompletedCount,
          });
        } catch (err) {
          console.log(err)
        }
      }
      this.setState({ isLoading: false });
    });
  };
  /*------------------------- Handlers ------------------------- */
  _handleReset = () => {
    this.setState({
      //Order 
      attention: '', bid_number: '', bcontact: '',
      isCreateOrder: true, location: '', onShowOrderTab: true,
      selectedManager: [], selectedShippingMethod: [], selectedKit: [], emailCollection: [], value: "", emailError: null, selectedKitID: "",
      selectedShippingMethodId: "", selectedManagerId: "", order_id: '', defaultLocation: '',
      isEditOrder: false, isLocation: false,
      //premnet Address  
      address: '', city: '', state: '', country: '', zipcode: '', address_check: false, ErrorMessage: '',
      //Shipping Address
      shipping_name: '', shipping_address: '', shipping_city: '', shipping_state: '', shipping_country: '', shipping_zipcode: '', shipping_cost: '',
      tracking_id: '', selectedShippingService: [], selectedShippingServiceId: '', quantity: '',
      isShippingTrackable: false, shipping_account: '', comment: '',
      return_lable_check: false, return_label_quantity: '', return_label_cost: '', purchase_order: '', charge_card: false,
      pendingOrderList: [], completeOrderList: [], isCompletedOrderView: false, isPendingOrderView: false, orderStatus: '', created_by: '', shipping_created_at: "",
      selectedPacking: [constants.PRE_DEFINED_VALUES.PACKING[0]],
    })
  }
  _getOrderList = () => {
    this.setState({ isSIFLoading: true });
    var params = {};
    if (this.state.isPendingOrderView) { params.sortStatus = "pending"; }
    if (this.state.isCompletedOrderView) { params.sortStatus = "shipped"; }
    sampleService.getOrderList(params, (result) => {
      if (result) {
        try {
          this.setState({
            orderStatus: params.sortStatus,
            orderList: result.data || [],
          });
        } catch (err) {
          console.log(err)
        }
      }
      this.setState({ isLoading: false });
    });
  };
  _getManagerList = () => {
    userService.getUsersList('manager', false, result => { if (result) this.setState({ managerList: result, }) })
  }
  _getShippingMethodList = () => {
    dataControllerService.getShippingMethodList(result => {
      if (result) this.setState({ ShippingList: result.data, })
    })
  }
  _getFuelKitList = () => {
    dataControllerService.getKitList(result => { if (result) this.setState({ kitList: result, }) })
  }
  _getLocationList = () => {
    userService.getAllLocation((locationList) => {
      if (locationList && locationList.data && locationList.data.length) this.setState({ locationList: locationList.data || [], });
    });

  }
  _getShippingSeviceList = () => {
    sampleService.getShippingSeviceList((result) => { let list = [{ name: "Select One" }]; this.setState({ shippingServiceList: list.concat(result.data || []) }); });
  }
  _handleLocationSelection = (location) => {
    if (location) {
      var temp = { address: '', city: "", state: "", country: "", };
      if (
        location.address !== "undefined" && location.address !== null && location.address !== ""
      ) {
        temp.address = location.address + ", " || " ";
      }
      if (
        location.city !== "undefined" && location.city !== null && location.city !== ""
      ) {
        temp.city = location.city + ", " || " ";
      }
      if (
        location.state !== "undefined" && location.state !== null && location.state !== ""
      ) {
        temp.state = location.state + ", " || " ";
      }
      if (
        location.country !== "undefined" && location.country !== null && location.country !== ""
      ) {
        temp.country = location.country + ", " || " ";
      }
      var tempEmail = [];
      if (location.email && location.email.length > 0) {
        location.email.map((email) => {
          if (
            email !== undefined && email !== "undefined" && email !== null &&
            email !== ""
          ) {
            return tempEmail.push(email);
          }
          return false;
        });
      }
      if (
        location.primaryemail !== undefined && location.primaryemail !== "undefined" && location.primaryemail !== null && location.primaryemail !== ""
      ) {
        tempEmail.push(location.primaryemail);
      }
      this.setState({
        ErrorMessage: '',
        address_check: false,
        location: location._id,
        address: location.address,
        city: location.city,
        state: location.state,
        country: location.country,
        zipcode: location.zipcode,
        phone: location.phone,
        bcontact: location.phone.toString().slice(0, 3) + '-' + location.phone.toString().slice(3, 6) + '-' + location.phone.toString().slice(6, 10),
        emailCollection: tempEmail || [],
        isLocation: true,
        defaultLocation: location.name,
      });
    }
  };
  _handleKitSelection = (event) => {
    this.setState({ ErrorMessage: '', selectedKit: [event.target.value], selectedKitID: event.target.value.id, })
  }
  _handleShippingMethodSelection = (event) => {
    this.setState({ ErrorMessage: '', selectedShippingMethod: [event.target.value], selectedShippingMethodId: event.target.value.id })
  }
  _handleManagerSelection = (event) => {
    this.setState({ selectedManager: [event.target.value], selectedManagerId: event.target.value.id })
  }
  _handleSelectShippingAddress = () => {
    if (this.state.address_check === false) {
      this.setState({
        ErrorMessage: '', address_check: true, shipping_name: this.state.defaultLocation,
        shipping_address: this.state.address, shipping_city: this.state.city, shipping_state: this.state.state, shipping_country: this.state.country, shipping_zipcode: this.state.zipcode,
      })
    }
    else if (this.state.address_check === true) {
      this.setState({
        ErrorMessage: '',
        address_check: false,
      })
    }

  }
  _handlePackingSelection = event => {
    this.setState({
      selectedPacking: event.target.value ? [event.target.value] : [constants.PRE_DEFINED_VALUES.PACKING[0]]
    })
  }
  _handleShippingSeviceSelection = (event) => {
    if (event.target.value.trackable === true) {
      this.setState({ isShippingTrackable: true, tracking_id: '' })
    }
    else if (event.target.value.trackable === false) {
      this.setState({ isShippingTrackable: false, tracking_id: '' })
    }
    this.setState({ selectedShippingService: [event.target.value], selectedShippingServiceId: event.target.value.id, ErrorMessage: '' })
  }
  _handleSelectReturnLabel = () => {
    if (this.state.return_lable_check === true) {
      this.setState({ return_lable_check: false })
    }
    else if (this.state.return_lable_check === false) {
      this.setState({ return_lable_check: true })
    }
  }
  _handleSearchFilter = (event) => {
    this.setState({ isLoading: true });
    sampleService.searchOrder(event.target.value, (result) => {
      if (result) {
        this.setState({ orderList: result.data, page: 0, });
      }
    });
    this.setState({ isLoading: false })
  };
  _handleSelectChargeCard = () => {
    if (this.state.charge_card === true) { this.setState({ charge_card: false }) }
    else if (this.state.charge_card === false) {
      this.setState({ charge_card: true })
    }
  }
  _handleTrackingURL = (value) => {
    const URL = value.shipping_service_url
    const pay = value.shipping_service_payload
    const key = value.shipping_service_key
    const payload = pay.substring(1, pay.length - 1)
    const Dummypay = payload.replace(/\s/g, "")
    let dummyurl = ""
    // if( Dummypay === ""){
    //   dummyurl = URL +"?action=track&&"+key+ "=" +value.tracking_id
    // }
    // else if(Dummypay !== ""){
    //   dummyurl = URL +"?action=track&&" +Dummypay + "&&" +key+ "=" +value.tracking_id
    // } 
    if (pay === "{}") {
      dummyurl = URL + "?" + key + "=" + value.tracking_id
    }
    else if (pay !== {}) {
      dummyurl = URL + "?" + Dummypay + "&&" + key + "=" + value.tracking_id
    }
    // console.log(dummyurl)
    // return 
    if (value.shipping_service_url) {
      window.open(dummyurl)
    }
  }
  /*----------------- Multiple email entry handle ------------------*/
  handleKeyDown = (evt) => {
    if (["Enter", "Tab", ","].includes(evt.key)) {
      evt.preventDefault();
      var value = this.state.value.trim();
      if (value && this._handleEmailIsValid(value)) {
        this.setState({
          emailCollection: [...this.state.emailCollection, this.state.value], value: "",
        });
      }
    }
  };
  _handleEmailAddChange = (evt) => {
    this.setState({
      value: evt.target.value,
      emailError: null,
    });
  };
  _handleEmailRemoveChange = (item) => {
    this.setState({
      emailCollection: this.state.emailCollection.filter((i) => i !== item),
    });
  };
  _handleEmailPasteChange = (evt) => {
    evt.preventDefault();
    var paste = evt.clipboardData.getData("text");
    var emails = paste.match(/[\w\d\.-]+@[\w\d\.-]+\.[\w\d\.-]+/g);
    if (emails) {
      var toBeAdded = emails.filter((email) => !this._handleEmailIsInList(email));
      this.setState({ emailCollection: [...this.state.emailCollection, ...toBeAdded], });
    }
  };
  _handleEmailIsValid(email) {
    let emailError = null;
    if (this._handleEmailIsInList(email)) { emailError = `${email} has already been added.`; }
    if (!this._handleEmailIsIsEmail(email)) { emailError = `${email} is not a valid email address.`; }
    if (emailError) { this.setState({ emailError }); return false; }
    return true;
  }
  _handleEmailIsInList(email) {
    return this.state.emailCollection.includes(email);
  }
  _handleEmailIsIsEmail(email) {
    return /[\w\d\.-]+@[\w\d\.-]+\.[\w\d\.-]+/.test(email);
  }
  NumberFormatCustom = (props) => {
    const { inputRef, onChange, ...other } = props;
    return (
      <NumberFormat
        {...other}
        getInputRef={inputRef}
        onValueChange={(values) => {
          onChange({
            target: {
              name: props.name,
              value: values.value,
            },
          });
        }}
        isNumericString
        prefix="$"
        decimalScale={2}
        fixedDecimalScale={true}

      />
    );
  }
  /*------------------------------ Pagination -----------------------------------*/
  _handleChangePage = (event, newPage) => { this.setState({ page: newPage }); };
  _handleChangeRowsPerPage = (event) => { this.setState({ rowsPerPage: parseInt(+event.target.value) }); this.setState({ page: 0 }); };
  /**************************************ORDER***************************************/
  _onCreateOrderKit = () => {
    let formdata = {
      location: this.state.location || null,
      shipping_method: this.state.selectedShippingMethodId || null,
      shipping_address: this.state.shipping_address,
      shipping_city: this.state.shipping_city,
      shipping_state: this.state.shipping_state,
      shipping_country: this.state.shipping_country,
      shipping_zipcode: this.state.shipping_zipcode,
      email: this.state.emailCollection,
      attention: this.state.attention,
      phone: this.state.bcontact || " ",
      bid_number: this.state.bid_number,
      kit: this.state.selectedKitID || null,
      manager: this.state.selectedManagerId || null,
      shipping_service: this.state.selectedShippingServiceId || null,
      quantity: this.state.quantity,
      shipping_account: this.state.shipping_account,
      comment: this.state.comment,
      return_label_quantity: this.state.return_label_quantity || null,
      return_label_cost: this.state.return_label_cost || null,
      purchase_order: this.state.purchase_order,
      charge_card: this.state.charge_card,
      shipping_name: this.state.shipping_name,
      shipping_cost: this.state.shipping_cost || null,
      isShippingTrackable: this.state.isShippingTrackable,
      tracking_id: this.state.tracking_id,
      packing: "",
    };
    if (this.state.selectedPacking[0] != this.state.packingList[0])
      formdata.packing = this.state.selectedPacking[0]

    sampleService.createOrder(formdata, (result) => {
      if (result) {
        this._handleReset();
        this._getOrderCountList();
        this._getOrderList();
      }
    });
  }
  getOrderbyID = (id) => {
    this._handleReset();
    let selectedPacking = [constants.PRE_DEFINED_VALUES.PACKING[0]];
    let address_check = this.state.address_check;
    let return_lable_check = this.state.return_lable_check;
    sampleService.getOrderById(id, (result) => {
      if (result) {
        this.state.locationList.map((e) => {
          if (result.location_name === e.name || result.location === e.id) {
            this.setState({ defaultLocation: e.name, isLocation: true, address: e.address, city: e.city, state: e.state, country: e.country, zipcode: e.zipcode, phone: e.phone })
          }
          if (e.address === result.shipping_address && e.city === result.shipping_city && e.state === result.shipping_state && e.country === result.shipping_country && e.zipcode == result.shipping_zipcode) {
            address_check = true
          }
          return false
        })
        this.state.kitList.map((e) => { if (result.kit === e.id) { this.state.selectedKit = [e] } return false })

        if (result.return_label_quantity !== null || result.return_label_cost !== null)
          return_lable_check = true

        if (result.manager !== null)
          this.state.managerList.map((e) => { if (result.manager === e.id) { this.state.selectedManager = [e] } return false })

        if (result.shipping_service !== null) {
          this.state.shippingServiceList.map((e) => {
            if (result.shipping_service === e.id) { this.state.selectedShippingService = [e] }
            if (e.trackable === true) { this.setState({ isShippingTrackable: e.trackable }) }
            return false
          })
        }
        if (result.shipping_method !== null) {
          this.state.ShippingList.map((e) => { if (result.shipping_method === e.id) { this.state.selectedShippingMethod = [e] } return false })
        }
        if (this.state.address === result.shipping_address && this.state.city === result.shipping_city && this.state.state === result.shipping_state && this.state.country === result.shipping_country && this.state.zipcode == result.shipping_zipcode)
          address_check = true

        if (result.packing) selectedPacking = [result.packing]

        this.setState({
          isEditOrder: true,
          attention: result.attention,
          bid_number: result.bid_number,
          charge_card: result.charge_card,
          comment: result.comment,
          created_by: result.created_by_fname && result.created_by_surname ? result.created_by_fname + " " + result.created_by_surname : null,
          emailCollection: result.email,
          selectedKitID: result.kit,
          location: result.location,
          selectedManagerId: result.manager,
          order_id: result.order_id,
          bcontact: result.phone,
          purchase_order: result.purchase_order,
          quantity: result.quantity,
          return_label_cost: result.return_label_cost,
          return_label_quantity: result.return_label_quantity,
          selectedShippingMethodId: result.shipping_method,
          selectedShippingServiceId: result.shipping_service,
          shipping_name: result.shipping_name,
          shipping_account: result.shipping_account,
          shipping_address: result.shipping_address,
          shipping_city: result.shipping_city,
          shipping_country: result.shipping_country,
          shipping_state: result.shipping_state,
          shipping_zipcode: result.shipping_zipcode,
          tracking_id: result.tracking_id,
          shipping_created_at: result.shipping_created_at ? moment(result.shipping_created_at).tz('America/Los_Angeles').format("MMM DD, YYYY") : "",
          address_check,
          return_lable_check,
          selectedPacking,
        });

        if (result.shipping_cost !== null && result.shipping_cost.$numberDecimal) {
          this.setState({ shipping_cost: result.shipping_cost.$numberDecimal })
        }
        else if (result.shipping_cost !== null) {
          this.setState({ shipping_cost: result.shipping_cost })
        }
      }
    });
  };
  updateOrderById = () => {
    let formdata = {
      location: this.state.location || null,
      shipping_method: this.state.selectedShippingMethodId || null,
      shipping_address: this.state.shipping_address,
      shipping_city: this.state.shipping_city,
      shipping_state: this.state.shipping_state,
      shipping_country: this.state.shipping_country,
      shipping_zipcode: this.state.shipping_zipcode,
      email: this.state.emailCollection,
      attention: this.state.attention,
      phone: this.state.bcontact || " ",
      bid_number: this.state.bid_number,
      kit: this.state.selectedKitID,
      manager: this.state.selectedManagerId || null,
      tracking_id: this.state.tracking_id,
      shipping_service: this.state.selectedShippingServiceId || null,
      quantity: this.state.quantity,
      shipping_account: this.state.shipping_account,
      comment: this.state.comment,
      return_label_quantity: this.state.return_label_quantity || null,
      return_label_cost: this.state.return_label_cost || null,
      purchase_order: this.state.purchase_order,
      charge_card: this.state.charge_card,
      shipping_name: this.state.shipping_name,
      shipping_cost: this.state.shipping_cost || null,
      isShippingTrackable: this.state.isShippingTrackable,
      packing: "",
    }
    if (this.state.selectedPacking[0] != this.state.packingList[0])
      formdata.packing = this.state.selectedPacking[0]

    confirmAlert({
      title: 'Confirm',
      message: 'Are you sure you want to update',
      buttons: [
        {
          label: 'Update',
          onClick: () => {
            sampleService.updateOrderById(this.state.order_id, formdata, result => {
              if (result) {
                this.setState({ tracking_id: result.tracking_id })
                this._getOrderCountList();
                this._getOrderList();
              }
            })
          }
        },
        {
          label: 'Cancel',
          onClick: () => console.log('cancel update')
        }
      ]
    });
  }
  deleteOrder = (id) => {
    confirmAlert({
      title: "Confirm",
      message: "Are you sure you want to delete",
      buttons: [{ label: "Delete", onClick: () => { sampleService.deleteOrder(id, (result) => { if (result) { this._getOrderList(); this._getOrderCountList(); } }); }, },
      { label: "Cancel", onClick: () => console.log("cancel delete"), },
      ],
    });
  };
  /*----------------------------------- Render ---------------------------*/
  _renderHeader = () => {
    const { classes } = this.props;
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={6}>
          <Card>
            <CardHeader color="info" stats icon>
              <CardIcon color="info">
                <Icon>P</Icon>
              </CardIcon>
              <p className={classes.cardCategory}>Pending Orders</p>
              <h3 className={classes.cardTitle}>
                {this.state.orderPendingCount}
              </h3>
            </CardHeader>
            {this.state.isLoading ? null : <CardFooter stats>
              <a href="#filter_pending"
                onClick={(e) => {
                  this.setState(
                    {
                      isPendingOrderView: !this.state.isPendingOrderView, isCompletedOrderView: false,
                    },
                    () => {
                      this._getOrderList()
                    }
                  );
                }}
              >
                {this.state.orderPendingCount ? this.state.isPendingOrderView ? "Show full" : "View" : "No results"}
              </a>
            </CardFooter>}

          </Card>
        </GridItem>
        <GridItem xs={12} sm={12} md={6}>
          <Card>
            <CardHeader color="success" stats icon>
              <CardIcon color="success">
                <Icon>C</Icon>
              </CardIcon>
              <p className={classes.cardCategory}>Completed Orders</p>
              <h3 className={classes.cardTitle}>
                {this.state.orderCompletedCount}
              </h3>
            </CardHeader>
            {this.state.isLoading ? null : <CardFooter stats>
              <a href="#filter_completed" onClick={(e) => {
                this.setState(
                  {
                    isPendingOrderView: false,
                    isCompletedOrderView: !this.state.isCompletedOrderView

                  },
                  () => {
                    this._getOrderList()
                  }
                );
              }}
              >
                {this.state.orderCompletedCount ? this.state.isCompletedOrderView ? "Show full" : "View" : "No results"} </a>
            </CardFooter>}

          </Card>
        </GridItem>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <ul className="nav nav-tabs" id="myTab" role="tablist">
              <li className="nav-item">
                <a className={this.state.onShowOrderTab ? " nav-link active" : "nav-link"} style={{ fontSize: 24 }} id="home-tab" data-toggle="tab" href="#Order" role="tab" aria-controls="home" aria-selected="true" >
                  Order Kit
                </a>
              </li>
            </ul>
          </Card>
        </GridItem>
        <GridItem xs={6} sm={6} md={6}>  <Button onClick={() => { this._handleReset(); this.setState({ isCreateOrder: !this.state.isCreateOrder }); }} color="info" > {this.state.isCreateOrder ? " Return " : "Create Order"} </Button> </GridItem>
      </GridContainer>
    );
  };
  _renderOrderForm = () => {
    const { classes } = this.props;
    const location_value = { name: this.state.defaultLocation };
    return (
      <Card>
        <CardHeader color="primary">    <h4 className={classes.cardTitleWhite}>      {this.state.isEditOrder ? `Edit Order ${this.state.order_id}` : "Create Order Kit Form"}  </h4>
          <p className={classes.cardCategoryWhite}> Complete order form </p>
        </CardHeader>
        <CardBody>
          <GridContainer>
            {!this.state.isLocation ? (
              <GridItem xs={12} sm={12} md={12}>
                <FormControl className={classes.formControl}>
                  <Autocomplete id="location-type-selection"
                    onChange={(event, newValue) => this._handleLocationSelection(newValue)}
                    options={this.state.locationList}
                    getOptionLabel={(option) => option.name}
                    style={{ backgroundColor: "white" }}
                    autoComplete
                    includeInputInList
                    renderInput={(params) => (<TextField  {...params} label="Location" variant="outlined" />)}
                  />
                </FormControl>
              </GridItem>
            ) : null}
            {this.state.isLocation ? (
              <GridItem xs={12} sm={12} md={12}>
                <FormControl className={classes.formControl}>
                  <Autocomplete
                    id="location-type-selection"
                    onChange={(event, newValue) => this._handleLocationSelection(newValue)}
                    value={location_value}
                    options={this.state.locationList}
                    getOptionLabel={option => option.name}
                    disablePortal
                    includeInputInList
                    autoComplete
                    renderInput={(params) => (<TextField   {...params} label="Location" variant="outlined" />)}
                  />
                </FormControl>
              </GridItem>
            ) : null}
            <br />
            <GridItem xs={12} sm={12} md={12}><br />  Present Address   </GridItem>
            <GridItem xs={12} sm={12} md={12}>
              <CustomInput
                labelText="Address"
                onChange={e => { this.setState({ address: e.target.value }); }}
                id="address"
                inputProps={{ type: "text", value: this.state.address, disabled: true }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="City"
                onChange={e => { this.setState({ city: e.target.value }); }}
                id="city"
                inputProps={{ type: "text", value: this.state.city, disabled: true }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="State"
                onChange={e => { this.setState({ state: e.target.value }); }}
                id="state"
                inputProps={{ type: "text", value: this.state.state, disabled: true }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="Country"
                onChange={e => { this.setState({ country: e.target.value }); }}
                id="country"
                inputProps={{ type: "text", value: this.state.country, disabled: true }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="Zip Code"
                onChange={e => { this.setState({ zipcode: e.target.value }); }}
                id="zipcode"
                inputProps={{ type: "text", value: this.state.zipcode, disabled: true }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={12}>
              <br />
              <FormControlLabel control={<Checkbox
                defaultChecked
                color="default"
                checked={this.state.address_check}
                onChange={this._handleSelectShippingAddress}
                inputProps={{ 'aria-label': 'checkbox with default color' }}
              />} label=" Click here if the shipping address same as present address" />
            </GridItem>

            <GridItem xs={12} sm={12} md={12}><br />  Shipping Address  </GridItem>
            <GridItem xs={12} sm={12} md={12}>
              <CustomInput
                labelText="Location Name"
                onChange={e => { this.setState({ shipping_name: e.target.value }); }}
                id="shipping_name"
                inputProps={{ type: "text", value: this.state.shipping_name }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={12}>
              <CustomInput
                labelText="Shipping Address"
                onChange={e => { this.setState({ shipping_address: e.target.value }); }}
                id="shipping_address"
                inputProps={{ type: "text", value: this.state.shipping_address }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>

            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="Shipping City"
                onChange={e => { this.setState({ shipping_city: e.target.value }); }}
                id="shipping_city"
                inputProps={{ type: "text", value: this.state.shipping_city }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="Shipping State"
                onChange={e => { this.setState({ shipping_state: e.target.value }); }}
                id="shipping_state"
                inputProps={{ type: "text", value: this.state.shipping_state }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="Shipping Country"
                onChange={e => { this.setState({ shipping_country: e.target.value }); }}
                id="shipping_country"
                inputProps={{ type: "text", value: this.state.shipping_country }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="Shipping Zipcode"
                onChange={e => { this.setState({ shipping_zipcode: e.target.value }); }}
                id="shipping_zipcode"
                inputProps={{ type: "text", value: this.state.shipping_zipcode }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>

            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="Billing Contact"
                onChange={e => { this.setState({ bcontact: e.target.value }); }}
                id="bcontact"
                inputProps={{ type: "text", value: this.state.bcontact }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <CustomMultiEmailInput
                id="email"
                disabled={true}
                inputProps={{ type: "text", value: this.state.value }}
                labelText="Billing Email"
                onKeyDown={this.handleKeyDown}
                onChange={this._handleEmailAddChange}
                onPaste={this._handleEmailPasteChange}
                formControlProps={{ fullWidth: true, }}
              />
              {this.state.emailError !== null ? <span style={{ color: 'red' }}>{this.state.emailError}</span> : null}
              {this.state.emailCollection.map((item, key) => (<div className={classes.chips} key={item}>   <Chip onClick={() => this._handleEmailRemoveChange(item)} key={key} label={item + "  X"} className={classes.chip} /></div>))}
            </GridItem>

            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="BId Number"
                onChange={e => { this.setState({ bid_number: e.target.value }); }}
                id="bid_number"
                inputProps={{ type: "text", value: this.state.bid_number }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="Purchase order"
                onChange={e => { this.setState({ purchase_order: e.target.value }); }}
                id="purchase_order"
                inputProps={{ type: "text", value: this.state.purchase_order }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>

            <GridItem xs={12} sm={12} md={6}>
              <br />
              <FormControl fullWidth >
                <TextField
                  label="Total Cost"
                  value={this.state.shipping_cost}
                  onChange={e => { this.setState({ shipping_cost: e.target.value }); }}
                  name="shipping_cost"
                  id="formatted-numberformat-input"
                  InputProps={{
                    inputComponent: this.NumberFormatCustom,
                  }}
                />
              </FormControl>
            </GridItem>
            <GridItem xs={12} sm={12} md={6}></GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <br />
              <FormControlLabel control={<Checkbox
                defaultChecked
                color="default"
                checked={this.state.charge_card}
                onChange={this._handleSelectChargeCard}
                inputProps={{ 'aria-label': 'checkbox with default color' }}
              />} label="Charge Card on File" />
            </GridItem>

            <GridItem xs={12} sm={12} md={6}>
              <br />
              <FormControlLabel control={<Checkbox
                defaultChecked
                color="default"
                checked={this.state.return_lable_check}
                onChange={this._handleSelectReturnLabel}
                inputProps={{ 'aria-label': 'checkbox with default color' }}
              />} label=" Return Label" />
            </GridItem>
            {this.state.return_lable_check ?
              <GridItem xs={12} sm={12} md={6}>
                <CustomInput
                  labelText="Quantity"
                  onChange={e => { this.setState({ return_label_quantity: e.target.value }); }}
                  id="return_label_quantity"
                  inputProps={{ type: "text", value: this.state.return_label_quantity }}
                  formControlProps={{ fullWidth: true, }}
                />
              </GridItem>
              : null}
            {this.state.return_lable_check ?
              <GridItem xs={12} sm={12} md={6}>
                <br />
                <FormControl fullWidth >
                  <TextField
                    label="Cost"
                    value={this.state.return_label_cost}
                    onChange={e => { this.setState({ return_label_cost: e.target.value }); }}
                    name="return_label_cost"
                    id="formatted-numberformat-input"
                    InputProps={{
                      inputComponent: this.NumberFormatCustom,
                    }}
                  />
                </FormControl>
              </GridItem>
              : null}

            <GridItem xs={12} sm={12} md={6}>
              <FormControl className={classes.formControl}>
                <InputLabel className={classes.InputLabelGrey} id={"manager-selection"}>Manager</InputLabel>
                <Select
                  labelId={"manager-selection"} id={"manager-selection"} name={"manager-selection"} single
                  value={this.state.selectedManager ? this.state.selectedManager : null}
                  onChange={this._handleManagerSelection}
                  input={<Input id="select-multiple-chip" />}
                  renderValue={(selected) => (<div className={classes.chips}>    {selected.map((value) => (<Chip key={value.id} label={value.fname} className={classes.chip} />))}    </div>)}
                  MenuProps={MenuProps}
                >
                  {this.state.managerList.map((name) => (<MenuItem key={name.id} value={name} >  {name.fname}  </MenuItem>))}
                </Select>
              </FormControl>
            </GridItem >
            <GridItem xs={12} sm={12} md={6}>
              <FormControl className={classes.formControl}>
                <InputLabel className={classes.InputLabelGrey} id={"shipping-selection"}>Shipping Method</InputLabel>
                <Select labelId={"shipping-selection"} id={"shipping-selection"} name={"shipping-selection"} single
                  value={this.state.selectedShippingMethod}
                  onChange={this._handleShippingMethodSelection}
                  input={<Input id="select-multiple-chip" />}
                  renderValue={(selected) => (<div className={classes.chips}> {selected.map((value) => (<Chip key={value.id} label={value.name} className={classes.chip} />))}  </div>
                  )}
                  MenuProps={MenuProps}
                >
                  {this.state.ShippingList.map((name) => (<MenuItem key={name.id} value={name} >    {name.name} </MenuItem>))}
                </Select>
              </FormControl>
            </GridItem >
            <GridItem xs={12} sm={12} md={6}>
              <FormControl className={classes.formControl}>
                <InputLabel className={classes.InputLabelGrey} id={"kit-selection"}>Kit</InputLabel>
                <Select
                  labelId={"kit-selection"} id={"kit-selection"} name={"kit-selection"} single
                  value={this.state.selectedKit}
                  onChange={this._handleKitSelection}
                  input={<Input id="select-multiple-chip" />}
                  renderValue={(selected) => (<div className={classes.chips}>  {selected.map((value) => (<Chip key={value.id} label={value.name} className={classes.chip} />))}  </div>)}
                  MenuProps={MenuProps}
                >
                  {this.state.kitList.map((name) => (<MenuItem key={name.id} value={name} >   {name.name}  </MenuItem>))}
                </Select>
              </FormControl>
            </GridItem >
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="Quantity"
                onChange={e => { this.setState({ quantity: e.target.value }); }}
                id="quantity"
                inputProps={{ type: "number", value: this.state.quantity }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>

            <GridItem xs={12} sm={12} md={6}>
              <FormControl className={classes.formControl}>
                <InputLabel className={classes.InputLabelGrey} id={"packing-selection"}>Packing</InputLabel>
                <Select labelId={"packing-selection"} id={"packing-selection"} name={"packing-selection"} single
                  value={this.state.selectedPacking}
                  onChange={this._handlePackingSelection}
                  input={<Input id="select-packing-chip" />}
                  renderValue={(selected) => (<div className={classes.chips}>  {selected.map((value, index) => (<Chip key={index} label={value} className={classes.chip} />))}  </div>)}
                  MenuProps={MenuProps}
                >
                  {this.state.packingList.map((name, index) => (<MenuItem key={`packing-${index}`} value={name} >{name}</MenuItem>))}
                </Select>
              </FormControl>
            </GridItem >
            <GridItem xs={12} sm={12} md={6}>
              <FormControl className={classes.formControl}>
                <InputLabel className={classes.InputLabelGrey} id={"shipping-selection"}>Shipping Service</InputLabel>
                <Select labelId={"shipping-selection"} id={"shipping-selection"} name={"shipping-selection"} single
                  value={this.state.selectedShippingService}
                  onChange={this._handleShippingSeviceSelection}
                  input={<Input id="select-multiple-chip" />}
                  renderValue={(selected) => (<div className={classes.chips}>  {selected.map((value) => (<Chip key={value.id} label={value.name} className={classes.chip} />))}  </div>)}
                  MenuProps={MenuProps}
                >
                  {this.state.shippingServiceList.map((name) => (<MenuItem key={name.id} value={name} >  {name.name} </MenuItem>))}
                </Select>
              </FormControl>
            </GridItem >
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="Tracking ID"
                onChange={e => { this.setState({ tracking_id: e.target.value }); }}
                id="tracking_id"
                inputProps={{ type: "text", value: this.state.tracking_id }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="Shipping Account"
                onChange={e => { this.setState({ shipping_account: e.target.value }); }}
                id="shipping_account"
                inputProps={{ type: "text", value: this.state.shipping_account }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText="Attention"
                onChange={e => { this.setState({ attention: e.target.value }); }}
                id="attention"
                inputProps={{ type: "text", value: this.state.attention }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
            <GridItem xs={12} sm={12} md={12}>
              <CustomInput
                labelText="Comment"
                onChange={e => { this.setState({ comment: e.target.value }); }}
                id="comment"
                inputProps={{ type: "text", value: this.state.comment }}
                formControlProps={{ fullWidth: true, }}
              />
            </GridItem>
          </GridContainer>
          {this.state.isEditOrder === true
            ? this.state.created_by === null
              ? null
              : (<>
                <GridContainer>
                  <GridItem xs={12} sm={12} md={6}><p style={{ paddingTop: 15 }}><b>Shipped At : </b>{this.state.shipping_created_at}</p></GridItem>
                </GridContainer>
                <GridContainer>
                  <GridItem xs={12} sm={12} md={6}><p style={{ paddingTop: 15 }}><b>Created By : </b>{this.state.created_by}</p></GridItem>
                </GridContainer>
              </>)
            : null}
        </CardBody>
        <CardFooter>
          {this.state.ErrorMessage ? <h5 style={{ color: 'red' }}>{this.state.ErrorMessage}</h5> : null}
          <Button onClick={this._handleReset} color="danger">Reset</Button>
          {!this.state.isEditOrder ? (<Button onClick={this._onCreateOrderKit} color="success"> Create Order </Button>) : null}
          {this.state.isEditOrder ? (<Button onClick={this.updateOrderById} color="warning"> Update Order </Button>) : null}
        </CardFooter>
      </Card>
    )
  }
  _renderOrderKitTable = () => {
    const { classes } = this.props;
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="info">
              <h4 className={classes.cardTitleWhite}>
                {this.state.orderStatus === "pending" ? "Pending  Order Kit" : this.state.orderStatus === "shipped" ? "Shipped Order Kit" : "Available  Order Kit"}
              </h4>
            </CardHeader>
            <CardBody>

              {this.state.isLoading ? (
                <GridItem xs={12} sm={12} md={4}>  <Loader type="ThreeDots" color="#a5d1e6" height={50} width={50} />  </GridItem>
              ) : (
                <div>
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={4}>
                      <CustomInput labelText={"Search"} id={"search"} onChange={this._handleSearchFilter} inputProps={{ type: "text" }} formControlProps={{ fullWidth: true, }} />
                    </GridItem>
                  </GridContainer>
                  <div className={classes.tableResponsive}>

                    <Table className={classes.table}>
                      <TableHead className={classes.info}>
                        <TableRow className={classes.tableHeadRow}>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"orderId"}>Order ID </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"shipping_address"} width="130"  >  Location Name </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"kitName"} > Kit </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"quantity"} >Quantity </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"shipping_service_name"} width="140"> Shipping Service </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"tracking_id"} > Tracking Id </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"status"} >Status</TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"created-on"} >Created on</TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"option"} > Options </TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                        {this.state.orderList.length ? (this.state.orderList
                          .slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage + this.state.rowsPerPage)
                          .map((value, key) => (
                            <TableRow key={key} tabIndex={-1} className={classes.tableBodyRow} >
                              <TableCell className={classes.tableCell} key={"orderId" + key}> {value.order_id} </TableCell>
                              <TableCell className={classes.tableCell} key={"shipping_address" + key} >{value.location_name} </TableCell>
                              <TableCell className={classes.tableCell} key={"kitName" + key} >  {value.kit_name} </TableCell>
                              <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"quantity"} >{value.quantity || null} </TableCell>
                              <TableCell className={classes.tableCell} key={"shipping_service_name" + key}  > {value.shipping_service_name || null}  </TableCell>
                              <TableCell className={classes.tableCell} key={"tracking_id" + key} ><Link className={classes.urlLink} onClick={() => this._handleTrackingURL(value)} > <b>  {value.tracking_id || null}</b> </Link ></TableCell>
                              <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"status"} > {value.shipping_service_name ? "Shipped" : "Pending"} </TableCell>
                              <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"created-at-" + key} > {value.createdAt ? moment(value.createdAt).tz('America/Los_Angeles').format("MMM DD, YYYY") : ""} </TableCell>
                              <TableCell className={classes.tableCell} key={"option" + key}>
                                <DeleteForeverIcon onClick={() => this.deleteOrder(value.order_id)} style={{ color: red[500] }} />
                                <BorderColorIcon onClick={() => this.getOrderbyID(value.order_id)} />
                              </TableCell>
                            </TableRow>
                          ))
                        ) : (
                          <TableRow>No records available</TableRow>
                        )}
                      </TableBody>
                    </Table>
                    <TablePagination component="div" count={this.state.orderList.length} rowsPerPage={this.state.rowsPerPage} page={this.state.page} onChangePage={this._handleChangePage} onChangeRowsPerPage={this._handleChangeRowsPerPage} />
                  </div>
                </div>)}
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    );
  };
  render() {
    return (
      <div>
        {this._renderHeader()}
        {this.state.isCreateOrder ? this._renderOrderForm() : null}
        {this.state.onShowOrderTab ? this._renderOrderKitTable() : null}
      </div>
    );
  }
}

export default withStyles(useStyles)(OrderKitsViewPage);
