import React from "react";
import { withStyles } from "@material-ui/core/styles";
import 'react-confirm-alert/src/react-confirm-alert.css';
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import Button from "components/CustomButtons/Button.js";
import CustomInput,{ CustomMultiEmailInput, } from "components/CustomInput/CustomInput";
import { userService,sampleService} from "services/api.services";
import Autocomplete from 'components/Autocomplete/Autocomplete';
import { FormControl,TextField,Chip } from "@material-ui/core";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TablePagination from "@material-ui/core/TablePagination";
import Loader from "react-loader-spinner";
import { confirmAlert } from "react-confirm-alert";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import BorderColorIcon from "@material-ui/icons/BorderColor";
import { red } from "@material-ui/core/colors";
import moment from "moment";
import PictureAsPdfIcon from "@material-ui/icons/PictureAsPdf";
import auth from "services/auth"
const useStyles = () => (styles)
const date = new Date().toLocaleString();
const emailRegex = /[\w\d\.-]+@[\w\d\.-]+\.[\w\d\.-]+/g

class Environmental_lab_reports extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      role:auth.authRole(),
      Env_labreportList:[],isLoading:true,
        isCreateSIF:false,locationList:[],location:'',lab_id:'',coc_number:'',project_no:'',reportDate:moment(date).format('YYYY-MM-DD'),
        //multiple Email
        emailCollection: [], value: "", emailError: null,
        file_name:'',record_id:'',onEditLabRecord:false,reportPDF:null,defaultLocation:'',isLocation:false,po_number:'',report_path:'',
        // Pagination
        page: 0, rowsPerPage: 10,
     
    }
  }
  componentDidMount = () => {
    userService.getAllLocation((locationList) => { if (locationList && locationList.data && locationList.data.length) this.setState({ locationList: locationList.data || [], }); });
    this._onLoad()
  }

  _onLoad = () => {
    // this.state.role=auth.authRole()
    this._getAllEnv_labreport()
  }

  /***********************************Handle********************************************** */
  _handleReset=()=>{
      this.setState({location:'',lab_id:'',coc_number:'',project_no:'',reportDate:moment(date).format('YYYY-MM-DD'),
    //multiple Email
    emailCollection: [], value: "", emailError: null, file_name:'',record_id:'',onEditLabRecord:false,reportPDF:null,defaultLocation:'',isLocation:true,po_number:'',report_path:''})
  }
  _handleLocationSelection=(location)=>{
    if(location){ 
      var tempEmail = [];
      if (location.email && location.email.length > 0) {
        location.email.map((email) => {
          if (
            email !== undefined && email !== "undefined" && email !== null &&
            email !== ""
          ) {
            return tempEmail.push(email);
          }
          return false;
        });
      }
      if (
        location.primaryemail !== undefined && location.primaryemail !== "undefined" && location.primaryemail !== null && location.primaryemail !== ""
      ) {
        tempEmail.push(location.primaryemail);
      }
    this.setState({location:location.id,emailCollection: tempEmail || [],defaultLocation:location.name})
    }
  }
  _handleReportDate = (event) => {
    this.setState({ reportDate: event.target.value });
  };
  /*------------------------------ Pagination -----------------------------------*/
  _handleChangePage = (event, newPage) => { this.setState({ page: newPage }); };
  _handleChangeRowsPerPage = (event) => { this.setState({ rowsPerPage: parseInt(+event.target.value) }); this.setState({ page: 0 }); };
 /*----------------- Multiple email entry handle ------------------*/
 handleKeyDown = (evt) => {
    if (["Enter", "Tab", ","].includes(evt.key)) {
      evt.preventDefault();
      var value = this.state.value.trim();
      if (value && this._handleEmailIsValid(value)) {
        this.setState({
          emailCollection: [...this.state.emailCollection, this.state.value], value: "",
        });
      }
    }
  };
  _handleEmailAddChange = (evt) => {
    this.setState({
      value: evt.target.value,
      emailError: null,
    });
  };
  _handleEmailRemoveChange = (item) => {
    this.setState({
      emailCollection: this.state.emailCollection.filter((i) => i !== item),
    });
  };
  _handleEmailPasteChange = (evt) => {
    evt.preventDefault();
    var paste = evt.clipboardData.getData("text");
    var emails = paste.match(emailRegex);
    if (emails) {
      var toBeAdded = emails.filter((email) => !this._handleEmailIsInList(email));
      this.setState({ emailCollection: [...this.state.emailCollection, ...toBeAdded], });
    }
  };
  _handleEmailIsValid(email) {
    let emailError = null;
    if (this._handleEmailIsInList(email)) { emailError = `${email} has already been added.`; }
    if (!this._handleEmailIsIsEmail(email)) { emailError = `${email} is not a valid email address.`; }
    if (emailError) { this.setState({ emailError }); return false; }
    return true;
  }
  _handleEmailIsInList(email) {
    return this.state.emailCollection.includes(email);
  }
  _handleEmailIsIsEmail(email) {
    return /[\w\d\.-]+@[\w\d\.-]+\.[\w\d\.-]+/.test(email);
  }
  _uploadPDF=(event)=>{
    this.dofileUpload.click()
    
  }
  handleFile=(event)=>{
    event.preventDefault();
    this.setState({reportPDF:event.target.files[0],file_name:event.target.files[0].name,report_path:''})
    event.target.value = null
    }
  
  /**********************************Api integration******************************************** */
  _getAllEnv_labreport=()=>{
    // this.setState({isLoading:true})
    sampleService.getAllEnv_labreport(result => { if (result) { 
     this.setState({ Env_labreportList: result.data})
     this.setState({ isLoading: false })
      }
    })
   }
  createEnv_labreport=()=>{
    let formdata = {
    reportPDF:this.state.reportPDF,
    location:this.state.location ||null,
    lab_id:this.state.lab_id,
    emailCollection:this.state.emailCollection,
    project_no:this.state.project_no,
    coc_number:this.state.coc_number,
    reportDate:this.state.reportDate,
    po:this.state.po_number
  }
    if(this.state){
      sampleService.createEnv_labreport(formdata, (result) => {
        if(result){
          // console.log(result)
          this.setState({onEditLabRecord:true,record_id:result._id,report_path:result.report_path,file_name:''})
          // this._handleReset();
          this._getAllEnv_labreport();
        }
      });
    }
  }
  _getEnv_labreportById = (id) => {
    this._handleReset();
    sampleService.getEnv_labreportById(id, (result) => {
      if (result) {
        console.log(result)
        this.state.locationList.map((ele)=>{
          if(result.location_name === ele.name && result.location === ele.id){
              var tempEmail = [];
              if (ele.email && ele.email.length > 0) {
                ele.email.map((email) => {
                  if (
                    email !== undefined && email !== "undefined" && email !== null &&
                    email !== ""
                  ) {
                    return tempEmail.push(email);
                  }
                  return false;
                });
              }
              if (
                ele.primaryemail !== undefined && ele.primaryemail !== "undefined" && ele.primaryemail !== null && ele.primaryemail !== ""
              ) {
                tempEmail.push(ele.primaryemail);
              }
            this.setState({location:ele.id,emailCollection: tempEmail || [],isLocation:true})
          }
          return false
        })
        this.setState({
          onEditLabRecord:true,
          isCreateSIF:true,
          lab_id:result.labid,
          defaultLocation:result.location_name,
          coc_number:result.coc || '',
          project_no:result.project_number ||'',
          reportDate:result.reported_at === null? moment(date).format('YYYY-MM-DD'):moment(result.reported_at).format("YYYY-MM-DD"),
          record_id:result._id,
          po_number:result.po|| '',
          report_path:result.report_path || ''
        });
      }
    });
  };
  _updateEnv_labreport = () => {
    let formdata = {
      reportPDF:this.state.reportPDF,
      location:this.state.location ||null,
      lab_id:this.state.lab_id,
      emailCollection:this.state.emailCollection,
      project_no:this.state.project_no,
      coc_number:this.state.coc_number,
      reportDate:this.state.reportDate,
      po:this.state.po_number
    }
    if(this.state){
        confirmAlert({
            title: 'Confirm',
            message: 'Are you sure you want to update',
            buttons: [
                {
                    label: 'Update',
                    onClick: () => {
                        sampleService.updateEnv_labreport(this.state.record_id,formdata, result => {
                            if (result) {
                                this._getAllEnv_labreport();
                            }
                        })
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => console.log('cancel update')
                }
            ]
        });
    } 
  }
  _deleteEnv_labreport = (id) => {
    confirmAlert({
      title: "Confirm",
      message: "Are you sure you want to delete",
      buttons: [{ label: "Delete", onClick: () => { sampleService.deleteEnv_labreport(id, (result) => { if (result) { this._getAllEnv_labreport(); } }); }, },
      { label: "Cancel", onClick: () => console.log("cancel delete"), },
      ],
    });
  };
  getpdf = (report_path) => {
    sampleService.downloadReportpdf(report_path, (result) => { });
  };
  _handleSearchFilter = (event) => {  this.setState({ isLoading: true });
  sampleService.searchEnvLabReport(event.target.value, (result) => {
    if (result) { this.setState({Env_labreportList: result.data, isLoading: false,  page: 0,     });  }
  });
};
_sendMail = () => {
  confirmAlert({
    title: "Send",
    message: "Are you sure you want to send this report",
    buttons: [
      {
        label: "Send",
        onClick: () => {
          sampleService.sendMailtoClient(this.state.lab_id, (result) => { });
        },
      },
      {
        label: "Cancel",
        onClick: () => console.log("cancel send report"),
      },
    ],
  });
};
  /*******************************Render****************************************** */
  _renderHeader = () => {
    return (
      <GridContainer>
         <GridItem xs={6} sm={6} md={6}><Button onClick={() => {
           this._handleReset(); this.setState({ isCreateSIF: !this.state.isCreateSIF }); }} 
           color="info" > {this.state.isCreateSIF ? " Return " : "Create SIF"} </Button>
            </GridItem> 
      </GridContainer>
    );
  };
  _renderForm = () => {
    const { classes } = this.props;
    const location_value = { name: this.state.defaultLocation };
     return (
          <Card>
              <CardHeader color="primary">  
              <h4 className={classes.cardTitleWhite}>{this.state.onEditLabRecord===true ?  `Edit FOI Lab Environmental of ${this.state.lab_id}`:`FOI Lab Environmental Form`}</h4>
              <p className={classes.cardCategoryWhite}> Complete FOI Lab Environmental Form  </p>
              </CardHeader>
            <CardBody>
            <GridContainer>
            {!this.state.isLocation ? (
              <GridItem xs={12} sm={12} md={12}>
                <FormControl className={classes.formControl}>
                  <Autocomplete  id="location-type-selection"
                    onChange={(event, newValue) =>  this._handleLocationSelection(newValue) }
                    options={this.state.locationList}
                    getOptionLabel={(option) => option.name}
                    style={{ backgroundColor: "white" }}
                    autoComplete
                    includeInputInList
                    renderInput={(params) => (   <TextField  {...params}  label="Location"  variant="outlined" />   )}
                  />
                </FormControl>
              </GridItem>
            ) : null}
            {this.state.isLocation ? (
              <GridItem xs={12} sm={12} md={12}>
                <FormControl className={classes.formControl}>
                  <Autocomplete
                    id="location-type-selection"
                    onChange={(event, newValue) =>   this._handleLocationSelection(newValue)   }
                    value={location_value}
                    options={this.state.locationList}
                    getOptionLabel={option => option.name}
                    disablePortal
                    includeInputInList
                    autoComplete
                    renderInput={(params) => ( <TextField   {...params}  label="Location" variant="outlined"  />        )}
                  />
                </FormControl>
              </GridItem>
            ) : null}
              <GridItem xs={12} sm={12} md={6}>
                <CustomInput
                labelText="Lab ID"
                onChange={e => {this.setState({ lab_id: e.target.value });    }}
                id="lab_id"
                inputProps={{ type: "text", value: this.state.lab_id }}
                formControlProps={{   fullWidth: true,   }}
                />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
                <CustomInput
                labelText="C O C Number"
                onChange={e => {this.setState({ coc_number: e.target.value });    }}
                id="coc_number"
                inputProps={{ type: "text", value: this.state.coc_number }}
                formControlProps={{   fullWidth: true,   }}
                />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
                <CustomInput
                labelText="Project Number"
                onChange={e => {this.setState({ project_no: e.target.value });    }}
                id="project_no"
                inputProps={{ type: "text", value: this.state.project_no }}
                formControlProps={{   fullWidth: true,   }}
                />
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
                <CustomInput
                labelText="PO Number"
                onChange={e => {this.setState({ po_number: e.target.value });    }}
                id="po_number"
                inputProps={{ type: "text", value: this.state.po_number }}
                formControlProps={{   fullWidth: true,   }}
                /> 
            </GridItem>
            <GridItem xs={12} sm={12} md={6}>
              <CustomInput
                labelText={"Report Date"}
                id="Reportdate"
                onChange={this._handleReportDate}
                labelProps={{ shrink: true }}
                inputProps={{ type: "date", value: this.state.reportDate }}
                formControlProps={{
                  fullWidth: true,
                }}
              />
            </GridItem>
           
            <GridItem xs={12} sm={12} md={6}>
                            <CustomMultiEmailInput
                                id="email"
                              disabled={true}
                              inputProps={{ type: "text", value: this.state.value }}
                              labelText="Additional Email"
                              onKeyDown={this.handleKeyDown}
                              onChange={this._handleEmailAddChange}
                              onPaste={this._handleEmailPasteChange}
                              formControlProps={{    fullWidth: true,  }}
                            />
                             {this.state.emailError !== null ? <span style={{color:'red'}}>{this.state.emailError}</span> :null}
                            {this.state.emailCollection.map((item, key) => (<div className={classes.chips} key={item}>   <Chip onClick={() => this._handleEmailRemoveChange(item)} key={key} label={item + "  X"} className={classes.chip} /></div>))}
                          </GridItem>   
              </GridContainer>
              
            </CardBody>
            <CardFooter>
              <div className={classes.FileNameAlignment}>
              {this.state.report_path !== ""? <Button className={classes.SIFbutton} onClick={() => this.getpdf(this.state.report_path)} color="info" > <PictureAsPdfIcon  /> </Button>  :null}
              <Button className={classes.SIFbutton} onClick={this._uploadPDF} color="info" >  Upload PDF </Button> 
             <span style={{paddingLeft:'10px'}}>{this.state.file_name}</span>
              </div>
          <input type="file" className={classes.hidden}
            multiple={false}
            accept=".pdf"
            onChange={event => this.handleFile(event)}
            ref={e=>this.dofileUpload = e}
          />
            <div className={classes.CardFooterAlignment} >
            <Button className={classes.SIFbutton} onClick={this._handleReset} color="danger"> Reset </Button>
            {this.state.onEditLabRecord===true ?   <Button className={classes.SIFbutton} onClick={this._sendMail} color="success"> Submit to Client </Button> :null}
            {this.state.onEditLabRecord===false ?<Button className={classes.SIFbutton} onClick={this.createEnv_labreport} color="success"> Create  </Button> :null}
              {this.state.onEditLabRecord===true ? <Button className={classes.SIFbutton} onClick={this._updateEnv_labreport} color="warning"> Update </Button>:null}
             </div>
            </CardFooter>
          </Card>
      )
  }
  _renderTable=()=>{
    const { classes } = this.props;
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="info"> <h4 className={classes.cardTitleWhite}>FOI Lab Environmental List </h4> </CardHeader>
            <CardBody>
              <GridItem xs={12} sm={12} md={4}>
                <CustomInput labelText={"Search"} id={"search"} onChange={this._handleSearchFilter} inputProps={{ type: "text" }}  formControlProps={{ fullWidth: true,  }} />
              </GridItem>
              {this.state.isLoading ? (
                <GridItem xs={12} sm={12} md={4}>  <Loader type="ThreeDots" color="#a5d1e6"  height={50}  width={50}/>  </GridItem>
              ) : (
                  <div className={classes.tableResponsive}>
                     <Table className={classes.table}>
                        <TableHead className={classes.info}>
                          <TableRow className={classes.tableHeadRow}>
                          <TableCell className={ classes.tableCell + " " + classes.tableHeadCell} key={"pdf"} > PDF  </TableCell>
                          <TableCell className={ classes.tableCell + " " + classes.tableHeadCell} key={"location"}   >  Location  </TableCell>
                          <TableCell className={ classes.tableCell + " " + classes.tableHeadCell} key={"Lab_id"}   >  Lab ID  </TableCell>
                          <TableCell className={ classes.tableCell + " " + classes.tableHeadCell} key={"coc"}   >  COC Number  </TableCell>
                          <TableCell className={ classes.tableCell + " " + classes.tableHeadCell} key={"po"}   > PO Number  </TableCell>
                        <TableCell className={ classes.tableCell + " " + classes.tableHeadCell} key={"report_date"}   >  Reported Date  </TableCell>
                          
        {this.state.role !== 'customer' ?  <TableCell  className={ classes.tableCell + " " + classes.tableHeadCell } key={"option"} > Options </TableCell>
                        :null}    </TableRow>
                        </TableHead>
                        <TableBody>
                          {this.state.Env_labreportList.length ? ( this.state.Env_labreportList
                          .slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage +this.state.rowsPerPage )
                              .map((value, key) => (
                                <TableRow key={key} tabIndex={-1} className={classes.tableBodyRow} >
                                  <TableCell  className={classes.tableCell} key={key}> <PictureAsPdfIcon  onClick={() => this.getpdf(value.report_path)} /> </TableCell>
                                  <TableCell   className={classes.tableCell} key={"location" + key} >{value.location_name} </TableCell>
                                  <TableCell className={classes.tableCell} key={"Lab_id" + key }   >  {value.labid}  </TableCell>
                                  <TableCell className={classes.tableCell} key={"coc" + key }   >  {value.coc || null}  </TableCell>
                                  <TableCell className={classes.tableCell} key={"po" + key }   >  {value.po || null}  </TableCell>
                                  <TableCell className={classes.tableCell} key={"report_date" + key }   > {value.reported_at === null ? null :moment(value.reported_at).format("MMM DD, YYYY") }</TableCell>
                                  {this.state.role !== 'customer' ?   <TableCell className={classes.tableCell} key={"option" + key}>
                                    <DeleteForeverIcon onClick={() => this._deleteEnv_labreport(value._id)} style={{ color: red[500] }} />
                                    <BorderColorIcon onClick={() => this._getEnv_labreportById(value._id)}/>
                                  </TableCell> :null}
                               
                                </TableRow>
                              ))
                          ) : (
                              <TableRow>No records available</TableRow>
                            )}
                        </TableBody>
                      </Table>
                    <TablePagination component="div" count={this.state.Env_labreportList.length} rowsPerPage={this.state.rowsPerPage} page={this.state.page} onChangePage={this._handleChangePage} onChangeRowsPerPage={this._handleChangeRowsPerPage} />
                  </div>
                )}
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    )
  }
render() {
    return (
      <div>
         {this.state.role !== 'customer' ?this._renderHeader() :null}
        {this.state.isCreateSIF ? this._renderForm() : null}
        {this._renderTable()}
      </div>
    );
  }
}

export default withStyles(useStyles)(Environmental_lab_reports);
