  getpdf = (labid, generateNew) => {
    if (generateNew) {
      sampleService.generateAndDownloadpdf(labid, (result) => { });
    } else {
      sampleService.downloadpdf(labid, (result) => { });
    }
  };

  _resetCheck = () => {
    var temp = [];
    this.state.sifList
      .slice(
        this.state.page * this.state.rowsPerPage,
        this.state.page * this.state.rowsPerPage + this.state.rowsPerPage
      )
      .map((value, key) => {
        document.getElementById(value.labid).checked = false;
        return temp.push(value.labid);
      });
    document.getElementById("select-all").checked = false;
    this.setState({ multiLabIDSelected: [] });

  }

  _handleMultiSIFsend = () => {
    if (this.state.multiLabIDSelected && this.state.multiLabIDSelected.length) {
      sampleService.mailMultiSIF(this.state.multiLabIDSelected, () => {
        this._resetCheck()
      })
    }
  };
  _handleMultiSelect = (event) => {
    var temp = this.state.multiLabIDSelected;
    if (event.target.checked) {
      if (!temp.includes(event.target.id)) {
        temp.push(event.target.id);
      }
    } else {
      if (temp.includes(event.target.id)) {
        temp = temp.filter((e) => e !== event.target.id);
      }
    }
    document.getElementById("select-all").checked = false;
    this.setState({ multiLabIDSelected: temp });
  };
  _handleSelectAll = (event) => {
    if (event.target.checked) {
      var temp = [];
      this.state.sifList
        .slice(
          this.state.page * this.state.rowsPerPage,
          this.state.page * this.state.rowsPerPage + this.state.rowsPerPage
        )
        .map((value, key) => {
          document.getElementById(value.labid).checked = true;
          return temp.push(value.labid);
        });
      this.setState({ multiLabIDSelected: temp });
    }
  };

  _handleRequireCustomLabIDSection = () => this.setState({ requireCustomLabIDSection: !this.state.requireCustomLabIDSection })

  _deleteFuelTextSelected = async fuelTest => {
    const { id: fuelTestId } = fuelTest
    const { selectedSif: { labid } } = this.state;
    const { status = false } = await sampleService.disposeFuelTest(labid, fuelTestId)
    if (status) {
      sampleService.getSIFById(labid, (result, i) => {
        const tempFuel = [];
        this.state.fuelList.map((fuel) => {
          if (fuel._id === result.fuel) {
            return tempFuel.push(fuel);
          }
          return false;
        });
        const applicableTestID = [];
        const applicableTest = [];
        if (tempFuel.length) {
          tempFuel.map((fuel) => {
            if (fuel.test.length) {
              fuel.test.map((test) => {
                if (applicableTestID.indexOf(test._id) === -1) {
                  return applicableTestID.push(test._id);
                }
                return false;
              });
            }
            return false;
          });
        }
        applicableTestID.map((testid) => {
          this.state.labTestList.map((labtest) => {
            if (labtest._id === testid) {
              return applicableTest.push(labtest);
            }
            return false;
          });
          return true;
        });
        const selectedTest = [];
        if (result.fueltest && result.fueltest.length) {
          result.fueltest.map((test) => {
            this.state.labTestList.map((labtest) => {
              if (test.testid === labtest.id) {
                var temp = labtest;
                temp._id = test._id
                temp.result = test.result;
                temp.comment = test.comment;
                selectedTest.push(temp);
                return true;
              }
              return false;
            });
            return false;
          });
        }

        this.setState({
          selectedFuelTypes: tempFuel,
          applicableTest: applicableTest,
          selectedFuelTest: selectedTest,
        })
      })
    }
  }

  _handleCreatedDate = (event) => {
    this.setState({ createdDate: event.target.value });
  };

