import Dashboard from "@material-ui/icons/Dashboard";
import LibraryBooks from "@material-ui/icons/LibraryBooks";
import InsertChart from "@material-ui/icons/InsertChart";
import auth from "services/auth"
import DashboardPage from "views/Dashboard";
import Sample from "views/SampleV2";
import Trending from "views/Trending";
import Controller from "views/Controller";
import PersonViewPage from "views/Person";
import ReportPage from 'views/Reports';
import OrderKits from 'views/OrderKits';
import CheckList from 'views/CheckList';
import Environmental_lab_reports from 'views/Environmental_lab_reports';
import GC_FTIR_OIL_Report, { SECTION_NAME as GC_FTIR_OIL_SECTION_NAME } from 'views/GC_FTIR_OIL_Report';
import { userManagementConfig as userManagement } from "views/UserManagement";
import { workFlowManagementConfig as workFlowManagement } from "views/WorkFlowManagement";

/* 
**************** Permission Level control ****************
 authorizedUserOnlyPermission = Any authorized user permitted
 employeeLevelPermission = Authorized till Employee Level
 managerLevelPermission = Authorized till Manager Level
 adminOnlyPermission = Authorized for admin level only
 customerOnlyPermission = Authorized for customer level only
*/
const authRole = auth.authRole();
const authorizedUserOnlyPermission = authRole == 'admin' || authRole == 'manager' || authRole == 'employee' || authRole == 'customer';
const employeeLevelPermission = authRole == 'admin' || authRole == 'manager' || authRole == 'employee';
const managerLevelPermission = authRole == 'admin' || authRole == 'manager';
const customerOnlyPermission = authRole == 'customer';

const dashboardRoutes = [
  {
    path: "/dashboard",
    name: "Dashboard",
    icon: Dashboard,
    component: DashboardPage,
    layout: "/home"
  }
];

if (customerOnlyPermission)
  dashboardRoutes.push({
    path: "/my-reports",
    name: "Reports",
    icon: "content_paste",
    component: ReportPage,
    layout: "/home"
  })

if (employeeLevelPermission)
  dashboardRoutes.push({
    path: "/order-booking",
    name: "Sample",
    icon: "content_paste",
    component: Sample,
    layout: "/home"
  },
    {
      path: workFlowManagement.path,
      name: workFlowManagement.name,
      icon: workFlowManagement.icon,
      component: workFlowManagement.component,
      layout: workFlowManagement.layout
    },
  );

if (authorizedUserOnlyPermission)
  dashboardRoutes.push({
    path: "/trending",
    name: "Trending",
    icon: InsertChart,
    component: Trending,
    layout: "/home"
  },
    {
      path: "/orderKits",
      name: "OrderKits",
      icon: "shopping_cart",
      component: OrderKits,
      layout: "/home"
    },
    {
      path: "/checklist",
      name: "Check List",
      icon: "fact_check",
      component: CheckList,
      layout: "/home"
    },
    {
      path: "/environmental_lab_reports",
      name: "FOI Lab Environmental",
      icon: "assignment_turned_in",
      component: Environmental_lab_reports,
      layout: "/home"
    },
    {
      path: "/gc_ftir_oil_report",
      name: `${GC_FTIR_OIL_SECTION_NAME} Report`,
      icon: "assignment_turned_in",
      component: GC_FTIR_OIL_Report,
      layout: "/home"
    })

if (authRole == 'admin')
  dashboardRoutes.push({
    path: "/value-control",
    name: "Controller",
    icon: LibraryBooks,
    component: Controller,
    layout: "/home"
  })

if (authorizedUserOnlyPermission)
  dashboardRoutes.push(
    {
      path: userManagement.path,
      name: userManagement.name,
      icon: userManagement.icon,
      component: userManagement.component,
      layout: userManagement.layout,
    },
    {
      path: "/profile",
      name: "Profile",
      icon: "manage_accounts",
      component: PersonViewPage,
      layout: "/home"
    })

export default (auth.isAuthenticated() && authRole) ? dashboardRoutes : [];
