import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// nodejs library to set properties for components
import PropTypes from "prop-types";

// material-ui components
import { makeStyles } from "@material-ui/core/styles"; 

import Autocomplete from "@material-ui/lab/Autocomplete";

import styles from "assets/jss/material-dashboard-react/components/autocompleteStyle.js";

const useStyles = makeStyles(styles);

export default function RegularAutocomplete(props) {
  const classes = useStyles();
  const {
    color,
    children,
    size,
    className,
    muiClasses,
    onClick,
    ...rest
  } = props;
  const autocompleteClasses = classNames({
    [classes.autocomplete]: true,
    [classes[size]]: size,
    [classes[color]]: color,
    [className]: className
  });
  return (
    <Autocomplete {...rest} onClick={onClick} classes={muiClasses} className={autocompleteClasses}>
      {children}
    </Autocomplete>
  );
}

RegularAutocomplete.propTypes = {
  color: PropTypes.oneOf([
    "white",
    "transparent"
  ]),
  size: PropTypes.oneOf(["sm", "lg"]),
  className: PropTypes.string,
  muiClasses: PropTypes.object,
  children: PropTypes.node
};
