import React, { useState, useEffect } from 'react'
import SwitchableTab from 'components/SwitchableTab'
import ManagerViewPage from './ManagerViewPage';
import EmployeeViewPage from './EmployeeViewPage';
import CustomerViewPage from './CustomerViewPage';
import auth from 'services/auth';

function UserManagement() {
  const authRole = auth.authRole();
  const [selectedIndex, changeSelectedIndex] = useState(2)
  const [renderSection, setRenderSection] = useState(null)

  const changeSection = (index = 0) => {
    changeSelectedIndex(index);
    setRenderSection(MyTabConfig[index].component)
  }

  const MyTabConfig = [
    {
      title: "Customer", id: 'customer', href: 'customer',
      component: <CustomerViewPage />,
      onClick: () => changeSection(0),
    }
  ]

  if (authRole == 'admin' || authRole == 'manager') {
    MyTabConfig.push({
      title: "Employee", id: 'employee', href: 'employee',
      component: <EmployeeViewPage />,
      onClick: () => changeSection(1),
    })
  }
  if (authRole == 'admin') {
    MyTabConfig.push({
      title: "Manager", id: 'manager', href: 'manager',
      component: <ManagerViewPage />,
      onClick: () => changeSection(2),
    })
  }

  useEffect(() => {
    changeSection()
  }, [])

  return (
    <div>
      <SwitchableTab tabValues={MyTabConfig} selectedIndex={selectedIndex} />
      {renderSection}
    </div>
  )
}

export default UserManagement

export const userManagementConfig = {
  path: '/user-management',
  name: "User Management",
  icon: "assignment_ind",
  component: UserManagement,
  layout: "/home",
}