import React from "react";
import { withStyles } from "@material-ui/core/styles";
import { Icon } from "@material-ui/core";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TablePagination from "@material-ui/core/TablePagination";
import PictureAsPdfIcon from "@material-ui/icons/PictureAsPdf";
import "react-confirm-alert/src/react-confirm-alert.css";
import moment from "moment-timezone";
import Loader from "react-loader-spinner";
import Button from "components/CustomButtons/Button.js";

import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import CardIcon from "components/Card/CardIcon";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import { sampleService } from "services/api.services";
import CustomInput from "components/CustomInput/CustomInput";

const useStyles = () => styles;

class ReportsViewPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // On Load list
      sifList: [],
      isLoading: true,
      sifPendingCount: 0,
      sifCompletedCount: 0,
      // Pagination
      page: 0,
      rowsPerPage: 10,
      // Mailing
      multiLabIDSelected: [],
      mailEmail: "",
    };
  }

  componentDidMount = () => {
    this.getSIFList();
  };

  getSIFList = () => {
    this.setState({ isLoading: true });

    var params = {
      limit: ""
    };
    if (this.state.isPendingSIFView) {
      params.filter = "pending";
    }
    if (this.state.isCompletedSIFView) {
      params.filter = "completed";
    }

    sampleService.getSIFList(params, (result) => {
      if (result) {
        const count = result.count;
        this.setState({
          sifList: result.sif || [],
          sifPendingCount: count.pending || 0,
          sifCompletedCount: count.completed || 0,
        });
      }
      this.setState({ isLoading: false });
    });
  };

  getpdf = (labid) => {
    sampleService.downloadpdf(labid, () => { });
  };

  mailpdf = (labid) => {
    sampleService.sendMail({ labid: labid }, (result) => { });
  };

  /*------------------------- Handlers ------------------------- */
  _handleReset = () => {
    this.setState({
      page: 0,
      rowsPerPage: 10,
      // mailing
      multiLabIDSelected: [],
      mailEmail: "",
    });
  };

  /*------------------------------ Pagination -----------------------------------*/

  _handleChangePage = (event, newPage) => {
    this.setState({ page: newPage });
  };

  _handleChangeRowsPerPage = (event) => {
    this.setState({ rowsPerPage: +event.target.value });
    this.setState({ page: 0 });
  };

  /*-------------------- SIF -----------------------*/

  _handleSearchFilter = (event) => {
    sampleService.searchSIFListSingleParam(event.target.value, (result) => {
      this.setState({
        sifList: result,
      });
    });
  };

  /* -------------------- Multi mail handler ---------------------------- */

  _handleMultiSelect = (event) => {
    var temp = this.state.multiLabIDSelected;
    if (event.target.checked) {
      if (!temp.includes(event.target.id)) {
        temp.push(event.target.id);
      }
    } else {
      if (temp.includes(event.target.id)) {
        temp = temp.filter((e) => e !== event.target.id);
      }
    }
    document.getElementById("select-all").checked = false;
    this.setState({ multiLabIDSelected: temp });
  };

  _handleSelectAll = (event) => {
    if (this.state.sifList && this.state.sifList.length > 0) {
      if (event.target.checked) {
        var temp = [];
        this.state.sifList
          .slice(
            this.state.page * this.state.rowsPerPage,
            this.state.page * this.state.rowsPerPage + this.state.rowsPerPage
          )
          .map((value, key) => {
            document.getElementById(value.labid).checked = true;
            return temp.push(value.labid);
          });
        this.setState({ multiLabIDSelected: temp });
      }
    } else {
      document.getElementById("select-all").checked = false;
    }
  };

  // API
  _handleMultiSIFsend = () => {
    if (
      this.state.multiLabIDSelected &&
      this.state.multiLabIDSelected.length &&
      this.state.mailEmail
    ) {
      sampleService.mailMultiPersonalSIF(
        this.state.multiLabIDSelected,
        [this.state.mailEmail],
        (result) => {
          if (result) {
            var temp = [];
            this.state.sifList
              .slice(
                this.state.page * this.state.rowsPerPage,
                this.state.page * this.state.rowsPerPage +
                this.state.rowsPerPage
              )
              .map((value, key) => {
                document.getElementById(value.labid).checked = false;
                return temp.push(value.labid);
              });
            document.getElementById("select-all").checked = false;
            this.setState({
              multiLabIDSelected: [],
              mailEmail: "",
            });
          }
        }
      );
    }
  };

  /*----------------------------------- Render ---------------------------*/

  _renderMailOption = () => {
    return (
      <div>
        <GridItem xs={12} sm={12} md={4}>
          <CustomInput
            labelText={"Enter Email Address"}
            onChange={(event) => {
              this.setState({ mailEmail: event.target.value });
            }}
            inputProps={{ type: "text" }}
            formControlProps={{
              fullWidth: true,
            }}
          />
        </GridItem>
        <GridItem xs={12} sm={12} md={4}>
          <Button onClick={this._handleMultiSIFsend} color="info">
            {" "}
            Email{" "}
          </Button>
        </GridItem>
      </div>
    );
  };

  _renderSIFTable = () => {
    const { classes } = this.props;
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="info">
              <h4 className={classes.cardTitleWhite}>Reports</h4>
            </CardHeader>
            <CardBody>
              {this.state.multiLabIDSelected.length
                ? this._renderMailOption()
                : null}
              {this.state.isLoading ? (
                <Loader
                  type="ThreeDots"
                  color="#a5d1e6"
                  height={50}
                  width={50}
                />
              ) : (
                <div className={classes.tableResponsive}>
                  <GridItem xs={12} sm={12} md={4}>
                    <CustomInput
                      labelText={"Search"}
                      id={"search"}
                      onChange={this._handleSearchFilter}
                      inputProps={{ type: "text" }}
                      formControlProps={{
                        fullWidth: true,
                      }}
                    />
                  </GridItem>
                  <Table className={classes.table}>
                    <TableHead className={classes.info}>
                      <TableRow className={classes.tableHeadRow}>
                        <TableCell className={classes.tableCell} key={"check"}>
                          <input
                            id="select-all"
                            name="select-all"
                            type="checkbox"
                            onChange={this._handleSelectAll}
                          />
                        </TableCell>
                        <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"pdf"} >PDF</TableCell>
                        <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"labid"}>Lab ID</TableCell>
                        <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"labid"}>Sample ID</TableCell>
                        <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"customer"}>Location</TableCell>
                        <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"status"} >Status</TableCell>
                        <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"company"}>Released</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {this.state.sifList.length ? (
                        this.state.sifList
                          .slice(
                            this.state.page * this.state.rowsPerPage,
                            this.state.page * this.state.rowsPerPage +
                            this.state.rowsPerPage
                          )
                          .map((value, key) => (
                            <TableRow key={key} tabIndex={-1} className={classes.tableBodyRow}>
                              <TableCell className={`${classes.tableCell} ${classes.tableHeadCell}`} key={"check" + key}>
                                <input id={value.labid} type="checkbox" onChange={this._handleMultiSelect} />
                              </TableCell>
                              <TableCell className={classes.tableCell} key={key}>
                                <PictureAsPdfIcon onClick={() => this.getpdf(value.labid)} />
                              </TableCell>
                              <TableCell className={classes.tableCell} key={"labid" + key}>{value.labid}</TableCell>
                              <TableCell className={classes.tableCell} key={"labid" + key}>{value.sampleid} </TableCell>
                              <TableCell className={classes.tableCell} key={"work-order-location" + key} > {value.customer} </TableCell>
                              <TableCell className={classes.tableCell} key={"status" + key} >{value.status} </TableCell>
                              <TableCell className={classes.tableCell} key={"company" + key} >  {value.dateout ? moment(value.dateout).format("ll") : ""} </TableCell>
                            </TableRow>
                          ))
                      ) : (
                        <TableRow>No records available</TableRow>
                      )}
                    </TableBody>
                  </Table>
                  <TablePagination
                    component="div"
                    count={this.state.sifList.length}
                    rowsPerPage={this.state.rowsPerPage}
                    page={this.state.page}
                    onChangePage={this._handleChangePage}
                    onChangeRowsPerPage={this._handleChangeRowsPerPage}
                  />
                </div>
              )}
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    );
  };

  _renderHeader = () => {
    const { classes } = this.props;
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={6}>
          <Card>
            <CardHeader color="info" stats icon>
              <CardIcon color="info">
                <Icon>P</Icon>
              </CardIcon>
              <p className={classes.cardCategory}>Pending test</p>
              <h3 className={classes.cardTitle}>
                {this.state.sifPendingCount}{" "}
              </h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>
                <a
                  href="#filter_pending"
                  onClick={() => {
                    this.setState(
                      {
                        isPendingSIFView: !this.state.isPendingSIFView,
                        isCompletedSIFView: false,
                      },
                      () => {
                        this.getSIFList();
                      }
                    );
                  }}
                >
                  {" "}
                  {this.state.sifPendingCount
                    ? this.state.isPendingSIFView
                      ? "Show full"
                      : "View"
                    : "No results"}{" "}
                </a>
              </div>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={12} md={6}>
          <Card>
            <CardHeader color="success" stats icon>
              <CardIcon color="success">
                <Icon>C</Icon>
              </CardIcon>
              <p className={classes.cardCategory}>Completed test</p>
              <h3 className={classes.cardTitle}>
                {this.state.sifCompletedCount}
              </h3>
            </CardHeader>
            <CardFooter stats>
              <a
                href="#filter_completed"
                onClick={() => {
                  this.setState(
                    {
                      isPendingSIFView: false,
                      isCompletedSIFView: !this.state.isCompletedSIFView,
                    },
                    () => {
                      this.getSIFList();
                    }
                  );
                }}
              >
                {" "}
                {this.state.sifCompletedCount
                  ? this.state.isCompletedSIFView
                    ? "Show full"
                    : "View"
                  : "No results"}{" "}
              </a>
            </CardFooter>
          </Card>
        </GridItem>
      </GridContainer>
    );
  };

  render() {
    return (
      <div>
        {this._renderHeader()}
        {this._renderSIFTable()}
      </div>
    );
  }
}

export default withStyles(useStyles)(ReportsViewPage);
