import { Card } from '@material-ui/core'
import GridContainer from 'components/Grid/GridContainer'
import GridItem from 'components/Grid/GridItem'
import React from 'react'

function SwitchableTab({
  selectedIndex = 0,
  tabValues = [],
}) {
  return (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <ul className="nav nav-tabs" id="myTab" role="tablist">
            {tabValues.map(({ title, id, href, onClick }, index) => {
              if (!id) {
                id = `${title.replace(/\s/g, '-')}-${index}`
              }
              return (
                <li className="nav-item">
                  <a data-toggle="tab" role="tab" aria-controls="home" aria-selected="true"
                    style={{ fontSize: 24 }}
                    id={id}
                    href={`#${href || title}`}
                    className={(selectedIndex === index) ? " nav-link active" : "nav-link"}
                    onClick={onClick}
                  >
                    {title}
                  </a>
                </li>
              )
            })}

          </ul>
        </Card>
      </GridItem>
    </GridContainer >
  )
}

export default SwitchableTab