import React from "react";
import { withStyles } from "@material-ui/core/styles";
import { Icon } from "@material-ui/core";
import Loader from 'react-loader-spinner';
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf';
import 'react-confirm-alert/src/react-confirm-alert.css';
import moment from "moment-timezone";

import auth from 'services/auth'
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardIcon from "components/Card/CardIcon.js";
import CardBody from "components/Card/CardBody.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import { sampleService } from 'services/api.services'

const useStyles = () => (styles);
var _renderDashboard = null;

class DashboardViewPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      sifList: [], sifPendingCount: 0, sifArchivedCount: 0, sifCompletedCount: 0,
      isLoading: true, isPendingSIFView: false, isArchivedSIFView: false, isCompletedSIFView: false,
      role: 'customer',
    }
  }

  componentDidMount = () => {
    this._loadData()
    this.setState({
      role: auth.authRole()
    })
  }

  _loadData = () => {
    this.setState({ isLoading: true })
    this.getSIFList()
  }

  getSIFList = () => {
    var params = {
      limit: 10,
    }
    if (this.state.isPendingSIFView) { params.filter = 'pending' }
    if (this.state.isArchivedSIFView) { params.filter = 'archived' }
    if (this.state.isCompletedSIFView) { params.filter = 'completed' }

    sampleService.getSIFList(params, ({ sif = [], count = {} }) => {
      this.setState({
        sifList: sif || [],
        sifPendingCount: count.pending || 0,
        sifCompletedCount: count.completed || 0,
        sifArchivedCount: count.archived || 0
      })
      this.setState({ isLoading: false })
    })
  }

  getpdf = labid => {
    sampleService.downloadpdf(labid, result => {

    });
  }

  _renderAdminDash = () => {
    const { classes } = this.props;
    return (
      <div>
        {this._renderHeader()}
        <GridContainer>
          <GridItem xs={12} sm={12} md={12}>
            <Card>
              <CardHeader color="info">
                <h4 className={classes.cardTitleWhite}>Recent SIF</h4>
              </CardHeader>
              <CardBody>
                {this.state.isLoading
                  ? <Loader type="ThreeDots" color="#a5d1e6" height={50} width={50} />
                  : <div className={classes.tableResponsive}>
                    <Table className={classes.table}>
                      <TableHead className={classes.info}>
                        <TableRow className={classes.tableHeadRow}>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"pdf"} > PDF </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"customer"} > Customer </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"company"} > Company </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"labid"} > Lab ID </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"email"} > Email </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"status"} > Status </TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>

                        {this.state.sifList.length
                          ? this.state.sifList.map((value, key) =>
                            <TableRow key={key} tabIndex={-1} className={classes.tableBodyRow}>
                              <TableCell className={classes.tableCell} key={key}>
                                <PictureAsPdfIcon onClick={() => this.getpdf(value.labid)} />
                              </TableCell>
                              <TableCell className={classes.tableCell} key={'customername' + key}> {value.customer} </TableCell>
                              <TableCell className={classes.tableCell} key={'company' + key}> {value.company} </TableCell>
                              <TableCell className={classes.tableCell} key={'labid' + key}> {value.labid} </TableCell>
                              <TableCell className={classes.tableCell} key={'additionamemail' + key}> {value.additionalemail.length ? value.additionalemail.length + 1 : 1} </TableCell>
                              <TableCell className={classes.tableCell} key={'status' + key}> {value.status ? (value.status.slice(0, 1).toUpperCase() + value.status.slice(1)) : ""} </TableCell>
                            </TableRow>
                          )
                          : <TableRow>No records available</TableRow>}
                      </TableBody>
                    </Table>
                  </div>
                }
              </CardBody>
            </Card>
          </GridItem>
        </GridContainer>
      </div>
    )
  }

  _renderManagerDashboard = () => {
    return (
      this._renderAdminDash()
    )
  }

  _renderEmployeeDashboard = () => {
    return (
      this._renderAdminDash()
    )
  }

  _renderCustomerDashboard = () => {
    const { classes } = this.props;
    return (
      <div>
        {this._renderHeader()}
        <GridContainer>
          <GridItem xs={12} sm={12} md={12}>
            <Card>
              <CardHeader color="info">
                <h4 className={classes.cardTitleWhite}>Recent Result</h4>
              </CardHeader>
              <CardBody>
                {this.isLoading
                  ? <Loader type="ThreeDots" color="#a5d1e6" height={50} width={50} />
                  : <div className={classes.tableResponsive}>
                    <Table className={classes.table}>
                      <TableHead className={classes.info}>
                        <TableRow className={classes.tableHeadRow}>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"pdf"} > PDF </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"labid"} > Lab ID </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"sample-id"} > Sample ID </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"customer"} > Location </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"status"} > Status </TableCell>
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"company"} > Received </TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>

                        {this.state.sifList.length
                          ? this.state.sifList.map((value, key) =>
                            <TableRow key={key} tabIndex={-1} className={classes.tableBodyRow}>
                              <TableCell className={classes.tableCell} key={key}>
                                <PictureAsPdfIcon onClick={() => this.getpdf(value.labid)} />
                              </TableCell>
                              <TableCell className={classes.tableCell} key={'labid-' + key}> {value.labid} </TableCell>
                              <TableCell className={classes.tableCell} key={'sample-id-' + key}> {value.sampleid} </TableCell>
                              <TableCell className={classes.tableCell} key={'customername' + key}> {value.wolocation} </TableCell>
                              <TableCell className={classes.tableCell} key={'status' + key}> {value.status ? (value.status.slice(0, 1).toUpperCase() + value.status.slice(1)) : ""} </TableCell>
                              <TableCell className={classes.tableCell} key={'company' + key}> {moment(value.datein).format('ll')} </TableCell>
                            </TableRow>
                          )
                          : <TableRow>No records available</TableRow>}
                      </TableBody>
                    </Table>
                  </div>
                }
              </CardBody>
            </Card>
          </GridItem>
        </GridContainer>
      </div>
    )
  }
  _renderHeader = () => {
    const { classes } = this.props
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={this.state.role === 'customer' ? 6 : 4}>
          <Card>
            <CardHeader color="info" stats icon>
              <CardIcon color="info">
                <Icon>P</Icon>
              </CardIcon>
              <p className={classes.cardCategory}>Pending test</p>
              <h3 className={classes.cardTitle}>{this.state.sifPendingCount} </h3>
            </CardHeader>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={12} md={this.state.role === 'customer' ? 6 : 4}>
          <Card>
            <CardHeader color="success" stats icon>
              <CardIcon color="success">
                <Icon>C</Icon>
              </CardIcon>
              <p className={classes.cardCategory}>Completed test</p>
              <h3 className={classes.cardTitle}>{this.state.sifCompletedCount}</h3>
            </CardHeader>
          </Card>
        </GridItem>
        {this.state.role === 'customer'
          ? null
          : <GridItem xs={12} sm={12} md={4}>
            <Card>
              <CardHeader color="warning" stats icon>
                <CardIcon color="warning">
                  <Icon>A</Icon>
                </CardIcon>
                <p className={classes.cardCategory}>Archived test</p>
                <h3 className={classes.cardTitle}>{this.state.sifArchivedCount}</h3>
              </CardHeader>
            </Card>
          </GridItem>
        }
      </GridContainer>
    )
  }

  render() {
    switch (auth.authRole()) {

      case "admin": {
        _renderDashboard = this._renderAdminDash();
        break;
      }
      case "manager": {
        _renderDashboard = this._renderManagerDashboard();
        break;
      }
      case "employee": {
        _renderDashboard = this._renderEmployeeDashboard();
        break;
      }
      case "customer": {
        _renderDashboard = this._renderCustomerDashboard();
        break;
      }
      default: {
        _renderDashboard = null;
        break;
      }
    }
    return (
      _renderDashboard
    );

  }
}


export default withStyles(useStyles)(DashboardViewPage);
