import React from "react";

import { withStyles } from "@material-ui/core/styles";
import 'react-confirm-alert/src/react-confirm-alert.css';
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";

import ReportedCompanies from './ReportedCompaniesView';
import Package from './PackageView';
import FuelKit from './FuelKitView';
import Fuel from './FuelView';
import LaboratoryTest from './LaboratoryTestView/index';
import ShippingMethod from './ShippingMethodView';
const useStyles = () => (styles)

class ControllerViewPage extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            // tab view states
            onShowing: '',
            renderstate: null
        }
    }

    UNSAFE_componentWillMount = () => {
        return this.onChangeTab()
    }

    onChangeTab = event => {
        var index = null

        if (event) {
            index = event.target.id
        }

        switch (index) {
            case 'fuel': {
                this.setState({
                    onShowing: index,
                    renderstate: <Fuel />
                });
                break;
            }
            case 'kit': {
                this.setState({
                    onShowing: index,
                    renderstate: <FuelKit />
                });
                break;
            }
            case 'labtest': {
                this.setState({
                    onShowing: index,
                    renderstate: <LaboratoryTest />
                });
                break;
            }
            case 'package': {
                this.setState({
                    onShowing: index,
                    renderstate: <Package />
                });
                break;
            }
            case 'shippingMethod': {
                this.setState({
                    onShowing: index,
                    renderstate: <ShippingMethod />
                });
                break;
            }
            case 'reportcomp': {
                this.setState({
                    onShowing: index,
                    renderstate: <ReportedCompanies />
                });
                break;
            }
            default: {
                this.setState({
                    onShowing: 'fuel',
                    renderstate: <Fuel />
                });
                break;
            }
        }
    }

    render() {
        return (
            <div>
                <GridContainer>
                    <GridItem xs={12} sm={12} md={12}>
                        <Card>
                            <ul className="nav nav-tabs" id="myTab" role="tablist">
                                <li className="nav-item">
                                    <a className={(this.state.onShowing === 'fuel') ? " nav-link active" : "nav-link"} style={{ fontSize: 24 }} id="fuel" data-toggle="tab" href="#fuel_manuplation" onClick={this.onChangeTab} role="tab" aria-controls="home" aria-selected="true">Fuel</a>
                                </li>
                                <li className="nav-item">
                                    <a className={(this.state.onShowing === 'labtest') ? " nav-link active" : " nav-link"} style={{ fontSize: 24 }} onClick={this.onChangeTab} id="labtest" data-toggle="tab" href="#lab_test_manuplation" role="tab" aria-controls="profile" aria-selected="false">Laboratory test</a>
                                </li>
                                <li className="nav-item">
                                    <a className={(this.state.onShowing === 'kit') ? "nav-link active" : "nav-link"} style={{ fontSize: 24 }} onClick={this.onChangeTab} id="kit" data-toggle="tab" href="#kit_manuplation" role="tab" aria-controls="profile" aria-selected="false">Kit</a>
                                </li>
                                <li className="nav-item">
                                    <a className={(this.state.onShowing === 'shippingMethod') ? "nav-link active" : "nav-link"} style={{ fontSize: 24 }} onClick={this.onChangeTab} id="shippingMethod" data-toggle="tab" href="#shippingMethod_manuplation" role="tab" aria-controls="profile" aria-selected="false">Shipping Method</a>
                                </li>
                                <li className="nav-item">
                                    <a className={(this.state.onShowing === 'package') ? "nav-link active" : "nav-link"} style={{ fontSize: 24 }} onClick={this.onChangeTab} id="package" data-toggle="tab" href="#package_manuplation" role="tab" aria-controls="contact" aria-selected="false">Packages</a>
                                </li>
                                <li className="nav-item">
                                    <a className={(this.state.onShowing === 'reportcomp') ? "nav-link active" : "nav-link"} style={{ fontSize: 24 }} onClick={this.onChangeTab} id="reportcomp" data-toggle="tab" href="#Reporting_companies" role="tab" aria-controls="contact" aria-selected="false">Reporting companies</a>
                                </li>
                            </ul>
                        </Card>
                    </GridItem>
                </GridContainer >
                {this.state.renderstate}
            </div>
        );
    }
}

export default withStyles(useStyles)(ControllerViewPage);