import React from 'react'
import CustomInput from 'components/CustomInput/CustomInput';
import { Autocomplete } from '@material-ui/lab';
import { TextField } from '@material-ui/core';

const InputField = ({
  key, index, labelText, disabled, onChange, refValue, availableInputDataSet
}) => {

  return (
    (availableInputDataSet && availableInputDataSet.length)
      ? <Autocomplete
        id={`test-update-field-${key}-${index}`}
        onChange={(event, newValue) => onChange(newValue)}
        options={availableInputDataSet}
        getOptionLabel={(option) => option}
        style={{ backgroundColor: "white" }}
        autoComplete
        includeInputInList
        renderInput={(params) => (
          <TextField
            {...params}
            label={labelText}
            variant="outlined"
          />
        )}
      />
      : <CustomInput
        id={`test-update-field-${key}-${index}`}
        key={`test-update-field-${key}-${index}`}
        labelText={labelText}
        disabled={disabled}
        inputProps={{ type: "text" }}
        formControlProps={{
          fullWidth: false,
        }}
        onChange={(e) => onChange(e.target.value, refValue)}
      />
  )

}

export default InputField