import React from "react";

import { withStyles } from "@material-ui/core/styles";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import BorderColorIcon from '@material-ui/icons/BorderColor';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TablePagination from '@material-ui/core/TablePagination';
import { red } from "@material-ui/core/colors";
import Loader from 'react-loader-spinner';

import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import {dataControllerService} from "services/api.services";

const useStyles = () => (styles)

class ShippingMethod extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            // Get list on load
            ShippingList: [],
            // Fuel
           name: "",cost:'',duration:'', selectedMethod: {}, isViewMethod: false, nameError: false,costError:false,durationError:false, isCreateMethod: false,
            // Pagination
            page: 0, rowsPerPage: 10, isLoading: true,
        }
    }

    componentDidMount = () => {
        this._onLoadData()
    }

    _onLoadData = () => {
        this.getShippingMethodList();
    }

    _handleReset = () => {
        this.setState({
            // Fuel
            name: "",cost:'',duration:'', selectedMethod: {}, isViewMethod: false, nameError: false,costError:false,durationError:false, isCreateMethod: false,
        })
    }

    /*------------------------ Fuel operations -----------------------------*/

    createShippingMethod = () => {
        if (this.state.name) {
            const data={
                name: this.state.name,
                cost:parseInt(this.state.cost),
                duration:parseInt(this.state.duration),
            }
            confirmAlert({
                title: 'Confirm',
                message: 'Are you sure you want to create ' + this.state.name + " as a new Shipping Method",
                buttons: [
                    {
                        label: 'Create',
                        onClick: () => {
                            dataControllerService.createShippingMethod(data, result => {
                                if (result) {
                                    this._handleReset()
                                    this.getShippingMethodList();
                                }
                            })
                        }
                    },
                    {
                        label: 'Cancel',
                        onClick: () => console.log('cancel delete')
                    }
                ]
            });
        } else {
            this.setState({ nameError: true,costError:true,durationError:true })
        }
    }

    getShippingMethodList = () => {
        this.setState({ isLoading: true })
        dataControllerService.getShippingMethodList(result => {
            if(result){
                this.setState({ ShippingList: result.data })
            }
            this.setState({ isLoading: false })
        })
    }

    getShippingMethodById = id => {
        dataControllerService.getShippingMethodById(id, result => {
            if (result) {
                this.setState({
                    isViewMethod: true,
                    selectedMethod: result,
                    name: result.name,
                    cost:result.cost,
                    duration:result.duration
                })
            }
        })
    }

    onDeleteMethod = id => {
        confirmAlert({
            title: 'Confirm',
            message: 'Are you sure you want to delete',
            buttons: [
                {
                    label: 'Delete',
                    onClick: () => {
                        dataControllerService.deleteShippingMethod(id, result => {
                            if (result) {
                                this._handleReset();
                                this.getShippingMethodList();
                            }
                        })
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => console.log('cancel delete')
                }
            ]
        });
    }

    updateShippingMehtodById = id => {
        if (this.state.name) {
            const data = {
                name: this.state.name,
                cost:parseInt(this.state.cost),
                duration:parseInt(this.state.duration),
                id:id
            }
            confirmAlert({
                title: 'Confirm',
                message: 'Are you sure you want to update',
                buttons: [
                    {
                        label: 'Update',
                        onClick: () => {
                            dataControllerService.updateShippingMehtodById(id,data, result => {
                                if (result) {
                                    this.getShippingMethodList();
                                }
                            })
                        }
                    },
                    {
                        label: 'Cancel',
                        onClick: () => console.log('cancel update')
                    }
                ]
            });
        } else {
            this.setState({ nameError: true })
        }
    }

    /*------------------------------ Pagination -----------------------------------*/

    _handleChangePage = (event, newPage) => {
        this.setState({ page: newPage });
    };

    _handleChangeRowsPerPage = (event) => {
        this.setState({ rowsPerPage: +event.target.value });
        this.setState({ page: 0 });
    };

    /*------------------------------ Render -----------------------------------*/

    _renderShippingMethodTab = () => {
        const { classes } = this.props;
        return (
            <GridContainer >
                <GridItem xs={12} sm={12} md={12}>
                    {<Button onClick={() => { this._handleReset(); this.setState({ isCreateMethod: !this.state.isCreateMethod }) }} color="info">{this.state.isCreateMethod ? "Return" : "Create"}</Button>}
                </GridItem>
                {this.state.isCreateMethod || this.state.isViewMethod
                    ? <GridItem xs={12} sm={12} md={12}>
                        <Card>
                            <CardHeader color="primary">
                                <h4 className={classes.cardTitleWhite}>{this.state.isViewMethod ? "Shippimg Method details" : "Create Shippimg Methods"}</h4>
                                <p className={classes.cardCategoryWhite}>{this.state.isViewMethod ? "" : "Complete new shipping methods"}</p>
                            </CardHeader>
                            <CardBody>
                                <GridContainer>
                                    <GridItem xs={12} sm={12} md={6}>
                                        <CustomInput
                                            onChange={e => {
                                                this.setState({ name: e.target.value })
                                                if (e.target.value) { this.setState({ nameError: false }) }
                                                else { this.setState({ nameError: false }) }
                                            }}
                                            labelText="Name"
                                            id="name"
                                            error={this.state.nameError}
                                            inputProps={{ type: "text", value: this.state.name }}
                                            formControlProps={{
                                                fullWidth: true,
                                            }}
                                        />
                                    </GridItem>
                                    <GridItem xs={12} sm={12} md={6}>
                                        <CustomInput
                                            onChange={e => {
                                                this.setState({ cost: e.target.value })
                                                if (e.target.value) { this.setState({ costError: false }) }
                                                else { this.setState({ costError: false }) }
                                            }}
                                            labelText="Cost"
                                            id="cost"
                                            error={this.state.costError}
                                            inputProps={{ type: "test", value: this.state.cost }}
                                            formControlProps={{
                                                fullWidth: true,
                                            }}
                                        />
                                    </GridItem>
                                    <GridItem xs={12} sm={12} md={6}>
                                        <CustomInput
                                            onChange={e => {
                                                this.setState({ duration: e.target.value })
                                                if (e.target.value) { this.setState({ durationError: false }) }
                                                else { this.setState({ durationError: false }) }
                                            }}
                                            labelText="Duration"
                                            id="duration"
                                            error={this.state.durationError}
                                            inputProps={{ type: "test", value: this.state.duration }}
                                            formControlProps={{
                                                fullWidth: true,
                                            }}
                                        />
                                    </GridItem>
                                </GridContainer>
                            </CardBody>
                            <CardFooter>
                                <h5 style={{ color: 'red' }}>{this.state.nameError ? "Enter a fuel name" : ""}</h5>
                                {!this.state.isViewMethod ? <Button onClick={() => this.createShippingMethod()} color="success">Create</Button> : null}
                                {this.state.isViewMethod ? <Button onClick={() => this.updateShippingMehtodById(this.state.selectedMethod.id)} color="warning">Update</Button> : null}
                                {this.state.isViewMethod ? <Button onClick={() => this.onDeleteMethod(this.state.selectedMethod.id)} color="danger">Delete</Button> : null}
                                {this.state.isViewMethod ? <Button onClick={() => this._handleReset()} color="info">Reset</Button> : null}
                            </CardFooter>
                        </Card>
                    </GridItem> : null}
                <GridItem xs={12} sm={12} md={12}>
                    <Card>
                        <CardHeader color="info">
                            <h4 className={classes.cardTitleWhite}>Shipping Methods</h4>
                        </CardHeader>
                        <CardBody>
                            {!this.state.isLoading
                                ? <div className={classes.tableResponsive}>
                                    <Table className={classes.table}>
                                        <TableHead className={classes.info}>
                                            <TableRow className={classes.tableHeadRow}>
                                                <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'name'}  >
                                                    Name
                                                    </TableCell>
                                                    <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'cost'}  >
                                                    Cost
                                                    </TableCell>
                                                    <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'duration'}  >
                                                   Duration
                                                    </TableCell>
                                                <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'options'}  >
                                                    Options
                                                    </TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                            {this.state.ShippingList
                                            ? this.state.ShippingList.slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage + this.state.rowsPerPage).map((value, key) =>
                                                <TableRow hover key={key} tabIndex={-1} className={classes.tableBodyRow}>
                                                    <TableCell className={classes.tableCell} key={'name' + key}>
                                                        {value.name}
                                                    </TableCell>
                                                    <TableCell className={classes.tableCell} key={'cost' + key}>
                                                        {value.cost}
                                                    </TableCell>
                                                    <TableCell className={classes.tableCell} key={'duration' + key}>
                                                        {value.duration}
                                                    </TableCell>
                                                    <TableCell className={classes.tableCell} key={'options' + key}>
                                                        <DeleteForeverIcon style={{ color: red[500] }} onClick={() => this.onDeleteMethod(value.id)} />
                                                        <BorderColorIcon onClick={() => this.getShippingMethodById(value.id)} />
                                                    </TableCell>
                                                </TableRow>
                                            ): " No fuel records found"}
                                        </TableBody>
                                    </Table>
                                    <TablePagination
                                        component="div"
                                        count={this.state.ShippingList.length}
                                        rowsPerPage={this.state.rowsPerPage}
                                        page={this.state.page}
                                        onChangePage={this._handleChangePage}
                                        onChangeRowsPerPage={this._handleChangeRowsPerPage}
                                    />
                                </div>
                                : <Loader type="ThreeDots" color="#a5d1e6" height={50} width={50} />}
                        </CardBody>
                    </Card>
                </GridItem>
            </GridContainer >
        )
    }

    render() {
        return (
            this._renderShippingMethodTab()
        );
    }
}

export default withStyles(useStyles)(ShippingMethod);