import React, { Component } from 'react'
import { withStyles } from "@material-ui/core/styles";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import { Autocomplete } from '@material-ui/lab';
import moment from "moment-timezone";
import Loader from "react-loader-spinner";
import { TextField } from '@material-ui/core';
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import GridItem from "components/Grid/GridItem";
import GridContainer from "components/Grid/GridContainer";
import Card from "components/Card/Card";
import CardHeader from "components/Card/CardHeader";
import CardBody from "components/Card/CardBody";
import sampleService from 'services/sample.service';
import { CustomDateRangePicker } from 'components/CustomInput/CustomInput';
import sectionConfig from './sectionConfig';
import { dataControllerService } from "services/api.services";
import logger from '_helper/logger';
import InputField from './InputField';
import Button from "components/CustomButtons/Button.js";

const useStyles = () => styles;

const initialState = {
  testRecords: [],
  selectedtestRecord: {},
  dateReceived: moment().tz("America/Los_Angeles").format("LL"),
  dateOutRange: [null, null],
  tableRecords: [],
  isLoading: false,
  availableInputDataSet: [],
}

export class WorkFlowManagement extends Component {

  constructor(props) {
    super(props);
    this.state = initialState;
  }

  _initializeData = () => {
    /*
      All API Calls required for data population on component start defined here... 
    */
  }

  getLabTests = () => {
    const { dateOutRange } = this.state
    const startDate = dateOutRange[0];
    const endDate = dateOutRange[1];
    dataControllerService.getLabTestListByDate(startDate, endDate, ({ data: testRecords = [] }) => {
      console.log(testRecords, 'testRecords');
      this.setState({ testRecords });
    })
  }

  getTableData = async () => {
    const { selectedtestRecord, dateOutRange } = this.state
    if (selectedtestRecord && dateOutRange[0] && dateOutRange[1]) {
      this.setState({ isLoading: true })
      try {
        logger(selectedtestRecord, 'Selected Lab Test')
        const { data = [] } = await sampleService.getPendingLabtestsByLabTestFilter({
          test_id: selectedtestRecord._id || null,
          test_name: selectedtestRecord.name || null,
          start_date: moment(dateOutRange[0]).format('ll') || null,
          end_date: moment(dateOutRange[1]).format('ll') || null,
        })
        this.setState({ tableRecords: data, isLoading: false })
      } catch (error) {
        logger(error)
      }
    }
  }

  updateTestValue = async (eventData, recordRef) => {
    try {
      const testResult = eventData
      const content = {
        lab_ref: recordRef._id,
        test_ref: recordRef.testRefId,
        result: testResult,
      }
      logger(content, 'content')

      const { data = [] } = await sampleService.updatePendingLabtestsByLabTestFilter(content)
      logger(data);

    } catch (e) {
      logger(e, 'Update failed')
    }
  }

  _handletestSelection = test => {
    if (test) {
      this.setState({
        selectedtestRecord: test,
        availableInputDataSet: test.dataAvailable ? test.dataValue : initialState.availableInputDataSet
      }, () => this.getTableData())
    } else {
      this.setState({
        selectedtestRecord: {},
        availableInputDataSet: initialState.availableInputDataSet,
      }, () => this.getTableData())
    }
  }

  _handleChangeDateOutRange = (range) => {
    if (range === null) {
      range = [null, null];
    }
    this.setState({
      dateOutRange: [...range]
    }, () => this.getLabTests());
  }

  componentDidMount = () => {
    this._initializeData();
  }

  RenderRow = (props) => {
    if (props.editable === 'textfield') {
      return (
        <InputField {...props} />
      )
    }
    if (props.editable === 'update-button') {
      return (
        <Button {...props}>{props.label}</Button>
      )
    }
    return (
      <TableCell {...props}>{props.label}</TableCell>
    )
  }

  downloadReport = async () => {
    const { selectedtestRecord, dateOutRange } = this.state
    if (selectedtestRecord && dateOutRange[0] && dateOutRange[1]) {
      try {
        await sampleService.getWorkOrderSectionReportByDate({
          test_id: selectedtestRecord._id || null,
          test_name: selectedtestRecord.name || null,
          start_date: moment(dateOutRange[0]).format('ll') || null,
          end_date: moment(dateOutRange[1]).format('ll') || null,
        })
      } catch (error) {
        logger(error)
      }
    }
  }

  render() {
    const { classes } = this.props;
    const { testRecords, isLoading, tableRecords, selectedtestRecord, availableInputDataSet, dateOutRange } = this.state;
    const selectedTestName = selectedtestRecord.name || "";
    logger(tableRecords.length, 'tableRecords length')

    return (
      <div>
        <GridContainer spacing={2}>

          <GridItem xs={12} sm={12} md={3} lg={3}>
            <CustomDateRangePicker
              key="sifsearchdatepicker"
              id="sifsearchdatepicker"
              // labelText="Date range"
              onChange={this._handleChangeDateOutRange}
              inputProps={{ value: this.state.dateOutRange }}
              style={{ backgroundColor: "white", width: 260 }}
              formControlProps={{
                fullWidth: true,
              }}
            />
          </GridItem>
          {dateOutRange[0] && dateOutRange[1]
            ? <GridItem xs={12} sm={12} md={3} lg={3}>
            <Autocomplete
              key="test-search"
              id="test-search"
              onChange={(evt, newValue) =>
                this._handletestSelection(newValue)
              }
              options={testRecords}
              getOptionLabel={(option) => option.name}
              style={{ backgroundColor: "white", width: 260, marginBottom: 20 }}
              autoComplete
              includeInputInList
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Select a Lab Test"
                  variant="outlined"
                />
              )}
            />
          </GridItem>
            : null}

          <GridItem xs={12} sm={12} md={3} lg={3}>
            <Button onClick={this.downloadReport} color="success">
              Download Report
            </Button>
          </GridItem>
          <GridItem xs={12} sm={12} md={3} lg={3}>
            <Button onClick={this.downloadReport} color="success">
              Download Report
            </Button>
          </GridItem>
        </GridContainer>

        {/* Table Starts here... */}

        <GridContainer>
          <GridItem xs={12} sm={12} md={12}>
            <Card>
              <CardHeader color="info">
                <h4 className={classes.cardTitleWhite}>Work Flow</h4>
              </CardHeader>
              <CardBody>
                {isLoading ? (
                  <GridItem xs={12} sm={12} md={4}>
                    <Loader
                      type="ThreeDots"
                      color="#a5d1e6"
                      height={50}
                      width={50}
                    />
                  </GridItem>
                ) : (
                  <div className={classes.tableResponsive}>
                    <Table className={classes.table}>
                      <TableHead className={classes.info}>
                        <TableRow className={classes.tableHeadRow}>
                            {sectionConfig.tableColumns.map(({ title }, index) => (
                              <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={`table-header-${Math.random(10)}-${index}`}>
                              {title}
                            </TableCell>
                          ))}
                        </TableRow>
                      </TableHead>
                      <TableBody>
                        {tableRecords.length
                          ? (tableRecords
                            // .slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage + this.state.rowsPerPage)
                            .map((value, key) => (
                              <TableRow key={key} tabIndex={-1} className={classes.tableBodyRow} >
                                {sectionConfig.tableColumns.map(({ key, editable, defaultLabel }, index) =>
                                  <TableCell className={classes.tableCell} key={`labid-${key}-${index}`}>
                                    <this.RenderRow
                                      editable={editable}
                                      key={key}
                                      index={index}
                                      labelText={selectedTestName}
                                      onChange={(e) => this.updateTestValue(e, value)}
                                      availableInputDataSet={availableInputDataSet}
                                      label={value[key] || defaultLabel}
                                    />
                                  </TableCell>
                                )}
                              </TableRow>
                            ))
                          ) : (
                            <TableRow>No records available</TableRow>
                          )}
                      </TableBody>
                    </Table>
                    {/* <TablePagination
                      component="div"
                      count={this.state.sifList.length}
                      rowsPerPage={this.state.rowsPerPage}
                      page={this.state.page}
                      onChangePage={this._handleChangePage}
                      onChangeRowsPerPage={this._handleChangeRowsPerPage}
                    /> */}
                  </div>
                )}
              </CardBody>
            </Card>
          </GridItem>
        </GridContainer>
      </div>
    )
  }
}

export default WorkFlowManagement;

export const workFlowManagementConfig = {
  path: "/work-flow-management",
  name: "Work Flow Management",
  icon: "content_paste",
  component: withStyles(useStyles)(WorkFlowManagement),
  layout: "/home"
}
