import React from "react";
import { withStyles } from "@material-ui/core/styles";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import BorderColorIcon from '@material-ui/icons/BorderColor';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TablePagination from '@material-ui/core/TablePagination';
import { red } from "@material-ui/core/colors";
import Loader from 'react-loader-spinner';

import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import {dataControllerService} from "services/api.services";
const useStyles = () => (styles)

class FuelKitView extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            // Get list on load
            kitList: [],
            // Fuel kit
            fuelKitName: "", selectedFuelKit: {}, isViewFuelKit: false, fuelKitNameError: false, isCreateKit: false,
            // Pagination
            page: 0, rowsPerPage: 10, isLoading: true,
        }
    }

    componentDidMount = () => {
        this._onLoadData()
    }

    _onLoadData = () => {
        this.getFuelKitList();
    }

    _handleReset = () => {
        this.setState({
            // Fuel kit
            fuelKitName: "", selectedFuelKit: {}, isViewFuelKit: false, fuelKitNameError: false, isCreateKit: false,
        })
    }

    /*------------------------- Kit operations ----------------------------*/
    addFuelKit = () => {
        if (this.state.fuelKitName) {
            confirmAlert({
                title: 'Confirm',
                message: 'Are you sure you want to create ' + this.state.fuelKitName + " as a new kit",
                buttons: [
                    {
                        label: 'Create',
                        onClick: () => {
                            dataControllerService.createFuelKit(this.state.fuelKitName, result => {
                                if (result) {
                                    this._handleReset()
                                    this.getFuelKitList()
                                }
                            })
                        }
                    },
                    {
                        label: 'Cancel',
                        onClick: () => console.log('cancel create')
                    }
                ]
            });
        } else {
            this.setState({ fuelKitNameError: true })
        }
    }

    getFuelKitList = () => {
        this.setState({ isLoading: true })
        dataControllerService.getKitList(result => {
            if (result) {
                this.setState({ kitList: result })
            }
            this.setState({ isLoading: false })
        })
    }

    getFuelKitById = id => {
        dataControllerService.getKitById(id, result => {
            this.setState({
                selectedFuelKit: result,
                fuelKitName: result.name,
                isViewFuelKit: true,
            })

        })
    }

    onDeleteKit = id => {
        confirmAlert({
            title: 'Confirm',
            message: 'Are you sure you want to delete',
            buttons: [
                {
                    label: 'Delete',
                    onClick: () => {
                        dataControllerService.deletetKit(id, result => {
                            this._handleReset()
                            this.getFuelKitList()
                        })
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => console.log('cancel delete')
                }
            ]
        });
    }

    onUpdateKit = id => {
        if (this.state.fuelKitName) {
            const data = {
                name: this.state.fuelKitName,
                id: id,
            }
            confirmAlert({
                title: 'Confirm',
                message: 'Are you sure you want to Update ',
                buttons: [
                    {
                        label: 'Update',
                        onClick: () => {
                            dataControllerService.updateFuelKit(data, result => {
                                if (result) {
                                    this.getFuelKitList()
                                }
                            })
                        }
                    },
                    {
                        label: 'Cancel',
                        onClick: () => console.log('cancel update')
                    }
                ]
            });
        } else {
            this.setState({ fuelKitNameError: true })
        }
    }

    /*------------------------------ Pagination -----------------------------------*/

    _handleChangePage = (event, newPage) => {
        this.setState({ page: newPage });
    };

    _handleChangeRowsPerPage = (event) => {
        this.setState({ rowsPerPage: +event.target.value });
        this.setState({ page: 0 });
    };

    /*------------------- Render -------------------------*/

    _renderKitTab = () => {
        const { classes } = this.props;
        return (
            <GridContainer >
                <GridItem xs={12} sm={12} md={12}>
                    {<Button onClick={() => { this._handleReset(); this.setState({ isCreateKit: !this.state.isCreateKit }) }} color="info">{this.state.isCreateKit ? "Return" : "Create"}</Button>}
                </GridItem>
                {this.state.isCreateKit || this.state.isViewFuelKit
                    ? <GridItem xs={12} sm={12} md={12}>
                        <Card>
                            <CardHeader color="primary">
                                <h4 className={classes.cardTitleWhite}>{this.state.isViewFuelKit ? "Kit details" : "Create Fuels Kit"}</h4>
                                <p className={classes.cardCategoryWhite}>{this.state.isViewFuelKit ? "" : "Complete new fuels Kit"}</p>
                            </CardHeader>
                            <CardBody>
                                <GridContainer>
                                    <GridItem xs={12} sm={12} md={6}>
                                        <CustomInput
                                            onChange={e => {
                                                this.setState({ fuelKitName: e.target.value })
                                                if (e.target.value) { this.setState({ fuelKitNameError: false }) }
                                                else { this.setState({ fuelKitNameError: false }) }
                                            }}
                                            labelText="Name"
                                            id="fuelKitName"
                                            inputProps={{ type: "text", value: this.state.fuelKitName }}
                                            formControlProps={{
                                                fullWidth: true,
                                            }}
                                        />
                                    </GridItem>
                                </GridContainer>
                            </CardBody>
                            <CardFooter>
                                <h5 style={{ color: 'red' }}>{this.state.fuelKitNameError ? "Enter a kit name" : ""}</h5>
                                {!this.state.isViewFuelKit ? <Button onClick={() => this.addFuelKit()} color="success">Create</Button> : null}
                                {this.state.isViewFuelKit ? <Button onClick={() => this.onUpdateKit(this.state.selectedFuelKit.id)} color="warning">Update</Button> : null}
                                {this.state.isViewFuelKit ? <Button onClick={() => this.onDeleteKit(this.state.selectedFuelKit.id)} color="danger">Delete</Button> : null}
                                {this.state.isViewFuelKit ? <Button onClick={() => this._handleReset()} color="info">Reset</Button> : null}
                            </CardFooter>
                        </Card>
                    </GridItem> : null}

                <GridItem xs={12} sm={12} md={12}>
                    <Card>
                        <CardHeader color="info">
                            <h4 className={classes.cardTitleWhite}>Fuel Kits</h4>
                        </CardHeader>
                        <CardBody>
                            {!this.state.isLoading
                                ? <div className={classes.tableResponsive}>
                                    <Table className={classes.table}>
                                        <TableHead className={classes.info}>
                                            <TableRow className={classes.tableHeadRow}>

                                                <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'name'}  >
                                                    Name
                                                    </TableCell>
                                                <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'options'} >
                                                    Options
                                                    </TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                            {this.state.kitList.length 
                                            ? this.state.kitList.slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage + this.state.rowsPerPage).map((value, key) =>
                                                <TableRow key={key} tabIndex={-1} className={classes.tableBodyRow}>
                                                    <TableCell className={classes.tableCell} key={'name' + key}>
                                                        {value.name}
                                                    </TableCell>
                                                    <TableCell className={classes.tableCell} key={'options' + key}>
                                                        <DeleteForeverIcon style={{ color: red[500] }} onClick={() => this.onDeleteKit(value.id)} />
                                                        <BorderColorIcon onClick={() => this.getFuelKitById(value.id)} />
                                                    </TableCell>
                                                </TableRow>
                                            ): "No fuel kit records found"}
                                        </TableBody>
                                    </Table>
                                    <TablePagination
                                        component="div"
                                        count={this.state.kitList.length}
                                        rowsPerPage={this.state.rowsPerPage}
                                        page={this.state.page}
                                        onChangePage={this._handleChangePage}
                                        onChangeRowsPerPage={this._handleChangeRowsPerPage}
                                    />
                                </div>
                                : <Loader type="ThreeDots" color="#a5d1e6" height={50} width={50} />}
                        </CardBody>
                    </Card>
                </GridItem>
            </GridContainer>
        )
    }

    render() {
        return (
            this._renderKitTab()
        );
    }
}

export default withStyles(useStyles)(FuelKitView);