import React from "react";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TablePagination from '@material-ui/core/TablePagination';
import { FormControl, InputLabel, Select, Input, Chip, MenuItem } from "@material-ui/core";
import { red } from "@material-ui/core/colors";
import { withStyles } from "@material-ui/core/styles";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import BorderColorIcon from '@material-ui/icons/BorderColor';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import { dataControllerService } from "services/api.services";
import CustomMultiEmailInput from "components/CustomInput/CustomInput";
import LaboratoryTestViewConstants from './LaboratoryTestViewConstants';
import initialState from './initialState';
import DisplayTableData from './DisplayTableData';

const useStyles = () => (styles)
const { MenuProps, limitType } = LaboratoryTestViewConstants

class LaboratoryTestView extends React.Component {
    constructor(props) {
        super(props)
        this.state = initialState
    }

    componentDidMount = () => {
        this._onLoadData()
        document.addEventListener("keydown", this._handlePredefinedValues);
    }

    _onLoadData = () => {
        dataControllerService.getFuelList(result => {
            if (result) {
                this.setState({ fuelList: result })
            }
        })
        this.getLabTestList();
        this.getLabTestResultType();
    }

    _handleReset = () => {
        this.setState({
            // Lab test
            labTestName: "", labTestMethod: "", labTestDLT: "", isViewLabTest: false, labTestNameError: false, selectedResultType: [], selectedFuelList: [], selectedLabTest: {}, isCreateLabTest: false,
            // Lab test limit 
            selectedLabTestLimit: [], selectedLabTestLimitFuel: [], labTextLimitText: "", labTestLimitValue: "", labTestLimitAdditionError: "", selectedLabTestResponseLimit: [], selectedLimitIndex: "", isLimitEdit: false,
            labTestLimitMinComment: "", labTestLimitMaxComment: "",
            // Lab test package
            selectedLabTestList: [], packageName: "", packageError: "", isViewPackageDetails: false, selectedPackage: {}, isCreatePackage: false,
            //lab test result
            selectedResultValue: [{ id: "user defined", name: "user defined" }], LabTestResultValue: [{ id: "user defined", name: "user defined" }, { id: "predefined", name: "predefined" }], resultValues: [], showPredefinedValues: false, resultValue: ""
        })
    }
    /*------------------------ Laboratory test operations -------------------------------*/

    getLabTestResultType = () => {
        dataControllerService.getLabTestResultType(result => {
            this.setState({ LabTestResultType: result })
        })
    }

    _handleResultSelection = event => {
        this.setState({
            selectedResultType: [event.target.value]
        })
    }

    _handleResultValueSelection = event => {

        this.setState({
            selectedResultValue: [event.target.value]
        }, () => {
            if (this.state.selectedResultValue[0].name === "predefined") {
                this.setState({ showPredefinedValues: true })
            } else {
                this.setState({ showPredefinedValues: false }, () => {
                    this.setState({ resultValues: [] })
                })
            }

        })
    }

    _handlePredefinedValues = (event) => {
        if (['Enter', 'Tab', ','].includes(event.key)) {
            event.preventDefault();
            const value = this.state.resultValue.trim();
            if (value) {
                this.setState({
                    resultValues: [...this.state.resultValues, value],
                    resultValue: ""
                });
            }
        }
    }

    _handleResultValue = (event) => {
        this.setState({
            resultValue: event.target.value
        });
    }

    _handleDeleteResultValue = (toBeRemoved) => {
        this.setState({
            resultValues: this.state.resultValues.filter(value => value !== toBeRemoved)
        });
    }

    _handleFuelSelection = event => {
        this.setState({
            selectedFuelList: event.target.value
        })
    }

    _handleLimitSelection = event => {
        this.setState({
            selectedLabTestLimit: [event.target.value]
        })
    }

    _handleLimitFuelSelection = event => {
        this.setState({
            selectedLabTestLimitFuel: event.target.value
        })
    }

    onCreateLabTest = () => {
        if (this.state.labTestName) {
            let data = {
                name: this.state.labTestName,
                method: this.state.labTestMethod,
                dlt: this.state.labTestDLT,
                resulttype: this.state.selectedResultType.length ? this.state.selectedResultType[0].id : " ",
                dataAvailable: this.state.selectedResultValue[0].name === "user defined" ? false : true,
                dataValue: this.state.resultValues
            }
            if (this.state.selectedFuelList.length) {
                var temp = [];
                this.state.selectedFuelList.map(value => {
                    return temp.push(value.id);
                })
                data.fuels = temp;
            }
            confirmAlert({
                title: 'Confirm',
                message: 'Are you sure you want to create ' + this.state.labTestName + " as new laboratory test?",
                buttons: [
                    {
                        label: 'Create',
                        onClick: () => {
                            dataControllerService.createLabTest(data, result => {
                                if (result.data) {
                                    const res = result.data;
                                    this._handleReset()
                                    this.getLabTestList()
                                    if (res.id) {
                                        this.getLabTestById(res.id);
                                    }
                                }
                            })
                        }
                    },
                    {
                        label: 'Cancel',
                        onClick: () => console.log('cancel create')
                    }
                ]
            });
        } else {
            this.setState({ labTestNameError: true })
        }

    }

    getLabTestList = () => {
        this.setState({ isLoading: true })
        dataControllerService.getLabTestList(result => {
            if (result) {
                this.setState({ LabTestList: result })
            }
            this.setState({ isLoading: false })
        })
    }

    getLabTestById = id => {
        this._handleReset();
        dataControllerService.getLabTestById(id, result => {
            if (result.resulttype) {
                this.state.LabTestResultType.map(value => {
                    if (value.id === result.resulttype) {
                        return this.setState({ selectedResultType: [value] })
                    }
                    return false;
                })
            }
            let selectedFuelList = [];
            if (result.fuels && result.fuels.length) {
                result.fuels.map(resultfuelid => {
                    this.state.fuelList.map(fuel => {
                        console.log('Fuel validate', fuel._id, 'against', resultfuelid)
                        if (fuel.id === resultfuelid) {
                            console.log('Fuel match')
                            return selectedFuelList.push(fuel)
                        }
                        return false;
                    })
                    return false;
                })
            }
            console.log(selectedFuelList, 'selectedFuelList')
            this.setState({
                selectedLabTest: result,
                isViewLabTest: true,
                labTestName: result.name,
                labTestMethod: result.method,
                labTestDLT: result.dlt,
                selectedLabTestResponseLimit: result.limits,
                selectedFuelList,
                selectedResultValue: result.dataAvailable ? [{ id: "predefined", name: "predefined" }] : [{ id: "user defined", name: "user defined" }],
                resultValues: result.dataValue,
                showPredefinedValues: result.dataAvailable ? true : false
            })


        })
    }

    onUpdateLabTest = () => {
        if (this.state.labTestName) {
            let data = {
                id: this.state.selectedLabTest.id,
                name: this.state.labTestName,
                method: this.state.labTestMethod,
                dlt: this.state.labTestDLT,
                resulttype: this.state.selectedResultType.length ? this.state.selectedResultType[0].id : " ",
                dataAvailable: this.state.selectedResultValue[0].name === "user defined" ? false : true,
                dataValue: this.state.resultValues
            }
            if (this.state.selectedFuelList.length) {
                var temp = [];
                this.state.selectedFuelList.map(value => {
                    return temp.push(value._id);
                })
                data.fuels = temp;
            }
            confirmAlert({
                title: 'Confirm',
                message: 'Are you sure you want to update ',
                buttons: [
                    {
                        label: 'Update',
                        onClick: () => {
                            dataControllerService.updateLabTest(data, result => {
                                this._handleReset()
                                this.getLabTestById(data.id)
                                this.getLabTestList()
                            })
                        }
                    },
                    {
                        label: 'Cancel',
                        onClick: () => console.log('cancel update')
                    }
                ]
            });
        } else {
            this.setState({ labTestNameError: true })
        }
    }

    onCreateLimitLabTest = () => {
        if (this.state.labTestName && this.state.labTextLimitText && this.state.labTestLimitValue && this.state.selectedLabTestLimitFuel.length && this.state.selectedLabTestLimit.length) {
            let data = {
                id: this.state.selectedLabTest.id
            }
            var limit = {};
            var temp = [];
            if (this.state.selectedLabTestLimitFuel.length) {
                this.state.selectedLabTestLimitFuel.map(value => {
                    return temp.push(value.id)
                })
                limit.fuels = temp;
            }
            limit.limittext = this.state.labTextLimitText;
            limit.maxcomment = this.state.labTestLimitMaxComment;
            limit.mincomment = this.state.labTestLimitMinComment;
            if (this.state.selectedLabTestLimit.length) {
                limit.limitype = this.state.selectedLabTestLimit[0].id
            }
            limit.limit = this.state.labTestLimitValue
            temp = []
            const responseLimits = this.state.selectedLabTest.limits
            if (responseLimits.length) {
                responseLimits.map(value => {
                    return temp.push(value);
                })
            }
            temp.push(limit);

            data.limits = temp;

            confirmAlert({
                title: 'Confirm',
                message: 'Are you sure you want to add limit ',
                buttons: [
                    {
                        label: 'Add limit',
                        onClick: () => {
                            dataControllerService.updateLabTest(data, result => {
                                this._handleReset()
                                this.getLabTestById(data.id)
                                this.getLabTestList()
                            })
                        }
                    },
                    {
                        label: 'Cancel',
                        onClick: () => console.log('cancel add limit')
                    }
                ]
            });
        } else {
            this.setState({ labTestLimitAdditionError: "Please fill all fields to create limit" })
        }
    }

    onDeleteLabTestLimit = index => {
        let data = {
            id: this.state.selectedLabTest.id
        }
        let newLimit = [];
        this.state.selectedLabTestResponseLimit.map((value, key) => {
            if (key !== index) {
                return newLimit.push(value)
            }
            return false;
        })
        data.limits = newLimit

        confirmAlert({
            title: 'Delete',
            message: 'Are you sure you want to delete ',
            buttons: [
                {
                    label: 'Delete',
                    onClick: () => {
                        dataControllerService.updateLabTest(data, result => {
                            this._handleReset()
                            this.getLabTestById(data.id)
                            this.getLabTestList()
                        })
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => console.log('cancel delete')
                }
            ]
        });
    }

    onUpdateLabTestLimitSelection = index => {
        if (this.state.selectedLabTestResponseLimit.length) {
            const selectedLimit = this.state.selectedLabTestResponseLimit[index]
            var selectedFuel = [];
            selectedLimit.fuels.map(fuelid => {
                this.state.selectedFuelList.map(fuel => {
                    if (fuel.id === fuelid) {
                        return selectedFuel.push(fuel);
                    }
                    return false;
                })
                return true;
            })
            var limit = [];
            limitType.map(value => {
                if (value.id === selectedLimit.limitype) {
                    return limit.push(value)
                }
                return false;
            })
            this.setState({
                labTestLimitMinComment: selectedLimit.mincomment ? selectedLimit.mincomment : "",
                labTestLimitMaxComment: selectedLimit.maxcomment ? selectedLimit.maxcomment : "",
                labTextLimitText: selectedLimit.limittext ? selectedLimit.limittext : "",
                labTestLimitValue: selectedLimit.limit ? selectedLimit.limit : "",
                selectedLabTestLimitFuel: selectedFuel,
                selectedLabTestLimit: limit,
                selectedLimitIndex: index,
                isLimitEdit: true,
            })
        }
    }

    onUpdateLabTestLimit = () => {
        if (this.state.labTextLimitText && this.state.labTestLimitValue && this.state.selectedLabTestLimitFuel.length && this.state.selectedLabTestLimit.length) {
            let data = {
                id: this.state.selectedLabTest.id
            }
            var limit = {};
            var temp = [];
            if (this.state.selectedLabTestLimitFuel.length) {
                this.state.selectedLabTestLimitFuel.map(value => {
                    return temp.push(value.id)
                })
                limit.fuels = temp;
            }
            limit.limittext = this.state.labTextLimitText;
            limit.maxcomment = this.state.labTestLimitMaxComment;
            limit.mincomment = this.state.labTestLimitMinComment;
            if (this.state.selectedLabTestLimit.length) {
                limit.limitype = this.state.selectedLabTestLimit[0].id
            }
            limit.limit = this.state.labTestLimitValue
            temp = []
            const responseLimits = this.state.selectedLabTest.limits
            if (responseLimits.length) {
                responseLimits.map((value, key) => {
                    if (key !== this.state.selectedLimitIndex) {
                        return temp.push(value);
                    }
                    return false;
                })
            }
            temp.push(limit);

            data.limits = temp;

            let deleteData = {
                id: this.state.selectedLabTest.id
            }
            let newLimit = [];
            this.state.selectedLabTestResponseLimit.map((value, key) => {
                if (key !== this.state.selectedLimitIndex) {
                    return newLimit.push(value)
                }
                return false;
            })
            deleteData.limits = newLimit

            confirmAlert({
                title: 'Update',
                message: 'Are you sure you want to update',
                buttons: [
                    {
                        label: 'Update',
                        onClick: () => {
                            dataControllerService.updateLabTest(deleteData, result => {
                                dataControllerService.updateLabTest(data, result => {
                                    this._handleReset()
                                    this.getLabTestById(data.id)
                                    this.getLabTestList()
                                })
                            })

                        }
                    },
                    {
                        label: 'Cancel',
                        onClick: () => console.log('cancel update')
                    }
                ]
            });
        } else {
            this.setState({ labTestLimitAdditionError: "Please fill all fields to update limit" })
        }
    }

    /*------------------------------ Pagination -----------------------------------*/

    _handleChangePage = (event, newPage) => {
        this.setState({ page: newPage });
    };

    _handleChangeRowsPerPage = (event) => {
        this.setState({ rowsPerPage: +event.target.value });
        this.setState({ page: 0 });
    };

    _renderLabTestLimit = () => {
        const { classes } = this.props;
        return (
            <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                    <FormControl className={classes.formControl}>
                        <InputLabel className={classes.InputLabelGrey} id="result-type-selection">Fuels for limit</InputLabel>
                        <Select
                            labelId="result-type-selection"
                            id="result-type-selection"
                            multiple
                            value={this.state.selectedLabTestLimitFuel ? this.state.selectedLabTestLimitFuel : []}
                            onChange={e => {
                                this._handleLimitFuelSelection(e)
                                this.setState({ labTestLimitAdditionError: "" })
                            }}
                            input={<Input id="select-chip" />}
                            renderValue={(selected) => (
                                <div className={classes.chips}>
                                    {selected.map(value => (
                                        <Chip key={value.id} label={value.name} className={classes.chip} />
                                    ))}
                                </div>
                            )}
                            MenuProps={MenuProps}
                        >
                            {this.state.selectedFuelList.map(value => (
                                <MenuItem key={value.id} value={value} >
                                    {value.name}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                </GridItem >
                <GridItem xs={12} sm={12} md={6}>
                    <FormControl className={classes.formControl}>
                        <InputLabel className={classes.InputLabelGrey} id="result-type-selection">Limit type</InputLabel>
                        <Select
                            labelId="result-type-selection"
                            id="result-type-selection"
                            single
                            value={this.state.selectedLabTestLimit ? this.state.selectedLabTestLimit : []}
                            onChange={e => {
                                this._handleLimitSelection(e)
                                this.setState({ labTestLimitAdditionError: "" })
                            }}
                            input={<Input id="select-chip" />}
                            renderValue={(selected) => (
                                <div className={classes.chips}>
                                    {selected.map(value => (
                                        <Chip key={value.id} label={value.name} className={classes.chip} />
                                    ))}
                                </div>
                            )}
                            MenuProps={MenuProps}
                        >
                            {limitType.map(value => (
                                <MenuItem key={value.id} value={value} >
                                    {value.name}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                </GridItem >
                <GridItem xs={12} sm={12} md={6}>
                    <CustomInput
                        onChange={e => this.setState({ labTextLimitText: e.target.value, labTestLimitAdditionError: "" })}
                        labelText="Limit text"
                        id="labTextLimitText"
                        inputProps={{ type: "text", value: this.state.labTextLimitText }}
                        formControlProps={{
                            fullWidth: true,
                        }}
                    />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                    <CustomInput
                        onChange={e => { this.setState({ labTestLimitValue: e.target.value, labTestLimitAdditionError: "" }) }}
                        labelText="Limit"
                        id="labTestLimitValue"
                        inputProps={{ type: "text", value: this.state.labTestLimitValue }}
                        formControlProps={{
                            fullWidth: true,
                        }}
                    />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                    <CustomInput
                        onChange={e => this.setState({ labTestLimitMinComment: e.target.value })}
                        labelText="Min Recommendation"
                        id="labTestLimitMinComment"
                        inputProps={{ type: "text", value: this.state.labTestLimitMinComment }}
                        formControlProps={{
                            fullWidth: true,
                        }}
                    />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                    <CustomInput
                        onChange={e => this.setState({ labTestLimitMaxComment: e.target.value })}
                        labelText="Max Recommendation"
                        id="labTestLimitMaxComment"
                        inputProps={{ type: "text", value: this.state.labTestLimitMaxComment }}
                        formControlProps={{
                            fullWidth: true,
                        }}
                    />
                </GridItem>
                <GridItem xs={12} sm={12} md={12}>
                    <h5 style={{ color: 'red' }}>{this.state.labTestLimitAdditionError}</h5>
                    {this.state.isViewLabTest && !this.state.isLimitEdit ? <Button onClick={() => this.onCreateLimitLabTest()} color="success">Add Limit</Button> : null}
                    {this.state.isViewLabTest && this.state.isLimitEdit ? <Button onClick={() => this.onUpdateLabTestLimit()} color="warning">Update</Button> : null}
                    {this.state.isViewLabTest && this.state.isLimitEdit ? <Button onClick={() => this.setState({
                        labTextLimitText: "",
                        labTestLimitValue: "",
                        selectedLabTestLimitFuel: [],
                        selectedLabTestLimit: [],
                        selectedLimitIndex: "",
                        labTestLimitMinComment: "",
                        labTestLimitMaxComment: "",
                        isLimitEdit: false,
                    })} color="success">Reset</Button> : null}
                </GridItem>
                <GridItem xs={12} sm={12} md={12}>
                    <div className={classes.tableResponsive}>
                        <Table className={classes.table}>
                            <TableHead className={classes.info}>
                                <TableRow className={classes.tableHeadRow}>
                                    <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'limittext'} > Limit text </TableCell>
                                    <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'limittype'} > Limit type </TableCell>
                                    <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'limit'} > Limit </TableCell>
                                    <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'fueladded'} > Fuels added </TableCell>
                                    <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'options'} > Options </TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {this.state.selectedLabTestResponseLimit.length
                                    ? this.state.selectedLabTestResponseLimit.slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage + this.state.rowsPerPage).map((value, key) =>
                                        <TableRow key={key} tabIndex={-1} className={classes.tableBodyRow}>
                                            <TableCell className={classes.tableCell} key={'limittext' + key}> {value.limittext} </TableCell>
                                            <TableCell className={classes.tableCell} key={'limittype' + key}> {value.limitype} </TableCell>
                                            <TableCell className={classes.tableCell} key={'limit' + key}> {value.limit} </TableCell>
                                            <TableCell className={classes.tableCell} key={'fuels' + key}> {value.fuels.length ? value.fuels.length : 0} </TableCell>
                                            <TableCell className={classes.tableCell} key={'options' + key}>
                                                <DeleteForeverIcon style={{ color: red[500] }} onClick={() => this.onDeleteLabTestLimit(key)} />
                                                <BorderColorIcon onClick={() => this.onUpdateLabTestLimitSelection(key)} />
                                            </TableCell>
                                        </TableRow>
                                    ) : "No laboratory test record found"}
                            </TableBody>
                        </Table>
                        <TablePagination
                            component="div"
                            count={this.state.selectedLabTestResponseLimit.length}
                            rowsPerPage={this.state.rowsPerPage}
                            page={this.state.page}
                            onChangePage={this._handleChangePage}
                            onChangeRowsPerPage={this._handleChangeRowsPerPage}
                        />
                    </div>
                </GridItem>
            </GridContainer>
        )
    }

    handleTableResetData = (type) => {
        if (type === 'delete') {
            this.getLabTestList();
        }
    }

    _renderLabTestTab = () => {
        const { classes } = this.props;
        return (
            <>
                <GridContainer >
                    <GridItem xs={12} sm={12} md={12}>
                        {<Button onClick={() => { this._handleReset(); this.setState({ isCreateLabTest: !this.state.isCreateLabTest }) }} color="info">{this.state.isCreateLabTest ? "Return" : "Create"}</Button>}
                    </GridItem>
                    {this.state.isCreateLabTest || this.state.isViewLabTest
                        ? <GridItem xs={12} sm={12} md={12}>
                            <Card>
                                <CardHeader color="primary">
                                    <h4 className={classes.cardTitleWhite}>{this.state.isViewLabTest ? "Laboratory test details" : "Create Laboratory Test"}</h4>
                                    <p className={classes.cardCategoryWhite}>{this.state.isViewLabTest ? "" : "Complete new laboratory test form"}</p>
                                </CardHeader>
                                <CardBody>
                                    <GridContainer>
                                        <GridItem xs={12} sm={12} md={6}>
                                            <CustomInput
                                                onChange={e => {
                                                    this.setState({ labTestName: e.target.value })
                                                    if (e.target.value) { this.setState({ labTestNameError: false }) }
                                                    else { this.setState({ labTestNameError: false }) }
                                                }}
                                                labelText="Name"
                                                id="labTestName"
                                                error={this.state.labTestNameError}
                                                inputProps={{ type: "text", value: this.state.labTestName }}
                                                formControlProps={{
                                                    fullWidth: true,
                                                }}
                                            />
                                        </GridItem>
                                        <GridItem xs={12} sm={12} md={6}>
                                            <CustomInput
                                                onChange={e => this.setState({ labTestMethod: e.target.value })}
                                                labelText="Method"
                                                id="labTestMethod"
                                                inputProps={{ type: "text", value: this.state.labTestMethod }}
                                                formControlProps={{
                                                    fullWidth: true,
                                                }}
                                            />
                                        </GridItem>
                                        <GridItem xs={12} sm={12} md={6}>
                                            <CustomInput
                                                onChange={e => this.setState({ labTestDLT: e.target.value })}
                                                labelText="Default Limit Test ( Set default if test will have no limits )"
                                                id="labTestDLT"
                                                inputProps={{ type: "text", value: this.state.labTestDLT }}
                                                formControlProps={{
                                                    fullWidth: true,
                                                }}
                                            />
                                        </GridItem>
                                        <GridItem xs={12} sm={12} md={6}>
                                            <FormControl className={classes.formControl}>
                                                <InputLabel className={classes.InputLabelGrey} id="result-type-selection">Result type</InputLabel>
                                                <Select
                                                    labelId="result-type-selection"
                                                    id="result-type-selection"
                                                    single
                                                    value={this.state.selectedResultType ? this.state.selectedResultType : []}
                                                    onChange={this._handleResultSelection}
                                                    input={<Input id="select-chip" />}
                                                    renderValue={(selected) => (
                                                        <div className={classes.chips}>
                                                            {selected.map(value => (
                                                                <Chip key={value.id} label={value.name} className={classes.chip} />
                                                            ))}
                                                        </div>
                                                    )}
                                                    MenuProps={MenuProps}
                                                >
                                                    {this.state.LabTestResultType.map(value => (
                                                        <MenuItem key={value.id} value={value} >
                                                            {value.name}
                                                        </MenuItem>
                                                    ))}
                                                </Select>
                                            </FormControl>
                                        </GridItem >
                                        <GridItem xs={12} sm={12} md={6}>
                                            <FormControl className={classes.formControl}>
                                                <InputLabel className={classes.InputLabelGrey} id="fuel-selection">Fuel selection</InputLabel>
                                                <Select
                                                    labelId="fuel-selection"
                                                    id="fuel-selection"
                                                    multiple
                                                    value={this.state.selectedFuelList ? this.state.selectedFuelList : []}
                                                    onChange={this._handleFuelSelection}
                                                    input={<Input id="select-fuel" />}
                                                    renderValue={(selected) => (
                                                        <div className={classes.chips}>
                                                            {selected.map(value => (
                                                                <Chip key={value._id} label={value.name} className={classes.chip} />
                                                            ))}
                                                        </div>
                                                    )}
                                                    MenuProps={MenuProps}
                                                >
                                                    {this.state.fuelList.map(value => (
                                                        <MenuItem key={value.id} value={value} >
                                                            {value.name}
                                                        </MenuItem>
                                                    ))}
                                                </Select>
                                            </FormControl>
                                        </GridItem >
                                        <GridItem xs={12} sm={12} md={6}>
                                            <FormControl className={classes.formControl}>
                                                <InputLabel className={classes.InputLabelGrey} id="result-value-selection">Result value</InputLabel>
                                                <Select
                                                    labelId="result-value-selection"
                                                    id="result-value-selection"
                                                    single
                                                    value={this.state.selectedResultValue ? this.state.selectedResultValue : []}
                                                    onChange={this._handleResultValueSelection}
                                                    input={<Input id="select-chip" />}
                                                    renderValue={(selected) => (
                                                        <div className={classes.chips}>
                                                            {selected.map(value => (
                                                                <Chip key={value.id} label={value.name} className={classes.chip} />
                                                            ))}
                                                        </div>
                                                    )}
                                                    MenuProps={MenuProps}
                                                >
                                                    {this.state.LabTestResultValue.map(value => (
                                                        <MenuItem key={value.id} value={value} >
                                                            {value.name}
                                                        </MenuItem>
                                                    ))}
                                                </Select>
                                            </FormControl>
                                        </GridItem >
                                        {this.state.showPredefinedValues ?
                                            <GridItem xs={12} sm={12} md={6}>
                                                <CustomMultiEmailInput
                                                    labelText="Predefined values"
                                                    id="resultValuesChip"
                                                    inputProps={{ type: "text", value: this.state.resultValue }}
                                                    onChange={this._handleResultValue}
                                                    tabIndex="0"
                                                    onKeyDown={this._handlePredefinedValues}
                                                    formControlProps={{
                                                        fullWidth: true,
                                                    }}
                                                />
                                                {/* {this.state.resultValues.map((item, key) => ( <div className={classes.chips} key={item}>   <Chip onDelete={() => this._handleDeleteResultValue(item)} key={key}  label={item} className={classes.chip}  /></div> ))} */}
                                                <div className={classes.chips}>
                                                    {this.state.resultValues.map((email, index) => (
                                                        <Chip label={email} className={classes.chip} onDelete={() => this._handleDeleteResultValue(email)} />
                                                    ))}
                                                </div>
                                            </GridItem>
                                            :
                                            <GridItem style={{ display: "none" }}></GridItem>
                                        }
                                    </GridContainer>
                                </CardBody>
                                <CardFooter>
                                    <GridContainer>
                                        <GridItem xs={12} sm={12} md={12}>
                                            <h5 style={{ color: 'red' }}>{this.state.labTestNameError ? "Enter a Laboratory test name" : ""}</h5>
                                            {!this.state.isViewLabTest ? <Button onClick={() => this.onCreateLabTest()} color="success">Create</Button> : null}
                                            {this.state.isViewLabTest ? <Button onClick={() => this.onUpdateLabTest()} color="warning">Update</Button> : null}
                                        </GridItem>
                                    </GridContainer>
                                </CardFooter>
                            </Card>
                            <Card>
                                <CardBody>
                                    {this.state.isViewLabTest ? this._renderLabTestLimit() : null}
                                </CardBody>
                                <CardFooter>
                                    <GridContainer>
                                        <GridItem xs={12} sm={12} md={12}>
                                            {this.state.isViewLabTest ? <Button onClick={() => this._handleReset()} color="info">Reset</Button> : null}
                                        </GridItem>
                                    </GridContainer>
                                </CardFooter>
                            </Card>
                        </GridItem> : null}
                </GridContainer >
                {!this.state.isCreateLabTest || !this.state.isViewLabTest
                    ? <DisplayTableData
                        isLoading={this.state.isLoading}
                        LabTestList={this.state.LabTestList}
                        getLabTestById={(value) => this.getLabTestById(value.id)}
                        handleReset={type => this.handleTableResetData(type)}
                    />
                    : null}
            </>
        )
    }

    render() {
        return (
            this._renderLabTestTab()
        );
    }
}

export default withStyles(useStyles)(LaboratoryTestView);