import React, { useState } from 'react'
import Box from '@material-ui/core/Box';
import './style.css';
import auth from 'services/auth'
import { Copyright } from 'config/copyright.config';

const LoginV3 = (props) => {

  auth._onLoad(() => {
    props.history.push('/home')
    return null
  });

  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")

  const onSubmit = async () => {
    const loginData = {
      email: email.trim(),
      password: password.trim(),
    }

    auth.login(loginData, () => {
    })
  }

  return (
    <div class='body'>
      <section class='login' id='login'>
        <div class='head'>
          <h1 class='company'>Sign In </h1>
        </div>
        <div class='form' onSubmit={onSubmit}>
          <form>
            <input type="text" placeholder='Email' class='text' id='username' required onChange={event => setEmail(event.target.value)} ></input>
            <input type="password" placeholder='' class='password' id='password' required onChange={event => setPassword(event.target.value)} ></input>
            <a onClick={onSubmit} class='btn-login' id='do-login' disabled={!email || !password}>Login</a>
            {/* <a href="#" class='forgot'>Forgot?</a> */}
          </form>
        </div>
        <Box mt={8}>
          <Copyright />
        </Box>
      </section>
    </div>
  )
}

export default LoginV3;