import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { Router, Route, Switch, Redirect } from "react-router-dom";
import ReactNotification from 'react-notifications-component'
import 'react-notifications-component/dist/theme.css'
import ReactGA from 'react-ga';

import DashboardLayout from "layouts/DashboardLayout.jsx";
import "assets/css/material-dashboard-react.css?v=1.8.0";
import ProtectedRoute from 'config/protected.route';
import LoginV3 from "layouts/LoginV3";

const hist = createBrowserHistory();
ReactGA.initialize('UA-153857702-1');

ReactDOM.render(
  <div>
    <ReactNotification />
    <Router history={hist}>
      <Switch>
        {/* <Route path="/login" component={Login} /> */}
        {/* <Route path="/loginv2" component={LoginV2} /> */}
        <Route path="/login" component={LoginV3} />
        <ProtectedRoute path={"/home"} component={DashboardLayout} />
        {localStorage.getItem('foiLabUserToken') ? <Redirect from='/' to='/home/dashboard' /> : <Redirect from='/' to='/login' />}
        <Route path="*" component={() => "ERROR 404 Page not found. Please reload"} />
      </Switch>
    </Router>
  </div>,
  document.getElementById("root")
);
