import React from "react";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TablePagination from '@material-ui/core/TablePagination';
import { red } from "@material-ui/core/colors";
import { withStyles } from "@material-ui/core/styles";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import BorderColorIcon from '@material-ui/icons/BorderColor';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import Loader from 'react-loader-spinner';

import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import { dataControllerService } from "services/api.services";
import initialState from './initialState';

const useStyles = () => (styles)

const tableConfig = {
  tablePopulationControlValue: [
    { title: "Name", keyPair: "name", },
    { title: "Method", keyPair: "method", },
    { title: "Result type", keyPair: "resulttype" },
    { title: "Limits", keyPair: "limits", type: 'get-length' },
    { title: "Fuels", keyPair: "fuels", type: 'get-length' },
    { title: "Options", keyPair: "action" },
  ]
}

class LaboratoryTestView extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      page: initialState.page,
      rowsPerPage: initialState.rowsPerPage,
    }
  }

  onDeleteLabTest = id => {
    confirmAlert({
      title: 'Confirm',
      message: 'Are you sure you want to delete ',
      buttons: [
        {
          label: 'Delete',
          onClick: () => dataControllerService.deleteLabTest(id, result => this.props.handleReset('delete'))
        },
        {
          label: 'Cancel',
          onClick: () => console.log('cancel delete')
        }
      ]
    });
  }

  /*------------------------------ Pagination -----------------------------------*/

  _handleChangePage = (event, newPage) => {
    this.setState({ page: newPage });
  };

  _handleChangeRowsPerPage = (event) => {
    this.setState({ rowsPerPage: +event.target.value });
    this.setState({ page: 0 });
  };


  render() {
    const { classes, isLoading, LabTestList = [], getLabTestById } = this.props;
    return (
      <GridContainer >
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="info">
              <h4 className={classes.cardTitleWhite}>Laboratory tests</h4>
            </CardHeader>
            <CardBody>
              {isLoading
                ? <Loader type="ThreeDots" color="#a5d1e6" height={50} width={50} />
                : <div className={classes.tableResponsive}>
                  <Table className={classes.table}>
                    <TableHead className={classes.info}>
                      <TableRow className={classes.tableHeadRow}>
                        {tableConfig.tablePopulationControlValue.map(({ title }, tablePopulationControlValueIndex) => (
                          <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={`${title.toLowerCase()}-${tablePopulationControlValueIndex}`}>{title}</TableCell>
                        ))}
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {LabTestList.length
                        ? LabTestList.slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage + this.state.rowsPerPage).map((value, key) =>
                          <TableRow key={key} tabIndex={-1} className={classes.tableBodyRow}>
                            {tableConfig.tablePopulationControlValue.map(({ title, keyPair, type }, tablePopulationControlValueIndex) => (
                              <TableCell className={classes.tableCell} key={`${keyPair}-${title.toLowerCase()}-${tablePopulationControlValueIndex}`}>
                                {(keyPair === 'action')
                                  ? <>
                                    <DeleteForeverIcon style={{ color: red[500] }} onClick={() => this.onDeleteLabTest(value.id)} />
                                    <BorderColorIcon onClick={() => getLabTestById(value)} />
                                  </>
                                  : (type === 'get-length')
                                    ? (value[keyPair] && value[keyPair].length) ? value[keyPair].length : 0
                                    : value[keyPair] || ""
                                }
                              </TableCell>
                            )
                            )}
                          </TableRow>
                        ) : "No Laboraroty test record found"}
                    </TableBody>
                  </Table>
                  <TablePagination
                    component="div"
                    count={LabTestList.length}
                    rowsPerPage={this.state.rowsPerPage}
                    page={this.state.page}
                    onChangePage={this._handleChangePage}
                    onChangeRowsPerPage={this._handleChangeRowsPerPage}
                  />
                </div>}
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer >
    );
  }
}

export default withStyles(useStyles)(LaboratoryTestView);