import React from 'react';
import { Redirect } from 'react-router-dom'
import { store } from 'react-notifications-component';
import ReactGA from 'react-ga';

import constants from 'constants/index'
import axios, { header } from '_helper/axios'
import config from "config";
import AUTH_ROLE from './authRoles'
import logger from '_helper/logger';

const todaysDate = constants.todaysDate;
class Auth {
    constructor() {
        this.authenticated = true;
        this._onLoad(() => console.log(`Loading...`))
    }

    showNotification = (type, message) => {
        store.addNotification({
            message: message,
            type: type,
            insert: "top",
            container: "top-right",
            animationIn: ["animated", "fadeIn"],
            animationOut: ["animated", "fadeOut"],
            dismiss: {
                duration: 5000,
                onScreen: true
            }
        });
    }

    _onLoad = callback => {
        let storedTodaysDate = localStorage.getItem(constants.TODAYS_DATE);
        if (!storedTodaysDate)
            localStorage.setItem(constants.TODAYS_DATE, todaysDate)

        if (storedTodaysDate != todaysDate) {
            localStorage.removeItem(constants.TODAYS_DATE);
            localStorage.removeItem(constants.TOKEN_ID);
            localStorage.removeItem(constants.PRIVILAGE_ID);
        }

        if (!localStorage.getItem(constants.TOKEN_ID)) {
            this.authenticated = false;
            window.history.replaceState(null, null, "/login");
        }
        else if (localStorage.getItem(constants.TOKEN_ID)) {
            // console.log(localStorage.getItem(constants.TOKEN_ID))
            axios({
                method: 'get',
                url: config.SERVER + 'users/',
                headers: header
            }).then(response => {
                localStorage.setItem(constants.PRIVILAGE_ID, response.data.role)
                this.authenticated = true;
                callback(true)
            }).catch((error) => {
                this.authenticated = false;
                window.history.replaceState(null, null, "/");
            })
        }

    }

    login = ({ email, password }, callback) => {
        logger(`Login data: Email => ${email} & Password ${password}`)
        if (!email) {
            this.showNotification('danger', 'Please provide Email')
            return callback(false)
        }
        if (!constants.emailRegex.test(email)) {
            this.showNotification('danger', 'Invalid Email')
            return callback(false)
        }
        if (!password) {
            this.showNotification('danger', 'Please provide Password')
            return callback(false)
        }

        const loginData = {
            email: email.toLowerCase().trim(),
            password: password.trim(),
        }


        axios({
            method: 'post',
            url: config.SERVER + 'users/authenticate',
            data: loginData
        }).then(response => {
            ReactGA.event({
                category: 'USER',
                action: `USER LOGIN `
            });
            const res = response.data;
            if (response) {
                localStorage.setItem(constants.TOKEN_ID, res.token);
                axios({
                    method: 'get',
                    url: config.SERVER + 'users/',
                    headers: {
                        'authorization': `Bearer ${res.token}`,
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    }
                }).then(response => {
                    localStorage.setItem(constants.TODAYS_DATE, todaysDate)
                    localStorage.setItem(constants.PRIVILAGE_ID, response.data.role)
                    this.authenticated = true;
                    this.showNotification('success', 'Hi, ' + response.data.fname + " " + response.data.surname)
                    window.location.reload();
                    callback(res)
                }).catch((error) => {
                    this.showNotification('danger', 'Login failed')
                });
            }
        }).catch(error => {
            console.log(error)
            if (error) {
                this.showNotification('danger', 'Login failed')
            }
        })

    }

    logout = () => {
        localStorage.removeItem(constants.TOKEN_ID);
        localStorage.removeItem(constants.PRIVILAGE_ID);
        this.authenticated = false;
        window.history.replaceState(null, null, "/");
        window.location.reload(false);
    }

    isAuthenticated = () => {
        return this.authenticated
    }

    changeUser = userType => {
        this.authenticated = false;
        localStorage.setItem(constants.PRIVILAGE_ID, userType);
        window.location.reload(false);
        return <Redirect to='/' />
    }

    authRole = () => {
        if (localStorage.getItem(constants.PRIVILAGE_ID))
            return AUTH_ROLE[localStorage.getItem(constants.PRIVILAGE_ID)]
        axios({
            method: 'get',
            url: config.SERVER + 'users/',
            headers: header
        }).then(response => {
            localStorage.setItem(constants.PRIVILAGE_ID, response.data.role)
            return AUTH_ROLE[response.data.role]
        }).catch((error) => {
            return null;
        });
    }
}
export default new Auth()