import React from "react";

import { withStyles } from "@material-ui/core/styles";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import BorderColorIcon from '@material-ui/icons/BorderColor';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TablePagination from '@material-ui/core/TablePagination';
import { red } from "@material-ui/core/colors";
import Loader from 'react-loader-spinner';

import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import { dataControllerService } from "services/api.services";

const useStyles = () => (styles)

class FuelView extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            // Get list on load
            fuelList: [],
            // Fuel
            fuelName: "", selectedFuel: {}, isViewFuel: false, fuelNameError: false, isCreateFuel: false,
            // Pagination
            page: 0, rowsPerPage: 10, isLoading: true,
        }
    }

    componentDidMount = () => {
        this.getFuelList();
    }

    _handleReset = () => {
        this.setState({
            // Fuel
            fuelName: "", selectedFuel: {}, isViewFuel: false, fuelNameError: false, isCreateFuel: false,
        })
    }

    /*------------------------ Fuel operations -----------------------------*/

    addFuel = () => {
        if (this.state.fuelName) {
            confirmAlert({
                title: 'Confirm',
                message: 'Are you sure you want to create ' + this.state.fuelName + " as a new fuel",
                buttons: [
                    {
                        label: 'Create',
                        onClick: () => {
                            dataControllerService.createFuel(({ fuelName: this.state.fuelName }), result => {
                                if (result) {
                                    this._handleReset()
                                    this.getFuelList();
                                }
                            })
                        }
                    },
                    {
                        label: 'Cancel',
                        onClick: () => console.log('cancel delete')
                    }
                ]
            });
        } else {
            this.setState({ fuelNameError: true })
        }
    }

    getFuelList = () => {
        this.setState({ isLoading: true })
        dataControllerService.getFuelList(fuelList => {
            if (fuelList)
                this.setState({ fuelList })
            this.setState({ isLoading: false })
        })
    }

    getFuelById = id => {
        dataControllerService.fuelById(id, result => {
            if (result) {
                this.setState({
                    isViewFuel: true,
                    selectedFuel: result,
                    fuelName: result.name,
                })
            }
        })
    }

    onDeleteFuel = id => {
        confirmAlert({
            title: 'Confirm',
            message: 'Are you sure you want to delete',
            buttons: [
                {
                    label: 'Delete',
                    onClick: () => {
                        dataControllerService.deleteFuel(id, result => {
                            if (result) {
                                this._handleReset();
                                this.getFuelList();
                            }
                        })
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => console.log('cancel delete')
                }
            ]
        });
    }

    onUpdateFuel = id => {
        if (this.state.fuelName) {
            const data = {
                name: this.state.fuelName,
                id: id,
            }
            confirmAlert({
                title: 'Confirm',
                message: 'Are you sure you want to update',
                buttons: [
                    {
                        label: 'Update',
                        onClick: () => {
                            dataControllerService.updateFuel(data, result => {
                                if (result) {
                                    this.getFuelList();
                                }
                            })
                        }
                    },
                    {
                        label: 'Cancel',
                        onClick: () => console.log('cancel update')
                    }
                ]
            });
        } else {
            this.setState({ fuelNameError: true })
        }
    }

    /*------------------------------ Pagination -----------------------------------*/

    _handleChangePage = (event, newPage) => {
        this.setState({ page: newPage });
    };

    _handleChangeRowsPerPage = (event) => {
        this.setState({ rowsPerPage: +event.target.value });
        this.setState({ page: 0 });
    };

    /*------------------------------ Render -----------------------------------*/

    _renderFuelTab = () => {
        const { classes } = this.props;
        return (
            <GridContainer >
                <GridItem xs={12} sm={12} md={12}>
                    {<Button onClick={() => { this._handleReset(); this.setState({ isCreateFuel: !this.state.isCreateFuel }) }} color="info">{this.state.isCreateFuel ? "Return" : "Create"}</Button>}
                </GridItem>
                {this.state.isCreateFuel || this.state.isViewFuel
                    ? <GridItem xs={12} sm={12} md={12}>
                        <Card>
                            <CardHeader color="primary">
                                <h4 className={classes.cardTitleWhite}>{this.state.isViewFuel ? "Fuel details" : "Create Fuels"}</h4>
                                <p className={classes.cardCategoryWhite}>{this.state.isViewFuel ? "" : "Complete new fuels"}</p>
                            </CardHeader>
                            <CardBody>
                                <GridContainer>
                                    <GridItem xs={12} sm={12} md={6}>
                                        <CustomInput
                                            onChange={e => {
                                                this.setState({ fuelName: e.target.value })
                                                if (e.target.value) { this.setState({ fuelNameError: false }) }
                                                else { this.setState({ fuelNameError: false }) }
                                            }}
                                            labelText="Name"
                                            id="fuelName"
                                            error={this.state.fuelNameError}
                                            inputProps={{ type: "text", value: this.state.fuelName }}
                                            formControlProps={{
                                                fullWidth: true,
                                            }}
                                        />
                                    </GridItem>
                                </GridContainer>
                            </CardBody>
                            <CardFooter>
                                <h5 style={{ color: 'red' }}>{this.state.fuelNameError ? "Enter a fuel name" : ""}</h5>
                                {!this.state.isViewFuel ? <Button onClick={() => this.addFuel()} color="success">Create</Button> : null}
                                {this.state.isViewFuel ? <Button onClick={() => this.onUpdateFuel(this.state.selectedFuel.id)} color="warning">Update</Button> : null}
                                {this.state.isViewFuel ? <Button onClick={() => this.onDeleteFuel(this.state.selectedFuel.id)} color="danger">Delete</Button> : null}
                                {this.state.isViewFuel ? <Button onClick={() => this._handleReset()} color="info">Reset</Button> : null}
                            </CardFooter>
                        </Card>
                    </GridItem> : null}
                <GridItem xs={12} sm={12} md={12}>
                    <Card>
                        <CardHeader color="info">
                            <h4 className={classes.cardTitleWhite}>Fuels</h4>
                        </CardHeader>
                        <CardBody>
                            {!this.state.isLoading
                                ? <div className={classes.tableResponsive}>
                                    <Table className={classes.table}>
                                        <TableHead className={classes.info}>
                                            <TableRow className={classes.tableHeadRow}>
                                                <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'name'}  >
                                                    Name
                                                </TableCell>
                                                <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'options'}  >
                                                    Options
                                                </TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                            {this.state.fuelList
                                                ? this.state.fuelList.slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage + this.state.rowsPerPage).map((value, key) =>
                                                    <TableRow hover key={key} tabIndex={-1} className={classes.tableBodyRow}>
                                                        <TableCell className={classes.tableCell} key={'name' + key}>
                                                            {value.name}
                                                        </TableCell>
                                                        <TableCell className={classes.tableCell} key={'options' + key}>
                                                            <DeleteForeverIcon style={{ color: red[500] }} onClick={() => this.onDeleteFuel(value.id)} />
                                                            <BorderColorIcon onClick={() => this.getFuelById(value.id)} />
                                                        </TableCell>
                                                    </TableRow>
                                                ) : " No fuel records found"}
                                        </TableBody>
                                    </Table>
                                    <TablePagination
                                        component="div"
                                        count={this.state.fuelList.length}
                                        rowsPerPage={this.state.rowsPerPage}
                                        page={this.state.page}
                                        onChangePage={this._handleChangePage}
                                        onChangeRowsPerPage={this._handleChangeRowsPerPage}
                                    />
                                </div>
                                : <Loader type="ThreeDots" color="#a5d1e6" height={50} width={50} />}
                        </CardBody>
                    </Card>
                </GridItem>
            </GridContainer >
        )
    }

    render() {
        return (
            this._renderFuelTab()
        );
    }
}

export default withStyles(useStyles)(FuelView);