    reportCompanyCreate = (companyData, callback) => {

        var data = {
            'name': companyData.name,
            'address': companyData.address,
            'city': companyData.city,
            'state': companyData.state,
            'zipcode': companyData.zipcode,
            'phone1': (companyData.phone1.replace(/[^0-9]/g, '').length === 10) ? companyData.phone1.replace(/[^0-9]/g, '').slice(0, 10) : null,
            'phone2': (companyData.phone2.replace(/[^0-9]/g, '').length === 10) ? companyData.phone2.replace(/[^0-9]/g, '').slice(0, 10) : null,
        }

        axios({
            method: 'post',
            url: SERVICE_URL + 'reportcom/create/',
            headers: header,
            data: data,
        }).then(response => {
            if (response.data) {
                const companyResponse = response.data.data;
                this.reportLogoUpload(companyData.logo, companyResponse.id, () => {

                })
                this.showNotification('success', 'New reporting company created')
                callback(companyResponse)
            }
        }).catch(error => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
            console.log(error)
        })
    }

