    reportCompanyGetId = (id, callback) => {
        axios({
            method: 'get',
            url: SERVICE_URL + 'reportcom/' + id,
            headers: header,
        }).then(response => {
            var result = response.data
            if (result) {
                if (result.phone1) {
                    result.phone1 = (result.phone1.toString().slice(0, 3) + '-' + result.phone1.toString().slice(3, 6) + '-' + result.phone1.toString().slice(6, 10))
                } else {
                    result.phone1 = " "
                }
                if (result.phone2) {
                    result.phone2 = (result.phone2.toString().slice(0, 3) + '-' + result.phone2.toString().slice(3, 6) + '-' + result.phone2.toString().slice(6, 10))
                } else {
                    result.phone2 = " "
                }
            }
            callback(result)
        }).catch(error => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
            console.log(error)
        })
    }

    reportCompanyUpdate = (companyData, callback) => {

        var data = {
            'name': companyData.name,
            'address': companyData.address,
            'city': companyData.city,
            'state': companyData.state,
            'zipcode': companyData.zipcode,
            'phone1': (companyData.phone1.replace(/[^0-9]/g, '').length === 10) ? companyData.phone1.replace(/[^0-9]/g, '').slice(0, 10) : null,
            'phone2': (companyData.phone2.replace(/[^0-9]/g, '').length === 10) ? companyData.phone2.replace(/[^0-9]/g, '').slice(0, 10) : null,
        }

        axios({
            method: 'put',
            url: SERVICE_URL + 'reportcom/' + companyData.selectedid,
            headers: header,
            data: data,
        }).then(response => {
            this.showNotification('warning', 'Update success')
            callback(response.data)
        }).catch(error => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
            console.log(error)
        })
    }

    reportLogoUpload = (logo, id, callback) => {
        if (logo) {
            var data = new FormData()
            data.append('logo', logo);
            axios({
                method: 'post',
                url: SERVICE_URL + 'reportcom/logo/?id=' + id,
                headers: headerMultiPart,
                data: data,
            }).then(response => {
                this.showNotification('success', 'Logo uploaded')
                callback(response)
            }).catch(error => {
                this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
                console.log(error)
            })
            return
        }
        this.showNotification('danger', 'No logo for company')
    }

