import { store } from 'react-notifications-component';

import { USER_CONTROLLER_SERVER } from "config";
import constants from "constants/index"
import axios, { header } from '_helper/axios'
class UserService {

    showNotification = (type, message) => {
        store.addNotification({
            message: message,
            type: type,
            insert: "top",
            container: "top-right",
            animationIn: ["animated", "fadeIn"],
            animationOut: ["animated", "fadeOut"],
            dismiss: {
                duration: 5000,
                onScreen: true
            }
        });
    }

    // Users related
    register = async (data, role, callback) => {
        const formData = {
            managerid: data.managerid,
            company: data.company,
            email: data.email.toLowerCase().trim(),
            fname: data.fname.trim(),
            surname: data.surname.trim(),
            phone: (data.phone.replace(/[^0-9]/g, '').length === 10) ? data.phone.replace(/[^0-9]/g, '').slice(0, 10) : " ",
            address: data.address.trim(),
            city: data.city.trim(),
            state: data.state.trim(),
            country: data.country.trim(),
            zipcode: data.zipcode.trim(),
            role: role,
            clientid: data.clientid ? data.clientid.trim() : " ",
        }
        axios({
            method: 'post',
            url: USER_CONTROLLER_SERVER + 'users/register',
            headers: header,
            data: formData,
        }).then(response => {
            if (response.data && response.data.status === 'success') {
                this.showNotification(response.data.status, `${role} created`)
                callback({
                    message: response.data.message,
                    token: response.data.token,
                });
            } else {
                if (response.data && response.data.status) {
                    this.showNotification(response.data.status, response.data.message)
                }
            }
        }).catch(error => {
            if (error.message) {
                this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
            }
        })
    }

    getTokenUser = callback => {
        axios({
            method: 'get',
            url: USER_CONTROLLER_SERVER + 'users/',
            headers: header
        }).then(response => {
            callback(response.data)
        }).catch(() => {
            this.authenticated = false;
        })
    }

    getUsersList = (role, issiftable, callback) => {
        const params = {};
        if (role) params.search = role;

        if (issiftable) params.issiftable = issiftable;

        axios({
            method: 'get',
            url: USER_CONTROLLER_SERVER + 'users/all',
            headers: header,
            params: params,
        }).then(response => {
            callback(response.data);
        }).catch(error => {
            console.log(error)
        })
    }

    getUserById = (id, callback) => {
        axios({
            method: 'get',
            url: USER_CONTROLLER_SERVER + 'users/' + id,
            headers: header,
        }).then(response => {
            var result = response.data
            if (result.phone) {
                result.phone = result.phone.toString().slice(0, 3) + '-' + result.phone.toString().slice(3, 6) + '-' + result.phone.toString().slice(6, 10)
            } else {
                result.phone = " "
            }
            callback(result);
        }).catch(() => {
        })
    }

    resetPassword = (email, callback) => {
        axios({
            method: 'post',
            url: USER_CONTROLLER_SERVER + 'users/forgot',
            headers: header,
            data: { email: email },
        }).then(() => {
            this.showNotification('success', 'Password has been reset')
            callback({ message: "New password is sent to your mail " });
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
            callback({ message: "Please try again" })
        })
    }

    changePassword = (password, callback) => {
        const formData = {
            password: password
        }
        axios({
            method: 'post',
            url: USER_CONTROLLER_SERVER + 'users/password',
            headers: header,
            data: formData,
        }).then(() => {
            this.showNotification('success', 'Password changed')
            callback();
        }).catch(() => {
            this.showNotification('success', 'Sorry, cannot change password at this moment')
        })
    }

    updateUser = (userInfo, callback) => {
        if (userInfo.phone) {
            userInfo.phone = (userInfo.phone.replace(/[^0-9]/g, '').length === 10) ? userInfo.phone.replace(/[^0-9]/g, '').slice(0, 10) : " "
        }
        axios({
            method: 'put',
            url: USER_CONTROLLER_SERVER + 'users/' + userInfo.id,
            headers: header,
            data: userInfo,
        }).then(() => {
            this.showNotification('success', 'User details updated')
            callback();
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    deleteUser = (id, callback) => {
        axios({
            method: 'delete',
            url: USER_CONTROLLER_SERVER + 'users/' + id,
            headers: header,
        }).then(_response => {
            let response = (_response && _response.data) || null
            console.log(response.data)
            if (response && response.status) {
                this.showNotification('success', response.data.message || 'User deleted sucessfully')
                callback(response.data);
            } else {
                this.showNotification('danger', 'Operation failed')
            }

        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    /* --------------------------------- Customer section ------------------------------------- */

    createCustomer = (data, callback) => {
        axios({
            method: 'post',
            url: USER_CONTROLLER_SERVER + 'clients/create',
            headers: header,
            data: {
                name: data.companyName,
            },
        }).then(response => {
            if (response.data) {
                callback(response.data);
                this.showNotification('success', 'Customer created')
            }
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    getCustomerList = callback => {
        axios({
            method: 'get',
            url: USER_CONTROLLER_SERVER + 'clients/all',
            headers: header,
        }).then(response => {
            callback(response.data);
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    getCustomerById = (id, callback) => {
        axios({
            method: 'get',
            url: USER_CONTROLLER_SERVER + 'clients/' + id,
            headers: header,
        }).then(response => {
            callback(response.data);
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    deleteCustomer = (id, callback) => {
        axios({
            method: 'delete',
            url: USER_CONTROLLER_SERVER + 'clients/' + id,
            headers: header,
        }).then(() => {
            this.showNotification('success', 'customer deleted')
            callback();
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    updateCustomer = (data, callback) => {
        axios({
            method: 'put',
            url: USER_CONTROLLER_SERVER + 'clients/' + data.id,
            headers: header,
            data: data,
        }).then(() => {
            this.showNotification('success', 'customer updated')
            callback();
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    // Location

    createClientLocation = (data, callback) => {

        var locationData = data;
        if (locationData.phone) {
            locationData.phone = locationData.phone.replace(/[^0-9]/g, '') ? locationData.phone.replace(/[^0-9]/g, '') : " "
        }

        axios({
            method: 'post',
            url: USER_CONTROLLER_SERVER + 'clients/location/add',
            headers: header,
            data: locationData,
        }).then(response => {
            if (response.data && response.data.status) {
                this.showNotification('success', response.data.status)
                callback(response.data);
            }
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    getCustomerLocationByID = (id, callback) => {
        axios({
            method: 'get',
            url: USER_CONTROLLER_SERVER + 'clients/location/' + id,
            headers: header,
        }).then(response => {
            if (response.data && response.data.data) {
                var result = response.data.data;
                if (result && result.phone) {
                    result.phone = result.phone.toString().slice(0, 3) + '-' + result.phone.toString().slice(3, 6) + '-' + result.phone.toString().slice(6, 10)
                }
                callback(result);
            }
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    getCustomerLocationByClient = (id, callback) => {
        axios({
            method: 'get',
            url: USER_CONTROLLER_SERVER + 'clients/location/client/' + id,
            headers: header,
        }).then(response => {
            callback(response.data);
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    updateClientLocation = (data, callback) => {

        var locationData = data.location

        if (locationData.phone)
            locationData.phone = locationData.phone.replace(/[^0-9]/g, '') || " ";

        axios({
            method: 'put',
            url: USER_CONTROLLER_SERVER + 'clients/location/update/' + data.id,
            headers: header,
            data: locationData,
        }).then(() => {
            this.showNotification('success', 'Location updated')
            callback();
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    deleteClientLocation = (id, callback) => {
        axios({
            method: 'delete',
            url: USER_CONTROLLER_SERVER + 'clients/location/' + id,
            headers: header,
        }).then(() => {
            this.showNotification('success', 'Location deleted')
            callback();
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    getAllLocation = callback => {
        axios({
            method: 'get',
            url: USER_CONTROLLER_SERVER + 'clients/location/getall',
            headers: header,
        }).then(({ data }) => {
            if (data) {
                callback(data);
            }
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    // User

    getUsersByCustomerID = (id, callback) => {
        axios({
            method: 'get',
            url: USER_CONTROLLER_SERVER + 'users/client/' + id,
            headers: header,
        }).then(response => {
            callback(response.data);
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    makeUserPrimary = (data, callback) => {
        axios({
            method: 'post',
            url: USER_CONTROLLER_SERVER + 'users/primary/',
            headers: header,
            data: data,
        }).then(response => {
            this.showNotification('success', 'User is now primary')
            callback(true);
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    // Privilege

    createPrivilege = (privilegeData, callback) => {
        axios({
            method: 'post',
            url: USER_CONTROLLER_SERVER + 'permission/create',
            headers: header,
            data: privilegeData,
        }).then(response => {
            if (response.data.status) {
                this.showNotification('success', response.data.status || 'Privilage created')
                callback(response.data);
            }
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    getUserPrivilege = (data, callback) => {
        axios({
            method: 'get',
            url: USER_CONTROLLER_SERVER + 'permission/' + data.clientid + '/user/' + data.userid,
            headers: header,
        }).then(response => {
            if (response.data) {
                if (response.data) {
                    callback(response.data || []);
                }
            }
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    getUserPrivilegeByToken = (id, callback) => {
        axios({
            method: 'get',
            url: USER_CONTROLLER_SERVER + 'permission/get',
            headers: header,
            params: {
                c: id,
            }
        }).then(response => {
            if (response.data) {
                callback(response.data || {});
            }
        }).catch(error => {
            console.log(error)
        })
    }

    getUserPrivilegeOptions = callback => {
        const response = [
            { selection: "Admin", selectionKey: 'admin' },
            { selection: "User", selectionKey: 'user' },
            { selection: "Location", selectionKey: 'location' },
            { selection: "Order", selectionKey: 'order' },
            { selection: "SIF", selectionKey: 'sif' },
            { selection: "Trending", selectionKey: 'trending' },
        ];
        callback(response)
    }

    updatePrivilege = (privilegeData, callback) => {
        axios({
            method: 'put',
            url: USER_CONTROLLER_SERVER + 'permission/update/' + privilegeData.privilegeid,
            headers: header,
            data: privilegeData,
        }).then(response => {
            if (response.data) {

                this.showNotification('warning', response.data.message || 'Permission updated')
                callback(response.data);
            }
        }).catch(() => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
        })
    }

    deletePrivilege = (privilegeData, callback) => {
        callback()
    }

}

export default new UserService()