import React from "react";
import { withStyles } from "@material-ui/core/styles";
// import Loader from 'react-loader-spinner';

import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import {userService} from "services/api.services";

const useStyles = () => (styles)

class PersonViewPage extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            name: "",
            newPassword: '',
            newPasswordConfirm: '',
            isDisabled: false,
            isLoading: true,
        }
    }

    componentDidMount = () => {
        this._getProfile()
    }

    _getProfile = () => {
        this.setState({
            isLoading: true,
        })
        userService.getTokenUser(result => {
            this.setState({
                name: result.fname + " " + result.surname,
                isLoading: false,
            })
        })
    }

    changePassword = () => {
        this.setState({ isDisabled: true })
        userService.changePassword(this.state.newPassword, () => {
            this.setState({
                newPassword: '',
                newPasswordConfirm: '',
                isDisabled: false,
            })
        })
        this.setState({
            isDisabled: false,
        })

    }


    render() {
        return (
            <GridContainer>
                <GridItem xs={12} sm={12} md={8}>
                    <Card>
                        <form>
                            <CardBody>
                                <GridContainer>
                                    <GridItem xs={12} sm={12} md={12}>
                                        <h5>{"Hi, " + this.state.name}</h5>
                                    </GridItem>
                                    <GridItem xs={12} sm={12} md={6}>
                                        <CustomInput
                                            onChange={e => this.setState({ newPassword: e.target.value })}
                                            labelText="New Password"
                                            id="new-password"
                                            disabled={this.state.isDisabled}
                                            inputProps={{ type: "test", value: this.state.emnewPasswordail }}
                                            error={this.state.newPasswordError}
                                            formControlProps={{
                                                fullWidth: true,
                                            }}
                                        />
                                    </GridItem>
                                    <GridItem xs={12} sm={12} md={6}>
                                        <CustomInput
                                            onChange={e => this.setState({ newPasswordConfirm: e.target.value })}
                                            labelText="Confirm Password"
                                            id="new-password-confirm"
                                            disabled={this.state.isDisabled}
                                            inputProps={{ type: "text", value: this.state.newPasswordConfirm }}
                                            error={this.state.newPassword !== this.state.newPasswordConfirm}
                                            formControlProps={{
                                                fullWidth: true,
                                            }}
                                        />
                                    </GridItem>
                                </GridContainer>
                            </CardBody>
                        </form>
                        <CardFooter>
                            <Button disabled={!this.state.newPassword || this.state.newPassword !== this.state.newPasswordConfirm || this.state.isDisabled} onClick={() => this.changePassword()} color="primary">Update</Button>
                        </CardFooter>
                    </Card>
                </GridItem>
            </GridContainer>
        )
    }
}

export default withStyles(useStyles)(PersonViewPage);
