export function CustomDropBox(props) {
  const classes = useStyles();
  const {
    formControlProps,
    labelText,
    id,
    selectProps,
    error,
    success,
    onChange,
    valuekey,
    value,
    selectedValue,
    inputProps,
  } = props;
  
  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 250,
      },
    },
  };
  return (
    <FormControl
      {...formControlProps}
      className={formControlProps.className + " " + classes.formControl}
    >

      {labelText !== undefined
        ? (<InputLabel htmlFor={id} className={classes.labelRoot} id={id+"-selection"}>{labelText}</InputLabel>)
        : null}
      <Select
        labelId={id + "-selection"}
        id={id + "-selection"}
        value={selectedValue}
        onChange={onChange}
        {...inputProps}
        input={<Input id={id + "-multiple-chip"} />}
        renderValue={(selected) => (
          <div className={classes.chips}>
            {selected.map((value) => (
              <Chip key={value.id} label={valuekey.map(key => {
                if (key.trim() === 'email' && value[key] !== '' && value[key] !== 'undefined' && value[key] !== null) {
                  return ' - ' + value[key] || " "
                }
                return (value[key] || " ") + " "
              })} className={classes.chip} />
            ))}
          </div>
        )}
        MenuProps={MenuProps}
        {...selectProps}
      >
        {value.map((value) => (
          <MenuItem key={value.id} value={value} >
            {valuekey.map(key => {
              if (key.trim() === 'email' && value[key] !== '' && value[key] !== 'undefined' && value[key] !== null) {
                return ' -' + value[key]
              }
              return value[key] + " "
            })}
          </MenuItem>
        ))}
      </Select>
      {error ? (
        <Clear className={classes.feedback + " " + classes.labelRootError} />
      ) : success ? (
        <Check className={classes.feedback + " " + classes.labelRootSuccess} />
      ) : null}
    </FormControl>
  );
}

CustomInput.propTypes = {
  labelText: PropTypes.node,
  labelProps: PropTypes.object,
  id: PropTypes.string,
  inputProps: PropTypes.object,
  formControlProps: PropTypes.object,
  error: PropTypes.bool,
  success: PropTypes.bool
};
