import React from "react";

import { withStyles } from "@material-ui/core/styles";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import BorderColorIcon from '@material-ui/icons/BorderColor';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TablePagination from '@material-ui/core/TablePagination';
import { FormControl, InputLabel, Select, Input, Chip, MenuItem } from "@material-ui/core";
import { red } from "@material-ui/core/colors";
import Loader from 'react-loader-spinner';

import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import {dataControllerService} from "services/api.services";

const useStyles = () => (styles)
const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250,
        },
    },
};

class PackageView extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            // Get list on load
            LabTestList: [], isLoading: true,
            // Pagination
            page: 0, rowsPerPage: 10,
            // Lab test
            selectedLabTest: {},
            // Package
            selectedLabTestList: [], packageName: "", packageError: "", isViewPackageDetails: false, selectedPackage: {}, packageList: [], isCreatePackage: false,
        }
    }

    componentDidMount = () => {
        this._onLoadData()
    }

    _onLoadData = () => {
        this.getLabTestList();
        this.getAllPackages()
    }

    _handleReset = () => {
        this.setState({
            // Lab test
            selectedLabTest: {},
            // Lab test package
            selectedLabTestList: [], packageName: "", packageError: "", isViewPackageDetails: false, selectedPackage: {}, isCreatePackage: false,
        })
    }

    /*------------------------ Laboratory test operations -------------------------------*/

    getLabTestList = () => {
        this.setState({ isLoading: true })
        dataControllerService.getLabTestList(result => {
            if (result) {
                this.setState({ LabTestList: result })
            }
            this.setState({ isLoading: false })
        })
    }

    /*---------------------------- Package operations ---------------------------------*/

    _handleLabTestSelection = event => {
        this.setState({ selectedLabTestList: event.target.value })
    }

    onCreatePackage = () => {
        if (this.state.packageName && this.state.selectedLabTestList.length) {
            var data = {
                name: this.state.packageName,
            }
            var temp = [];
            if (this.state.selectedLabTestList.length) {
                this.state.selectedLabTestList.map(value => {
                    return temp.push({
                        testid: value.id,
                        testname: value.name
                    })
                })
            }
            data.test = temp;

            confirmAlert({
                title: 'Confirm',
                message: 'Are you sure you want to create this package',
                buttons: [
                    {
                        label: 'Create',
                        onClick: () => {
                            dataControllerService.createPackage(data, result => {
                                this._handleReset()
                                this.getAllPackages()
                            })
                        }
                    },
                    {
                        label: 'Cancel',
                        onClick: () => console.log('cancel create')
                    }
                ]
            });
        } else {
            this.setState({ packageError: "Incomplete form" })
        }
    }

    getAllPackages = () => {
        dataControllerService.getAllPackage(result => {
            this.setState({
                packageList: result
            })
        })
    }

    getPackageById = id => {
        dataControllerService.getPackageById(id, result => {
            if (result.test.length) {
                var tempids = []
                var temp = []
                result.test.map(value => {
                    return tempids.push(value.testid)
                })
                tempids.map(id => {
                    this.state.LabTestList.map(value => {
                        if (value.id === id) {
                            return temp.push(value);
                        }
                        return false;
                    })
                    return true;
                })
                this.setState({
                    isViewPackageDetails: true,
                    selectedPackage: result,
                    packageName: result.name,
                    selectedLabTestList: temp
                })
            }
        })
    }

    onUpdatePackage = () => {
        if (this.state.packageName && this.state.selectedLabTestList.length) {
            var data = {
                id: this.state.selectedPackage.id,
                name: this.state.packageName,
            }
            var temp = [];
            if (this.state.selectedLabTestList.length) {
                this.state.selectedLabTestList.map(value => {
                    return temp.push({
                        testid: value.id,
                        testname: value.name
                    })
                })
            }
            data.test = temp;
            confirmAlert({
                title: 'Confirm',
                message: 'Are you sure you want to update',
                buttons: [
                    {
                        label: 'Update',
                        onClick: () => {
                            dataControllerService.updatePackage(data, result => {
                                this.getAllPackages()
                            })
                        }
                    },
                    {
                        label: 'Cancel',
                        onClick: () => console.log('cancel update')
                    }
                ]
            });
        }
    }

    onDeletePackage = id => {
        confirmAlert({
            title: 'Confirm',
            message: 'Are you sure you want to delete',
            buttons: [
                {
                    label: 'Delete',
                    onClick: () => {
                        dataControllerService.deletePackage(id, result => {
                            this._handleReset()
                            this.getAllPackages()
                        })
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => console.log('cancel delete package')
                }
            ]
        });
    }


    /*------------------------------ Pagination -----------------------------------*/

    _handleChangePage = (event, newPage) => {
        this.setState({ page: newPage });
    };

    _handleChangeRowsPerPage = (event) => {
        this.setState({ rowsPerPage: +event.target.value });
        this.setState({ page: 0 });
    };

    /*------------------------- Package Render -------------------------------*/

    _renderPackageTab = () => {
        const { classes } = this.props;
        return (
            <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                    {<Button onClick={() => { this._handleReset(); this.setState({ isCreatePackage: !this.state.isCreatePackage }) }} color="info">{this.state.isCreatePackage ? "Return" : "Create"}</Button>}
                </GridItem>
                {this.state.isCreatePackage || this.state.isViewPackageDetails
                    ? <GridItem xs={12} sm={12} md={12}>
                        <Card>
                            <CardHeader color="primary">
                                <h4 className={classes.cardTitleWhite}>{this.state.isViewFuel ? "Package details" : "Create Package"}</h4>
                                <p className={classes.cardCategoryWhite}>{this.state.isViewFuel ? "" : "Complete new package"}</p>
                            </CardHeader>
                            <CardBody>
                                <GridItem xs={12} sm={12} md={6}>
                                    <CustomInput
                                        onChange={e => this.setState({ packageName: e.target.value, packageError: "" })}
                                        labelText="Name"
                                        error={!this.state.packageName}
                                        id="packageName"
                                        inputProps={{ type: "text", value: this.state.packageName }}
                                        formControlProps={{
                                            fullWidth: true,
                                        }}
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <FormControl className={classes.formControl}>
                                        <InputLabel className={classes.InputLabelGrey} id="fuel-selection">Fuel test selection</InputLabel>
                                        <Select
                                            labelId="fuel-selection"
                                            id="fuel-selection"
                                            multiple
                                            error={!this.state.selectedLabTestList.length}
                                            value={this.state.selectedLabTestList ? this.state.selectedLabTestList : []}
                                            onChange={e => {
                                                this._handleLabTestSelection(e)
                                                this.setState({ packageError: "" })
                                            }}
                                            input={<Input id="select-fuel" />}
                                            renderValue={(selected) => (
                                                <div className={classes.chips}>
                                                    {selected.map(value => (
                                                        <Chip key={value.id} label={value.name} className={classes.chip} />
                                                    ))}
                                                </div>
                                            )}
                                            MenuProps={MenuProps}
                                        >
                                            {this.state.LabTestList.map(value => (
                                                <MenuItem key={value.id} value={value} >
                                                    {value.name}
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    </FormControl>
                                </GridItem >
                            </CardBody>
                            <CardFooter>
                                <h5 style={{ color: 'red' }}>{this.state.packageError}</h5>
                                {!this.state.isViewPackageDetails ? <Button onClick={() => this.onCreatePackage()} color="success">Create</Button> : null}
                                {this.state.isViewPackageDetails ? <Button onClick={() => this.onUpdatePackage()} color="warning">Update</Button> : null}
                                {this.state.isViewPackageDetails ? <Button onClick={() => this.onDeletePackage(this.state.selectedPackage.id)} color="danger">Delete</Button> : null}
                                {this.state.isViewPackageDetails ? <Button onClick={() => this._handleReset()} color="success">Reset</Button> : null}
                            </CardFooter>
                        </Card>
                    </GridItem > : null}

                <GridItem xs={12} sm={12} md={12}>
                    <Card>
                        <CardHeader color="info">
                            <h4 className={classes.cardTitleWhite}>Packages</h4>
                        </CardHeader>
                        <CardBody>
                            {!this.state.isLoading
                                ? <div className={classes.tableResponsive}>
                                    <Table className={classes.table}>
                                        <TableHead className={classes.info}>
                                            <TableRow className={classes.tableHeadRow}>
                                                <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'name'} > Name </TableCell>
                                                <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'testcount'} > Test count </TableCell>
                                                <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={'options'} > Options </TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                            {this.state.packageList.length 
                                            ? this.state.packageList.slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage + this.state.rowsPerPage).map((value, key) =>
                                                <TableRow key={key} tabIndex={-1} className={classes.tableBodyRow}>
                                                    <TableCell className={classes.tableCell} key={'name' + key}> {value.name} </TableCell>
                                                    <TableCell className={classes.tableCell} key={'test' + key}> {value.test.length} </TableCell>
                                                    <TableCell className={classes.tableCell} key={'options' + key}>
                                                        <DeleteForeverIcon style={{ color: red[500] }} onClick={() => this.onDeletePackage(value.id)} />
                                                        <BorderColorIcon onClick={() => this.getPackageById(value.id)} />
                                                    </TableCell>
                                                </TableRow>
                                            ): "No packages found"}
                                        </TableBody>
                                    </Table>
                                    <TablePagination
                                        component="div"
                                        count={this.state.packageList.length}
                                        rowsPerPage={this.state.rowsPerPage}
                                        page={this.state.page}
                                        onChangePage={this._handleChangePage}
                                        onChangeRowsPerPage={this._handleChangeRowsPerPage}
                                    />
                                </div>
                                : <Loader type="ThreeDots" color="#a5d1e6" height={50} width={50} />}
                        </CardBody>
                    </Card>
                </GridItem>
            </GridContainer>
        )
    }

    render() {
        return (
            this._renderPackageTab()
        );
    }
}

export default withStyles(useStyles)(PackageView);