import React from "react";
import { withStyles } from "@material-ui/core/styles";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import BorderColorIcon from '@material-ui/icons/BorderColor';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TablePagination from '@material-ui/core/TablePagination';
import { red } from "@material-ui/core/colors";
import Loader from 'react-loader-spinner';

import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import {dataControllerService} from "services/api.services";
import config from 'config';
const useStyles = () => (styles)

class ReportedCompanies extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            isCreate: false, isEdit: false,
            message: "",
            isDisabled: false, selectedid: '',
            name: " ", address: " ", city: " ", state: " ", zipcode: " ", phone1: " ", phone2: " ", logo: null,
            companyList: [], isLoading: true,
            // Pagination
            page: 0, rowsPerPage: 10,
        }
    }

    componentDidMount = () => {
        this.loadCompanyList()
    }

    loadCompanyList = () => {
        this.setState({ isLoading: true })
        dataControllerService.reportCompanyList(companyList => {
            if(companyList){
                this.setState({ companyList: companyList })
            }
            this.setState({ isLoading: false })
        })
    }

    _handleReset = () => {
        this.setState({
            isCreate: false, isEdit: false,
            message: " ",
            isDisabled: false, selectedid: '',
            name: " ", address: " ", city: " ", state: " ", zipcode: " ", phone1: "", phone2: "", logo: '',
        })
    }

    onCreate = () => {
        confirmAlert({
            title: 'Confirm',
            message: 'Are you sure you want to create',
            buttons: [
                {
                    label: 'Create',
                    color: 'green',
                    onClick: () => {
                        dataControllerService.reportCompanyCreate(this.state, result => {
                            if (result) {
                                this._handleReset()
                                this.loadCompanyList()
                            }
                        })
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => console.log('cancel create')
                }
            ]
        });
    }

    getCompany = id => {
        dataControllerService.reportCompanyGetId(id, details => {
            if (details) {
                this.setState({
                    isEdit: true,
                    name: details.name,
                    address: details.address,
                    city: details.city,
                    state: details.state,
                    zipcode: details.zipcode,
                    phone1: details.phone1,
                    phone2: details.phone2,
                    image: config.SERVER + details.logo,
                    selectedid: details.id,
                    logo: config.SERVER + details.logo,
                })
            }
        })
    }

    onUpdate = () => {
        confirmAlert({
            title: 'Confirm',
            message: 'Are you sure you want to create',
            buttons: [
                {
                    label: 'Update',
                    color: 'green',
                    onClick: () => {
                        dataControllerService.reportCompanyUpdate(this.state, () => {
                            this.loadCompanyList()
                        })
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => console.log('cancel create')
                }
            ]
        });
    }

    onDelete = (id) => {
        confirmAlert({
            title: 'Confirm',
            message: 'Are you sure you want to delete',
            buttons: [
                {
                    label: 'Delete',
                    color: 'green',
                    onClick: () => {
                        dataControllerService.reportCompanyDelete(id, () => {
                            this.loadCompanyList()
                        })
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => console.log('cancel delete')
                }
            ]
        });

    }

    onChangeLogo = event => {
        if (this.state.isEdit) {
            dataControllerService.reportLogoUpload(event.target.files[0], this.state.selectedid, () => {

            })
        } else {
            this.setState({ logo: event.target.files[0] })
        }
    }

    _handleValidate = () => {

    }

    /*------------------------------ Pagination -----------------------------------*/

    _handleChangePage = (event, newPage) => {
        this.setState({ page: newPage });
    };

    _handleChangeRowsPerPage = (event) => {
        this.setState({ rowsPerPage: +event.target.value });
        this.setState({ page: 0 });
    };

    /*-------------------------- Render List ----------------------------*/

    _renderList = () => {
        const { classes } = this.props
        return (
            <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                    <Button onClick={() => { this._handleReset(); this.setState({ isCreate: !this.state.isCreate }) }} color="info">{this.state.isCreate ? " Return " : "Create"}</Button>
                </GridItem>
                <GridItem xs={12} sm={12} md={12}>
                    <Card>
                        <CardHeader color="info">
                            <h4 className={classes.cardTitleWhite}>Reported companies</h4>
                        </CardHeader>
                        <CardBody>
                            {this.state.isLoading
                                ? <Loader type="ThreeDots" color="#a5d1e6" height={50} width={50} />
                                : <div className={classes.tableResponsive}>
                                    <Table className={classes.table}>
                                        <TableHead className={classes.info}>
                                            <TableRow className={classes.tableHeadRow}>
                                                <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"name"} > Name </TableCell>
                                                <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"phone"} > Phone </TableCell>
                                                <TableCell className={classes.tableCell + " " + classes.tableHeadCell} key={"option"} > Options </TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>

                                            {this.state.companyList.length
                                                ? this.state.companyList.slice(this.state.page * this.state.rowsPerPage, this.state.page * this.state.rowsPerPage + this.state.rowsPerPage).map((value, key) =>
                                                    <TableRow key={key} tabIndex={-1} className={classes.tableBodyRow}>
                                                        <TableCell className={classes.tableCell} key={'name' + key}> {value.name} </TableCell>
                                                        <TableCell className={classes.tableCell} key={'phone' + key}> {value.phone1 ? (value.phone1.toString().slice(0,3) +'-'+ value.phone1.toString().slice(3,6)+'-'+value.phone1.toString().slice(6,10)) : " " } </TableCell>
                                                        <TableCell className={classes.tableCell} key={'option' + key}>
                                                            <DeleteForeverIcon onClick={() => this.onDelete(value.id)} style={{ color: red[500] }} />
                                                            <BorderColorIcon onClick={() => this.getCompany(value.id)} />
                                                        </TableCell>
                                                    </TableRow>
                                                )
                                                : <TableRow>No records available</TableRow>}
                                        </TableBody>
                                    </Table>
                                    <TablePagination
                                        component="div"
                                        count={this.state.companyList.length}
                                        rowsPerPage={this.state.rowsPerPage}
                                        page={this.state.page}
                                        onChangePage={this._handleChangePage}
                                        onChangeRowsPerPage={this._handleChangeRowsPerPage}
                                    />
                                </div>}
                        </CardBody>
                    </Card>
                </GridItem>
            </GridContainer >
        )
    }

    _renderForm = () => {
        const { classes } = this.props;
        return (
            <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                    <Button onClick={() => { this._handleReset(); this.setState({ isCreate: !this.state.isCreate }) }} color="info">{this.state.isCreate ? " Return " : "Create"}</Button>
                </GridItem>
                <GridItem xs={12} sm={12} md={12}>
                    <Card>
                        <CardHeader color="primary">
                            <h4 className={classes.cardTitleWhite}>{this.state.isCreate ? "Reported company" : "Create reported companies"}</h4>
                            <p className={classes.cardCategoryWhite}>{this.state.isCreate ? " " : "Complete new reported companies form"}</p>
                        </CardHeader>
                        <CardBody>
                            <GridContainer>
                                <GridItem xs={12} sm={12} md={6}>
                                    <CustomInput
                                        onChange={e => {
                                            this.setState({ name: e.target.value })
                                            this._handleValidate()
                                        }}
                                        labelText="Company name"
                                        disabled={this.state.isDisabled}
                                        id="name"
                                        inputProps={{ type: 'text', value: this.state.name }}
                                        formControlProps={{
                                            fullWidth: true,
                                        }}
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <CustomInput
                                        onChange={e => {
                                            this.setState({ address: e.target.value })
                                            this._handleValidate()
                                        }}
                                        labelText="Company address"
                                        disabled={this.state.isDisabled}
                                        id="address"
                                        inputProps={{ type: 'text', value: this.state.address }}
                                        formControlProps={{
                                            fullWidth: true,
                                        }}
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <CustomInput
                                        onChange={e => {
                                            this.setState({ city: e.target.value })
                                            this._handleValidate()
                                        }}
                                        labelText="Company city"
                                        disabled={this.state.isDisabled}
                                        id="city"
                                        inputProps={{ type: 'text', value: this.state.city }}
                                        formControlProps={{
                                            fullWidth: true,
                                        }}
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <CustomInput
                                        onChange={e => {
                                            this.setState({ state: e.target.value })
                                            this._handleValidate()
                                        }}
                                        labelText="Company state"
                                        disabled={this.state.isDisabled}
                                        id="state"
                                        inputProps={{ type: 'text', value: this.state.state }}
                                        formControlProps={{
                                            fullWidth: true,
                                        }}
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <CustomInput
                                        onChange={e => {
                                            this.setState({ zipcode: e.target.value })
                                            this._handleValidate()
                                        }}
                                        labelText="Company zipcode"
                                        disabled={this.state.isDisabled}
                                        id="zipcode"
                                        inputProps={{ type: 'text', value: this.state.zipcode }}
                                        formControlProps={{
                                            fullWidth: true,
                                        }}
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <CustomInput
                                        onChange={e => {
                                            this.setState({ phone1: e.target.value })
                                            this._handleValidate()
                                        }}
                                        labelText="Company phone 1"
                                        disabled={this.state.isDisabled}
                                        id="phone1"
                                        inputProps={{ type: 'text', value: this.state.phone1 }}
                                        formControlProps={{
                                            fullWidth: true,
                                        }}
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <CustomInput
                                        onChange={e => {
                                            this.setState({ phone2: e.target.value })
                                            this._handleValidate()
                                        }}
                                        labelText="Company phone 2"
                                        disabled={this.state.isDisabled}
                                        id="phone2"
                                        inputProps={{ type: 'text', value: this.state.phone2 }}
                                        formControlProps={{
                                            fullWidth: true,
                                        }}
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <CustomInput
                                        onChange={this.onChangeLogo}
                                        labelText="Logo"
                                        disabled={this.state.isDisabled}
                                        id="file"
                                        name="file"
                                        inputProps={{ type: 'file' }}
                                        formControlProps={{
                                            fullWidth: true,
                                        }}
                                    />
                                </GridItem>
                                {(this.state.isEdit && this.state.logo) ?
                                    <GridItem xs={12} sm={12} md={6}>
                                        <img
                                            style={{ maxWidth: "200px", maxHeight: "200px" }}
                                            src={this.state.logo}
                                            alt={this.state.name}
                                        />
                                    </GridItem>
                                    : null}
                            </GridContainer>
                        </CardBody>
                        <CardFooter>
                            <h5 style={{ color: 'red' }}>{this.state.message}</h5>
                            {this.state.isCreate ? <Button disabled={this.state.isDisabled} onClick={() => this.onCreate()} color="success">Create</Button> : null}
                            {this.state.isEdit ? <Button disabled={this.state.isDisabled} onClick={() => this._handleReset()} color="success">Reset</Button> : null}
                            {this.state.isEdit ? <Button disabled={this.state.isDisabled} onClick={() => this.onUpdate()} color="warning">Update</Button> : null}
                            {this.state.isEdit ? <Button disabled={this.state.isDisabled} onClick={() => { this.onDelete(this.state.selectedid); this._handleReset() }} color="danger">Delete</Button> : null}
                        </CardFooter>
                    </Card>
                </GridItem>
            </GridContainer>
        )
    }

    render() {
        return (
            (this.state.isCreate || this.state.isEdit) ? this._renderForm() : this._renderList()
        )
    }
}

export default withStyles(useStyles)(ReportedCompanies);