    showNotification = (type, message) => {
        store.addNotification({
            message: message,
            type: type,
            insert: "top",
            container: "top-right",
            animationIn: ["animated", "fadeIn"],
            animationOut: ["animated", "fadeOut"],
            dismiss: {
                duration: 5000,
                onScreen: true
            }
        });
    }

    /*-------------------------------- SIF --------------------------*/
    createSIF = async (data, callback) => {
        if (data.requireCustomLabIDSection) {
            if (!data.labid) {
                this.showNotification('danger', `Opted for custom Lab ID but Lab ID Invalid`);
                callback(false)
            }
        }
        delete data.requireCustomLabIDSection
        axios({
            method: 'post',
            url: SAMPLE_CONTROLLER_SERVER + 'sample/create',
            headers: header,
            data: data,
        }).then(response => {
            this.showNotification('success', response.data.message || 'SIF created')
            if (response.data && response.data.data) {
                callback(response.data.data);
            }
        }).catch(error => {
            this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
            console.log(error)
        })
    }

