    getPendingLabtestsByLabTestFilter = async (params) => {
        if (!params.test_id || !params.start_date || !params.end_date) {
            return []
        }

        try {
            const { data: response } = await axios({
                method: 'get',
                url: SAMPLE_CONTROLLER_SERVER + 'sample/work-flow/lab-test-value',
                headers: header,
                params,
            })
            logger(response.data)
            return response
        } catch (e) {
            logger(e)
            return []
        }
    }

    updatePendingLabtestsByLabTestFilter = async ({ lab_ref, test_ref, result }) => {
        if (!lab_ref || !test_ref || !result) {
            logger('INvalid data', lab_ref, test_ref, result)
            return null
        }
        try {
            const { data: response } = await axios({
                method: 'put',
                url: SAMPLE_CONTROLLER_SERVER + 'sample/work-flow/lab-test-value',
                headers: header,
                params: {
                    lab_ref,
                },
                data: {
                    result,
                    test_ref,
                },
            })
            logger(response.data)
            return response
        } catch (e) {
            logger(e)
            return null
        }
    }

    disposeFuelTest = async (labRef, test_ref) => {
        if (!test_ref || !test_ref) {
            logger({ labRef, test_ref, message: "Missing data" })
            return false;
        }
        try {
            const { data: response } = await axios({
                method: 'PUT',
                url: `${SAMPLE_CONTROLLER_SERVER}sample/dispose/fuel-test/${labRef}/${test_ref}`,
                headers: header,
            })
            logger(response.data)
            return response
        } catch (e) {
            logger(e)
            return []
        }
    }

    getWorkOrderSectionReportByDate = async (params) => {
        if (!params.test_id || !params.start_date || !params.end_date) {
            return []
        }

        try {
            const { data: response } = await axios({
                method: 'get',
                url: `${SAMPLE_CONTROLLER_SERVER}sample/work-flow/report/lab-test-value`,
                headers: header,
                params,
                responseType: 'blob',
            })

            const fileName = `${params.test_name}-${params.start_date} to ${params.end_date}.xlsx`
            const url = window.URL.createObjectURL(new Blob([response]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', fileName);
            document.body.appendChild(link);
            link.click();
            return response;
        } catch (e) {
            logger(e)
            return []
        }
    }

    /************************Order********************************* */
    createOrder = async (data, callback) => {
        data.phone = (data.phone.replace(/[^0-9]/g, '').length === 10) ? data.phone.replace(/[^0-9]/g, '').slice(0, 10) : ""
        if (data.location === null) {
            this.showNotification('danger', constants.Location_error)
        }
        else if (data.shipping_address === "" && data.shipping_city === "" && data.shipping_state === "" && data.shipping_country === "" && data.shipping_zipcode === "") {
            this.showNotification('danger', constants.Shipping_Address)
        }
        else if (data.email.length === 0) {
            this.showNotification('danger', constants.Billing_Email)
        }
        else if (data.phone === "" || data.phone.length !== 10) {
            this.showNotification('danger', constants.Billing_contact)
        }
        else if (data.kit === null) {
            this.showNotification('danger', constants.Kit)
        }
        else if (data.quantity === '') {
            this.showNotification('danger', constants.Qauntity)
        }
        else if (data.isShippingTrackable === false && data.shipping_service !== null && data.tracking_id !== "") {
            // data.tracking_id=data.isShippingTrackable === false && data.shipping_service !== null && data.tracking_id !== "" ? "" : data.tracking_id
            this.showNotification('warning', "This Shipping Service not Trackable")
        }
        else if (data.shipping_service !== null && data.tracking_id === "" && data.isShippingTrackable === true) {
            this.showNotification('danger', constants.Tracking_id)
        }
        else if (data.tracking_id !== "" && data.shipping_service === null) {
            this.showNotification('danger', constants.Shipping_Service)
        }
        else {
            // data.phone= (data.phone.replace(/[^0-9]/g, '').length === 10) ? data.phone.replace(/[^0-9]/g, '').slice(0, 10) : " "
            axios({
                method: 'post',
                url: SAMPLE_CONTROLLER_SERVER + 'order',
                headers: header,
                data: data,
            }).then(response => {
                if (response.data && response.data.status === true) {
                    this.showNotification('success', response.data.message || 'Order created')
                    callback(response.data.data);
                }
                else if (response.data.status === false) {
                    this.showNotification('warning', response.data.message)
                }
            }).catch(error => {
                this.showNotification('danger', constants.DEFAULT_ERROR_ALERT)
                console.log(error)
            })
        }
    }
