    /******************************************* environmental_lab_reports********************************************** */
    createEnv_labreport = async (state_data, callback) => {
        const reWhiteSpace = new RegExp(/\s/);
        const whitespace = reWhiteSpace.test(state_data.lab_id)
        if (state_data.location === null) {
            this.showNotification('danger', constants.Location_error)
        }
        else if (state_data.lab_id === "") {
            this.showNotification('danger', "Lab ID is required")
        }
        else if (/^[A-Z0-9]*$/.test(state_data.lab_id) === false) {
            this.showNotification('danger', "Only capital letter Allowed in LabID")
        }
        else if (whitespace === true) {
            this.showNotification('danger', "Spacing Not Allowed in LabID")
        }
        else if (state_data.reportPDF === null) {
            this.showNotification('danger', "PDF file is required")
        }
        else {
            const data = new FormData()
            data.append('report', state_data.reportPDF)
            data.append("location", state_data.location || null)
            data.append("labid", state_data.lab_id)
            data.append("additional_email", state_data.emailCollection)
            data.append("project_number", state_data.project_no)
            data.append("coc", state_data.coc_number)
            data.append("po", state_data.po)
            data.append("reported_at", state_data.reportDate)
            axios({
                method: 'post',
                url: SAMPLE_CONTROLLER_SERVER + 'env_labreport/',
                headers: header,
                data: data,
            }).then(response => {
                if (response.data) {
                    this.showNotification('success', 'Environmental Lab Reports Created Successfully')
                    callback(response.data.data);
                }

            }).catch(error => {
                this.showNotification('danger', error.response.data.error_message || constants.DEFAULT_ERROR_ALERT)
                console.log(error)
            })
        }

    }
