import auth from "services/auth";
/* eslint-disable-next-line */
const emailRegex = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;

class Validator {

    orderForm = formFields => {
        var validatorStatus = {
            nameError: false, phoneError: false, emailError: false, stateError: false, cityError: false,
            addressError: false, selectedCustomerError: false, status: true, fuelError: false, countryError: false,
            bidError: false, deliveryError: false, priorityError: false,
        }
        if (!formFields.customerId && !formFields.companyName && !formFields.shippingPhone && !formFields.shippingEmail
            && !formFields.shippingAddress && !formFields.shippingCity && !formFields.shippingState && !formFields.priority
            && !formFields.shippingCountry && !formFields.shippingZipcode && !formFields.shippingMethod) {
            validatorStatus.selectedCustomerError = true;
            validatorStatus.nameError = true;
            validatorStatus.phoneError = true;
            validatorStatus.emailError = true;
            validatorStatus.addressError = true;
            validatorStatus.fuelError = true;
            validatorStatus.cityError = true;
            validatorStatus.stateError = true;
            validatorStatus.countryError = true;
            validatorStatus.zipCodeError = true;
            validatorStatus.deliveryError = true;
            validatorStatus.priorityError = true;
            validatorStatus.status = false;
        }

        if (!formFields.customerId) {
            validatorStatus.selectedCustomerError = true
            validatorStatus.status = false;
        }
        if (!formFields.companyName) {
            validatorStatus.nameError = true
            validatorStatus.status = false;
        }
        if (!formFields.shippingPhone) {
            validatorStatus.phoneError = true
            validatorStatus.status = false;
        }
        if (!formFields.shippingEmail) {
            validatorStatus.emailError = true
            validatorStatus.status = false;
        }
        if (!formFields.shippingAddress) {
            validatorStatus.addressError = true
            validatorStatus.status = false;
        }
        if (!formFields.shippingCity) {
            validatorStatus.cityError = true
            validatorStatus.status = false;
        }
        if (!formFields.shippingState) {
            validatorStatus.stateError = true
            validatorStatus.status = false;
        }
        if (!formFields.shippingCountry) {
            validatorStatus.countryError = true
            validatorStatus.status = false;
        }
        if (!formFields.shippingZipcode) {
            validatorStatus.zipCodeError = true
            validatorStatus.status = false;
        }
        if (!formFields.shippingMethod) {
            validatorStatus.deliveryError = true
            validatorStatus.status = false;
        }
        if (!formFields.fuel) {
            validatorStatus.fuelError = true
            validatorStatus.status = false;
        }
        if (!formFields.priority) {
            validatorStatus.priorityError = true
            validatorStatus.status = false;
        }

        return validatorStatus
    }

    loginForm = formFields => {
        if (!formFields.email && !formFields.password) {
            return {
                emailError: true,
                passwordError: true,
                status: false,
            }
        }
        if (!emailRegex.test(formFields.email)) {
            return {
                emailError: true,
                passwordError: false,
                status: false,
            }
        }
        if (!formFields.password) {
            return {
                emailError: false,
                passwordError: true,
                status: false,
            }
        }
        return {
            status: true
        }
    }

    managerForm = ({ fname, surname, email, address, phone, city, state, country, zipcode }) => {
        var error = {
            fNameError: false, lNameError: false, emailError: false, phoneError: false, addressError: false,
            cityError: false, stateError: false, countryError: false, pinCodeError: false, status: false, message: ""
        }

        if (!fname && !surname && !email && !address && !phone && !city && !country && !zipcode) {
            return {
                fNameError: true, lNameError: true, emailError: true, phoneError: true, addressError: true,
                cityError: true, stateError: true, countryError: true, pinCodeError: true, status: false, message: "Incomplete form"
            }
        }

        if (!fname) {
            error.status = false
            error.fNameError = true
            error.message = " First name is required "
            return error
        }
        if (!surname) {
            error.status = false
            error.lNameError = true
            error.message = " Last name is required "
            return error
        }
        if (!email || !emailRegex.test(email)) {
            error.status = false
            error.emailError = true
            error.message = " Email is required in format info@foilab.com"
            return error
        }
        if (!address) {
            error.status = false
            error.addressError = true
            error.message = " Address is required"
            return error
        }
        if (!phone || phone.replace(/[^0-9]/g, '').length !== 10) {
            error.status = false
            error.phoneError = true
            error.message = " Phone number is required"
            return error
        }
        if (!city) {
            error.status = false
            error.cityError = true
            error.message = " City is required"
            return error
        }
        if (!state) {
            error.status = false
            error.stateError = true
            error.message = " State is required"
            return error
        }
        if (!country) {
            error.status = false
            error.countryError = true
            error.message = " Country is required"
            return error
        }
        if (!zipcode) {
            error.status = false
            error.pinCodeError = true
            error.message = " ZIP Code is required"
            return error
        }
        error.status = true
        return error
    }

    employeeForm = ({ managerid, fname, surname, email, address, phone, city, state, country, zipcode }) => {
        var error = {
            managerError: false, fNameError: false, lNameError: false, emailError: false, phoneError: false, addressError: false,
            cityError: false, stateError: false, countryError: false, pinCodeError: false, status: false, message: "",
        }

        if (!fname && !surname && !email && !address && !phone && !city && !country && !zipcode) {
            return {
                fNameError: true, lNameError: true, emailError: true, phoneError: true, addressError: true,
                cityError: true, countryError: true, stateError: true, pinCodeError: true, status: false, managerError: true,
                message: "Incomplete form",
            }
        }

        if (!managerid && auth.authRole() === "admin") {
            error.status = false
            error.managerError = true
            error.message = " Assign an manager "
            return error
        }

        if (!fname) {
            error.status = false
            error.fNameError = true
            error.message = " First name is required "
            return error
        }
        if (!surname) {
            error.status = false
            error.lNameError = true
            error.message = " Last name is required "
            return error
        }
        if (!email || !emailRegex.test(email.toLowerCase().trim())) {
            error.status = false
            error.emailError = true
            error.message = " Email is required in format info@foilab.com"
            return error
        }
        if (!address) {
            error.status = false
            error.addressError = true
            error.message = " Address is required"
            return error
        }
        if (!phone || phone.replace(/[^0-9]/g, '').length !== 10) {
            error.status = false
            error.phoneError = true
            error.message = " Phone number is required"
            return error
        }
        if (!city) {
            error.status = false
            error.cityError = true
            error.message = " City is required"
            return error
        }
        if (!state) {
            error.status = false
            error.stateError = true
            error.message = " State is required"
            return error
        }
        if (!country) {
            error.status = false
            error.countryError = true
            error.message = " Country is required"
            return error
        }
        if (!zipcode) {
            error.status = false
            error.pinCodeError = true
            error.message = " ZIP Code is required"
            return error
        }
        error.status = true
        return error
    }

    customerForm = ({ fname, surname, email, address, phone, city, state, country, zipcode }) => {
        var error = {
            emailError: false, addressError: false,
            cityError: false, stateError: false, countryError: false, pinCodeError: false, status: false, message: "",
        }

        if (!email && !address && !city && !country && !zipcode) {
            return {
                emailError: true, addressError: true,
                cityError: true, stateError: false, countryError: true, pinCodeError: true, status: false,
                message: "Incomplete form",
            }
        }

        if (!email || !emailRegex.test(email.toLowerCase().trim())) {
            error.status = false
            error.emailError = true
            error.message = " Email is required in format info@foilab.com"
            return error
        }
        if (!address) {
            error.status = false
            error.addressError = true
            error.message = " Address is required"
            return error
        }
        if (!city) {
            error.status = false
            error.cityError = true
            error.message = " City is required"
            return error
        }
        if (!state) {
            error.status = false
            error.stateError = true
            error.message = " State is required"
            return error
        }
        if (!country) {
            error.status = false
            error.countryError = true
            error.message = " Country is required"
            return error
        }
        if (!zipcode) {
            error.status = false
            error.pinCodeError = true
            error.message = " ZIP Code is required"
            return error
        }
        error.status = true
        return error
    }

    customerLocationForm = ({ phone }) => {
        var error = {
            phoneError: false,
        }

        if (phone && phone.replace(/[^0-9]/g, '').trim().length !== 10) {
            error.phoneError = true;
        }

        return error;
    }
    
    customerUserForm = ({ phone }) => {
        var error = {
            phoneError: false,
        }

        if (phone && phone.replace(/[^0-9]/g, '').trim().length !== 10) {
            error.phoneError = true;
        }

        return error;
    }

    orderForm = ({ shippingAddress,phone,manager,attention,bid,bcontact,emailCollection,returnLabel,ground,kit }) => {
        var error = {
            shippingAddressError:false,phoneError:false,managerError:false,attentionError:false,bidError:false,bcontactError:false,emailCollectionError:false,returnLabelError:false,groundError:false,kit
            ,message: "",
        }

        if (!shippingAddress && !phone && !manager && !attention && !bid && !bcontact && !emailCollection && !returnLabel && !ground && !kit) {
            return {
                shippingAddressError:true ,phoneError:true ,managerError:true ,attentionError:true ,bidError:true ,bcontactError:true ,emailCollectionError:true ,returnLabelError:true ,groundError:true ,kit
                ,message: "Incomplete form",
            }
        }

        if (!emailCollection.length > 0 || !emailRegex.test(emailCollection[0].toLowerCase().trim())) {
            error.status = false
            error.emailCollectionError = true
            error.message = " Email is required in format info@foilab.com"
            return error
        }
        if (!shippingAddress) {
            error.status = false
            error.shippingAddressError = true
            error.message = " shippingAddress is required"
            return error
        }
        if (!phone) {
            error.status = false
            error.phoneError = true
            error.message = " phone is required"
            return error
        }
        if (!manager) {
            error.status = false
            error.managerError = true
            error.message = " manager is required"
            return error
        }
        if (!attention) {
            error.status = false
            error.attentionError = true
            error.message = " attention is required"
            return error
        }
        if (!bid) {
            error.status = false
            error.bidError = true
            error.message = " bid is required"
            return error
        }
        if (!bcontact) {
            error.status = false
            error.bcontactError = true
            error.message = " bcontact is required"
            return error
        }if (!returnLabel) {
            error.status = false
            error.returnLabelError = true
            error.message = " returnLabel is required"
            return error
        }
        if (!ground) {
            error.status = false
            error.groundError = true
            error.message = " ground is required"
            return error
        }
        if (!kit) {
            error.status = false
            error.kitError = true
            error.message = " kit is required"
            return error
        }
        error.status = true
        return error
    }
    // End of Validator class
}

export default new Validator()
