import React from "react";
import { InputLabel, Select, Chip, MenuItem, FormControl, Input } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import Loader from 'react-loader-spinner';

import Table from "components/Table/Table.js";
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import validate from "_helper/formValidators";
import {userService} from "services/api.services";
import auth from "services/auth"

const useStyles = () => (styles)
const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

class EmployeeViewPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      passwordError: false,
      employeeList: [],
      managerList: [],
      isDisabled: false,
      fname: "", surname: "", email: "", address: "", phone: "", city: "",state: "", country: "", zipcode: "", manager: "", managerid: "", status: false,
      fNameError: false, lNameError: false, emailError: false, addressError: false, phoneError: false, cityError: false, stateError: false, countryError: false, pinCodeError: false,
      managerError: false, isView: false, isLoading: true,
    }
  }

  componentDidMount = () => {
    this._onLoad()
  }

  _onLoad = () => {
    this.setState({ isLoading: true })
    userService.getUsersList('employee', false, result => {
      if(result){
        this.setState({ employeeList: result })
      }
      this.setState({ isLoading: false })
    })
    userService.getUsersList('manager', false, result => {
      if(result){
        this.setState({ managerList: result })
      }
    })
  }

  fetchInfo = ({ id }) => {
    this._handleReset();
    this.setState({ isView: true })
    userService.getUserById(id, result => {
      this.setState(result)
      this.state.managerList.map(value => {
        if (value.id === result.managerid) {
          return this.setState({
            manager: [value],
            managerid: result.managerid
          })
        }
        return false;
      })
      this._handleValidate()
    })
  }

  _handleReset = () => {
    this.setState({
      isView: false, fname: "", surname: "", email: "", address: "", phone: "", city: "", state: "", country: "", zipcode: "", message: "", id: "",
      fNameError: false, lNameError: false, emailError: false, addressError: false, phoneError: false, cityError: false, stateError: false, countryError: false,
      pinCodeError: false, managerError: false, manager: [],
    })
  }

  _handleManagerSelection = event => {
    this.setState({
      manager: [event.target.value],
      managerid: event.target.value.id
    })
    this._handleValidate();
  }

  onCreateProfile = () => {
    if (this.state.status) {
      this.setState({ isDisabled: true })
      confirmAlert({
        title: 'Confirm',
        message: 'Are you sure you want to create',
        buttons: [
          {
            label: 'Create',
            color: 'green',
            onClick: () => {
              userService.register(this.state, "employee", result => {
                this._handleReset();
                if(result){
                  this.setState({message: result.message})
                }
                this._onLoad();
                return
              })
            }
          },
          {
            label: 'Cancel',
            onClick: () => console.log('cancel create')
          }
        ]
      });
    }
    this._handleValidate()
    this.setState({ isDisabled: false });
  }

  onDeleteProfile = () => {
    confirmAlert({
      title: 'Confirm',
      message: 'Are you sure you want to delete',
      buttons: [
        {
          label: 'Delete',
          color: 'green',
          onClick: () => {
            userService.deleteUser(this.state.id, result => {
              this._handleReset();
              this._onLoad();
            })
          }
        },
        {
          label: 'Cancel',
          onClick: () => console.log('cancel delete profile')
        }
      ]
    });
  }

  onUpdateProfile = () => {
    const userInfo = {
      id: this.state.id,
      managerid: this.state.managerid,
      email: this.state.email,
      fname: this.state.fname,
      surname: this.state.surname,
      phone: this.state.phone,
      address: this.state.address,
      city: this.state.city,
      state: this.state.state,
      country: this.state.country,
      zipcode: this.state.zipcode,
    }

    if (this.state.status) {
      confirmAlert({
        title: 'Confirm',
        message: 'Are you sure you want to update',
        buttons: [
          {
            label: 'Update',
            color: 'green',
            onClick: () => {
              userService.updateUser(userInfo, result => {
                this.fetchInfo({ id: userInfo.id })
                this._onLoad();
              })
            }
          },
          {
            label: 'Cancel',
            onClick: () => console.log('cancel update profile')
          }
        ]
      });
    }
  }

  _handleValidate = () => {
    this.setState(validate.employeeForm(this.state))
  }

  render() {
    const { classes } = this.props;
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>{this.state.isView ? "Employee details" : "Create Employee"}</h4>
              <p className={classes.cardCategoryWhite}>{!this.state.isView ? "Complete new employee form" : " "}</p>
            </CardHeader>
            <CardBody>
              <GridContainer>
                {auth.authRole() === 'admin'
                  ? <GridItem xs={12} sm={12} md={12}>
                    <FormControl className={classes.formControl}>
                      <InputLabel className={classes.InputLabelGrey} id="manager-type-selection">Manager assigned</InputLabel>
                      <Select
                        labelId="manager-type-selection"
                        id="manager-type-selection"
                        single
                        required
                        error={this.state.managerError}
                        disabled={this.state.isDisabled}
                        value={this.state.manager ? this.state.manager : []}
                        onChange={this._handleManagerSelection}
                        input={<Input id="select-chip" />}
                        renderValue={(selected) => (
                          <div className={classes.chips}>
                            {selected.map((value) => (
                              <Chip key={value.id} label={value.fname + " " + value.surname} className={classes.chip} />
                            ))}
                          </div>
                        )}
                        MenuProps={MenuProps}
                      >
                        {this.state.managerList.map(value => (
                          <MenuItem key={value.id} value={value} >
                            {value.fname + " " + value.surname}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>
                  </GridItem>
                  : null}
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    onChange={e => {
                      this.setState({ fname: e.target.value })
                      this._handleValidate()
                    }}
                    labelText="First Name"
                    id="fname"
                    disabled={this.state.isDisabled}
                    inputProps={{ type: "text", value: this.state.fname }}
                    error={this.state.fNameError}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    onChange={e => {
                      this.setState({ surname: e.target.value })
                      this._handleValidate()
                    }}
                    labelText="Last name"
                    id="surname"
                    disabled={this.state.isDisabled}
                    inputProps={{ type: "text", value: this.state.surname }}
                    error={this.state.lNameError}
                    formControlProps={{
                      fullWidth: true
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    onChange={e => {
                      this.setState({ email: e.target.value })
                      this._handleValidate()
                    }}
                    labelText="Email"
                    id="email-address"
                    disabled={this.state.isDisabled}
                    inputProps={{ type: "text", value: this.state.email }}
                    error={this.state.emailError}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    onChange={e => {
                      this.setState({ phone: e.target.value })
                      this._handleValidate()
                    }}
                    labelText="Phone"
                    id="phone"
                    disabled={this.state.isDisabled}
                    inputProps={{ type: "text", value: this.state.phone }}
                    error={this.state.phoneError}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={12}>
                  <CustomInput
                    onChange={e => {
                      this.setState({ address: e.target.value })
                      this._handleValidate()
                    }}
                    labelText="Address"
                    id="address"
                    disabled={this.state.isDisabled}
                    inputProps={{ type: "text", value: this.state.address }}
                    error={this.state.addressError}
                    formControlProps={{
                      fullWidth: true
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <CustomInput
                    onChange={e => {
                      this.setState({ city: e.target.value })
                      this._handleValidate()
                    }}
                    labelText="City"
                    id="city"
                    disabled={this.state.isDisabled}
                    inputProps={{ type: "text", value: this.state.city }}
                    error={this.state.cityError}
                    formControlProps={{
                      fullWidth: true
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <CustomInput
                    onChange={e => {
                      this.setState({ state: e.target.value })
                      this._handleValidate()
                    }}
                    labelText="State"
                    id="state"
                    disabled={this.state.isDisabled}
                    inputProps={{ type: "text", value: this.state.state }}
                    error={this.state.stateError}
                    formControlProps={{
                      fullWidth: true
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <CustomInput
                    onChange={e => {
                      this.setState({ country: e.target.value })
                      this._handleValidate()
                    }}
                    inputProps={{ type: "text", value: this.state.country }}
                    error={this.state.countryError}
                    labelText="Country"
                    id="country"
                    disabled={this.state.isDisabled}
                    formControlProps={{
                      fullWidth: true
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <CustomInput
                    onChange={e => {
                      this.setState({ zipcode: e.target.value })
                      this._handleValidate()
                    }}
                    labelText="Zip Code"
                    id="zipcode"
                    disabled={this.state.isDisabled}
                    inputProps={{ type: "text", value: this.state.zipcode }}
                    error={this.state.pinCodeError}
                    formControlProps={{
                      fullWidth: true
                    }}
                  />
                </GridItem>
              </GridContainer>
            </CardBody>
            <CardFooter>
              <h5 style={{ color: 'red' }}>{this.state.message}</h5>
              {!this.state.isView ? <Button disabled={this.state.isDisabled} onClick={() => this.onCreateProfile()} color="primary">Create Profile</Button> : null}
              {this.state.isView ? <Button onClick={this._handleReset} color="primary">Reset</Button> : null}
              {this.state.isView ? <Button onClick={this.onUpdateProfile} color="warning">Update</Button> : null}
              {this.state.isView ? <Button onClick={this.onDeleteProfile} color="danger">Delete</Button> : null}
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="info">
              <h4 className={classes.cardTitleWhite}>Employees</h4>
              <p className={classes.cardCategoryWhite}>
                Updated on 31st March, 2020
                </p>
            </CardHeader>
            <CardBody>
              {this.state.isLoading 
              ? <Loader type="ThreeDots" color="#a5d1e6" height={50} width={50} />
              : this.state.employeeList.length
                ? <Table
                onClick={this.fetchInfo}
                  tableHeaderColor="warning"
                  tableHead={["First name", "Last Name", "Email"]}
                  tableKey={["fname", "surname", "email"]}
                  tableData={this.state.employeeList}
                />
                : "No employee records found."}
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    )
  }
}

export default withStyles(useStyles)(EmployeeViewPage);
